import {artLink} from './constituicao.js'

const artigos =
{
	"1":{
		"caput":'O Senado Federal tem sede no Palcio do Congresso Nacional, em Braslia.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em caso de guerra, de comoo intestina, de calamidade pblica ou de ocorrncia que impossibilite o seu funcionamento na sede, o Senado poder reunir-se, eventualmente, em qualquer outro local, por determinao da Mesa, a requerimento da maioria dos Senadores.'
					},
			}
		}]
	},
	"2":{
		"caput":'O Senado Federal reunir-se-:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'anualmente, de 2 de fevereiro a 17 de julho e de 1 de agosto a 22 de dezembro, durante as sesses legislativas ordinrias, observado o disposto no art. 57 da Constituio;'
					},
					"2":{
						"caput":'quando convocado extraordinariamente o Congresso Nacional (Const., art. 57,  6 a 8).'
					},
			}
		}]
	},
	"3":{
		"caput":'A primeira e a terceira sesses legislativas ordinrias de cada legislatura sero precedidas de reunies preparatrias, que obedecero s seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'iniciar-se-o com o quorum mnimo de um sexto da composio do Senado, em horrio fixado pela Presidncia, observando-se, nas deliberaes, o disposto no art. 288;'
					},
					"2":{
						"caput":'a direo dos trabalhos caber  Mesa anterior, dela excludos, no incio de legislatura, aqueles cujos mandatos com ela houverem terminado, ainda que reeleitos;'
					},
					"3":{
						"caput":'na falta dos membros da Mesa anterior, assumir a Presidncia o mais idoso dentre os presentes, o qual convidar, para os quatro lugares de Secretrios, Senadores pertencentes s representaes partidrias mais numerosas;'
					},
					"4":{
						"caput":'a primeira reunio preparatria realizar-se-:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'incio de legislatura, a partir do dia 1 de fevereiro;'
									},
									"2":{
										"caput":'terceira sesso legislativa ordinria, no dia 1 de fevereiro;'
									},
							}
						}]
					},
					"5":{
						"caput":'no incio de legislatura, os Senadores eleitos prestaro o compromisso regimental na primeira reunio preparatria; em reunio seguinte, ser realizada a eleio do Presidente e, na terceira, a dos demais membros da Mesa;'
					},
					"6":{
						"caput":'na terceira sesso legislativa ordinria, far-se- a eleio do Presidente da Mesa na primeira reunio preparatria e a dos demais membros, na reunio seguinte;'
					},
					"7":{
						"caput":'reunies preparatrias, no ser lcito o uso da palavra, salvo para declarao pertinente  matria que nelas deva ser tratada.'
					},
			}
		}]
	},
	"4":{
		"caput":'A posse, ato pblico por meio do qual o Senador se investe no mandato, realizar-se- perante o Senado, durante reunio preparatria, sesso deliberativa ou no deliberativa, precedida da apresentao  Mesa do diploma expedido pela Justia Eleitoral, o qual ser publicado no Dirio do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A apresentao do diploma poder ser feita pelo diplomado, pessoalmente, por ofcio ao Primeiro-Secretrio, por intermdio do seu Partido ou de qualquer Senador.'
					},
					"2":{
						"caput":'Presente o diplomado, o Presidente designar trs Senadores para receb-lo, introduzi-lo no plenrio e conduzi-lo at a Mesa, onde, estando todos de p, prestar o seguinte compromisso: Prometo guardar a Constituio Federal e as leis do Pas, desempenhar fiel e lealmente o mandato de Senador que o povo me conferiu e sustentar a unio, a integridade e a independncia do Brasil.'
					},
					"3":{
						"caput":'Quando forem diversos os Senadores a prestar o compromisso a que se refere o  2, somente um o pronunciar e os demais, ao serem chamados, diro: Assim o prometo.'
					},
					"4":{
						"caput":'Durante o recesso, a posse realizar-se- perante o Presidente, em solenidade pblica em seu gabinete, observada a exigncia da apresentao do diploma e da prestao do compromisso, devendo o fato ser noticiado no Dirio do Senado Federal.'
					},
					"5":{
						"caput":'O Senador dever tomar posse dentro de noventa dias, contados da instalao da sesso legislativa, ou, se eleito durante esta, contados da diplomao, podendo o prazo ser prorrogado, por motivo justificado, a requerimento do interessado, por mais trinta dias.'
					},
					"6":{
						"caput":'Findo o prazo de noventa dias, se o Senador no tomar posse nem requerer sua prorrogao, considerar-se- como tendo renunciado ao mandato, convocando-se o primeiro Suplente.'
					},
			}
		}]
	},
	"5":{
		"caput":'O primeiro Suplente, convocado para a substituio de Senador licenciado, ter o prazo de trinta dias improrrogveis para prestar o compromisso, e, nos casos de vaga ou de afastamento nos termos do art. 39, II, de sessenta dias, que poder ser prorrogado, por motivo justificado, a requerimento do interessado, por mais trinta dias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se, dentro dos prazos estabelecidos neste artigo, o Suplente no tomar posse e nem requerer sua prorrogao, considerar-se- como tendo renunciado ao mandato, convocando-se o segundo Suplente, que ter, em qualquer hiptese, trinta dias para prestar o compromisso.'
					},
					"2":{
						"caput":'O Suplente, por ocasio da primeira convocao, dever prestar o compromisso na forma do art. 4 e, nas seguintes, o Presidente comunicar  Casa a sua volta ao exerccio do mandato.'
					},
			}
		}]
	},
	"6":{
		"caput":'Nos casos dos arts. 4,  5, e 5,  1, havendo requerimento e findo o prazo sem ter sido votado, considerar-se- como concedida a prorrogao.'
	},
	"7":{
		"caput":'Por ocasio da posse, o Senador ou Suplente convocado comunicar  Mesa, por escrito, o nome parlamentar com que dever figurar nas publicaes e registros da Casa e a sua filiao partidria, observando o disposto no art. 78, pargrafo nico.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Do nome parlamentar no constaro mais de duas palavras, no computadas nesse nmero as preposies.'
					},
					"2":{
						"caput":'A alterao do nome parlamentar ou da filiao partidria dever ser comunicada, por escrito,  Mesa, vigorando a partir da publicao no Dirio do Senado Federal.'
					},
			}
		}]
	},
	"8":{
		"caput":'O Senador deve apresentar-se no edifcio do Senado  hora regimental, para tomar parte nas sesses do Plenrio, bem como  hora de reunio da comisso de que seja membro, cabendo-lhe:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'oferecer proposies, discutir, votar e ser votado;'
					},
					"2":{
						"caput":'solicitar, de acordo com o disposto no art. 216, informaes s autoridades sobre fatos relativos ao servio pblico ou teis  elaborao legislativa;'
					},
					"3":{
						"caput":'usar da palavra, observadas as disposies deste Regimento.'
					},
			}
		}]
	},
	"9":{
		"caput":' facultado ao Senador, uma vez empossado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'examinar quaisquer documentos existentes no Arquivo;'
					},
					"2":{
						"caput":'requisitar da autoridade competente, por intermdio da Mesa ou diretamente, providncias para garantia das suas imunidades e informaes para sua defesa;'
					},
					"3":{
						"caput":'frequentar a Biblioteca e utilizar os seus livros e publicaes, podendo requisit-los para consulta, fora das dependncias do Senado, desde que no se trate de obras raras, assim classificadas pela Comisso Diretora;'
					},
					"4":{
						"caput":'frequentar o edifcio do Senado e as respectivas dependncias, s ou acompanhado, vedado ao acompanhante o ingresso no plenrio, durante as sesses, e nos locais privativos dos Senadores;'
					},
					"5":{
						"caput":'utilizar-se dos diversos servios do Senado, desde que para fins relacionados com as suas funes;'
					},
					"6":{
						"caput":'receber em sua residncia o Dirio do Senado Federal, o do Congresso Nacional e o Dirio Oficial da Unio.'
					},
			}
		}]
	},
	"10":{
		"caput":'O Senador ou Suplente, por ocasio da posse, inscrever, em livro especfico, de prprio punho, seu nome, o nome parlamentar, a respectiva rubrica, filiao partidria, idade, estado civil e outras declaraes que julgue conveniente fazer.'
	},
	"11":{
		"caput":'Com base nos dados referidos no art. 10, o Primeiro-Secretrio expedir as respectivas carteiras de identidade.'
	},
	"12":{
		"caput":'A remunerao do Senador  devida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a partir do incio da legislatura, ao diplomado antes da instalao da primeira sesso legislativa ordinria;'
					},
					"2":{
						"caput":'a partir da expedio do diploma, ao diplomado posteriormente  instalao;'
					},
					"3":{
						"caput":'a partir da posse, ao Suplente em exerccio.'
					},
			}
		}]
	},
	"13":{
		"caput":'Ser considerado ausente o Senador cujo nome no conste da lista de comparecimento, salvo se em licena, ou em representao a servio da Casa ou, ainda, em misso poltica ou cultural de interesse parlamentar, previamente aprovada pela Mesa, obedecido o disposto no art. 40.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O painel do plenrio ser acionado nas sesses deliberativas.'
					},
					"2":{
						"caput":'Considerar-se- ainda ausente o Senador que, embora conste da lista de presena das sesses deliberativas, deixar de comparecer s votaes, salvo se em obstruo declarada por lder partidrio ou de bloco parlamentar.'
					},
			}
		}]
	},
	"14":{
		"caput":'O Senador poder fazer uso da palavra:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos cento e vinte minutos que antecedem a Ordem do Dia, por dez minutos, nas sesses deliberativas, e por vinte minutos, nas sesses no deliberativas;'
					},
					"2":{
						"caput":'se lder, uma vez por sesso:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'cinco minutos, em qualquer fase da sesso, exceto durante a Ordem do Dia, para comunicao urgente de interesse partidrio; ou'
									},
									"2":{
										"caput":'vinte minutos, aps a Ordem do Dia, com preferncia sobre os oradores inscritos;'
									},
							}
						}]
					},
					"3":{
						"caput":'na discusso de qualquer proposio (art. 273), uma s vez, por dez minutos;'
					},
					"4":{
						"caput":'na discusso da proposio em regime de urgncia (art. 336), uma s vez, por dez minutos, limitada a palavra a cinco Senadores a favor e cinco contra;'
					},
					"5":{
						"caput":'na discusso da redao final (art. 321), uma s vez, por cinco minutos, o relator e um Senador de cada partido;'
					},
					"6":{
						"caput":'no encaminhamento de votao (art. 308 e pargrafo nico do art. 310), uma s vez, por cinco minutos;'
					},
					"7":{
						"caput":'no encaminhamento de votao de proposio em regime de urgncia (art. 336), uma s vez, por cinco minutos, o relator da comisso de mrito e os lderes de partido ou bloco parlamentar ou Senadores por eles designados;'
					},
					"8":{
						"caput":'para explicao pessoal, em qualquer fase da sesso, por cinco minutos, se nominalmente citado na ocasio, para esclarecimento de ato ou fato que lhe tenha sido atribudo em discurso ou aparte, no sendo a palavra dada, com essa finalidade, a mais de dois oradores na mesma sesso;'
					},
					"9":{
						"caput":'para comunicao inadivel, manifestao de aplauso ou semelhante, homenagem de pesar, uma s vez, por cinco minutos;'
					},
					"10":{
						"caput":'em qualquer fase da sesso, por cinco minutos:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ordem, para indagao sobre andamento dos trabalhos, reclamao quanto  observncia do Regimento, indicao de falha ou equvoco em relao  matria da Ordem do Dia, vedado, porm, abordar assunto j resolvido pela Presidncia;'
									},
									"2":{
										"caput":'suscitar questo de ordem, nos termos do art. 403;'
									},
									"3":{
										"caput":'contraditar questo de ordem, limitada a palavra a um s Senador;'
									},
							}
						}]
					},
					"11":{
						"caput":'aps a Ordem do Dia, pelo prazo de vinte minutos, para as consideraes que entender (art. 176);'
					},
					"12":{
						"caput":'para apartear, por dois minutos, obedecidas as seguintes normas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'aparte depender de permisso do orador, subordinando-se, em tudo que lhe for aplicvel, s disposies referentes aos debates;'
									},
									"2":{
										"caput":'sero permitidos apartes:'
									},
									"3":{
										"caput":'ao Presidente;'
									},
									"4":{
										"caput":'a parecer oral;'
									},
									"5":{
										"caput":'a encaminhamento de votao, salvo nos casos de requerimento de homenagem de pesar ou de voto de aplauso ou semelhante;'
									},
									"6":{
										"caput":'a explicao pessoal;'
									},
									"7":{
										"caput":'a questo de ordem;'
									},
									"8":{
										"caput":'a contradita a questo de ordem;'
									},
									"9":{
										"caput":'a uso da palavra por cinco minutos;'
									},
									"10":{
										"caput":'recusa de permisso para apartear ser sempre compreendida em carter geral, ainda que proferida em relao a um s Senador;'
									},
									"11":{
										"caput":'aparte proferido sem permisso do orador no ser publicado;'
									},
									"12":{
										"caput":'apartear, o Senador conservar-se- sentado e falar ao microfone;'
									},
							}
						}]
					},
					"13":{
						"caput":'para interpelar Ministro de Estado, por cinco minutos, e para a rplica, por dois minutos (art. 398, X).'
					},
					"14":{
						"caput":'por delegao de sua liderana partidria, por cinco minutos, observado o disposto na alnea a do inciso II e do  3 deste artigo.'
					},
			}
		}]
	},
	"15":{
		"caput":'Os prazos previstos no art. 14 s podero ser prorrogados, pelo Presidente, por um ou dois minutos, para permitir o encerramento do pronunciamento, aps o que o som do orador ser cortado, no sendo lcito ao Senador utilizar-se do tempo destinado a outro, em acrscimo ao de que disponha.'
	},
	"16":{
		"caput":'A palavra ser dada na ordem em que for pedida, salvo inscrio.'
	},
	"17":{
		"caput":'Haver, sobre a mesa, no plenrio, livro especial no qual se inscrevero os Senadores que quiserem usar da palavra, nas diversas fases da sesso, devendo ser rigorosamente observada a ordem de inscrio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Senador s poder usar da palavra mais de duas vezes por semana se no houver outro orador inscrito que pretenda ocupar a tribuna.'
					},
					"2":{
						"caput":'A inscrio ser para cada sesso, podendo ser aceita com antecedncia no superior a duas sesses deliberativas ordinrias ou no deliberativas.'
					},
			}
		}]
	},
	"18":{
		"caput":'O Senador, no uso da palavra, poder ser interrompido:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pelo Presidente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'leitura e votao de requerimento de urgncia, no caso do art. 336, I, e deliberao sobre a matria correspondente;'
									},
									"2":{
										"caput":'votao no realizada no momento oportuno, por falta de nmero (arts. 304 e 305);'
									},
									"3":{
										"caput":'comunicao importante;'
									},
									"4":{
										"caput":'recepo de visitante (art. 199);'
									},
									"5":{
										"caput":'votao de requerimento de prorrogao da sesso;'
									},
									"6":{
										"caput":'suspender a sesso, em caso de tumulto no recinto ou ocorrncia grave no edifcio do Senado;'
									},
									"7":{
										"caput":'adverti-lo quanto  observncia do Regimento;'
									},
									"8":{
										"caput":'prestar esclarecimentos que interessem  boa ordem dos trabalhos;'
									},
							}
						}]
					},
					"2":{
						"caput":'por outro Senador:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'o seu consentimento, para aparte-lo;'
									},
									"2":{
										"caput":'de seu consentimento, para formular  Presidncia reclamao quanto  observncia do Regimento.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'O tempo de interrupo previsto neste artigo ser descontado em favor do orador, salvo quanto ao disposto no inciso II, a.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"19":{
		"caput":'Ao Senador  vedado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'usar de expresses descorteses ou insultuosas;'
					},
					"2":{
						"caput":'falar sobre resultado de deliberao definitiva do Plenrio, salvo em explicao pessoal.'
					},
			}
		}]
	},
	"20":{
		"caput":'No ser lcito ler da tribuna ou incluir em discurso, aparte, declarao de voto ou em qualquer outra manifestao pblica, documento de natureza sigilosa.'
	},
	"21":{
		"caput":'O Senador, ao fazer uso da palavra, manter-se- de p, salvo licena para se conservar sentado, por motivo de sade, e dirigir-se- ao Presidente ou a este e aos Senadores, no lhe sendo lcito permanecer de costas para a Mesa.'
	},
	"22":{
		"caput":'Em caso de infrao do art. 19, I, proceder-se- da seguinte maneira:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Presidente advertir o Senador, usando da expresso Ateno!;'
					},
					"2":{
						"caput":'se essa observao no for suficiente, o Presidente dir Senador F..., ateno!;'
					},
					"3":{
						"caput":'no bastando o aviso nominal, o Presidente retirar-lhe- a palavra;'
					},
					"4":{
						"caput":'insistindo o Senador em desatender s advertncias, o Presidente determinar sua sada do recinto, o que dever ser feito imediatamente;'
					},
					"5":{
						"caput":'em caso de recusa, o Presidente suspender a sesso, que no ser reaberta at que seja obedecida sua determinao.'
					},
			}
		}]
	},
	"23":{
		"caput":'Constituir desacato ao Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'reincidir na desobedincia  medida disciplinar prevista no art. 22, IV;'
					},
					"2":{
						"caput":'agresso, por atos ou palavras, praticada por Senador contra a Mesa ou contra outro Senador, nas dependncias da Casa.'
					},
			}
		}]
	},
	"24":{
		"caput":'Em caso de desacato ao Senado, proceder-se- de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Segundo-Secretrio, por determinao da Presidncia, lavrar relatrio pormenorizado do ocorrido;'
					},
					"2":{
						"caput":'cpias autenticadas do relatrio sero encaminhadas aos demais membros da Mesa e aos lderes que, em reunio convocada pelo Presidente, deliberaro:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'arquivamento do relatrio;'
									},
									"2":{
										"caput":'constituio de comisso para, sobre o fato, se manifestar;'
									},
							}
						}]
					},
					"3":{
						"caput":'na hiptese prevista na alnea b do inciso II, a comisso, de posse do relatrio, reunir-se-, no prazo de duas horas, a partir de sua constituio, a fim de eleger o Presidente, que designar relator para a matria;'
					},
					"4":{
						"caput":'a comisso poder ouvir as pessoas envolvidas no caso e as testemunhas que entender;'
					},
					"5":{
						"caput":'a comisso ter o prazo de dois dias teis para emitir parecer, que ser conclusivo, podendo propor uma das seguintes medidas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'pblica ao Senador;'
									},
									"2":{
										"caput":'de processo de perda de mandato (Const., art. 55, II);'
									},
							}
						}]
					},
					"6":{
						"caput":'aprovado pela comisso, o parecer ser encaminhado  Mesa para o procedimento cabvel no caso.'
					},
			}
		}]
	},
	"25":{
		"caput":'Se algum Senador praticar, dentro do edifcio do Senado, ato incompatvel com o decoro parlamentar ou com a compostura pessoal, a Mesa dele conhecer e abrir inqurito,',
		"enumeravel": [{
			"tipo":"None",
			"lista":{
					"1":{
						"caput":'caso ao Plenrio, que sobre ele deliberar, no prazo improrrogvel de dez dias teis.'
					},
			}
		}]
	},
	"26":{
		"caput":'Falecendo algum Senador em perodo de funcionamento do Senado, o Presidente comunicar o fato  Casa e propor seja a sesso do dia dedicada a reverenciar a memria do extinto, deliberando o Plenrio com qualquer nmero.'
	},
	"27":{
		"caput":'O Senado far-se- representar, nas cerimnias fnebres que se realizarem pelo falecimento de qualquer dos seus membros, por uma comisso constituda, no mnimo, de trs Senadores, designados pelo Presidente, de ofcio ou mediante deliberao do Plenrio, sem embargo de outras homenagens aprovadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na hiptese de ser a comisso designada de ofcio, o fato ser comunicado ao Plenrio, pelo Presidente.'
					},
			}
		}]
	},
	"28":{
		"caput":'As vagas, no Senado, verificar-se-o em virtude de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'falecimento;'
					},
					"2":{
						"caput":'renncia;'
					},
					"3":{
						"caput":'perda de mandato.'
					},
			}
		}]
	},
	"29":{
		"caput":'A comunicao de renncia  senatria ou  suplncia deve ser dirigida por escrito  Mesa, com firma reconhecida, e independe da aprovao do Senado, mas somente tornar-se- efetiva e irretratvel depois de lida no Perodo do Expediente e publicada no Dirio do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":' lcito ao Senador, ou ao Suplente em exerccio, fazer em plenrio, oralmente, a renncia ao mandato, a qual tornar-se- efetiva e irretratvel depois da sua publicao no Dirio do Senado Federal.'
					},
			}
		}]
	},
	"30":{
		"caput":'Considerar-se- como tendo renunciado (arts. 4,  6, e 5,  1):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o Senador que no prestar o compromisso no prazo estabelecido neste Regimento;'
					},
					"2":{
						"caput":'o Suplente que, convocado, no se apresentar para entrar em exerccio no prazo estabelecido neste Regimento.'
					},
			}
		}]
	},
	"31":{
		"caput":'A ocorrncia de vacncia, em qualquer hiptese, ser comunicada pelo Presidente ao Plenrio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos casos do art. 30, at o dia til que se seguir  publicao da comunicao de vacncia, qualquer Senador dela poder interpor recurso para o Plenrio, que deliberar, ouvida a Comisso de Constituio, Justia e Cidadania.'
					},
			}
		}]
	},
	"32":{
		"caput":'Perde o mandato o Senador (Const., art. 55):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'que infringir qualquer das proibies constantes do art. 54 da Constituio;'
					},
					"2":{
						"caput":'cujo procedimento for declarado incompatvel com o decoro parlamentar;'
					},
					"3":{
						"caput":'que deixar de comparecer  tera parte das sesses deliberativas ordinrias do Senado, em cada sesso legislativa anual, salvo licena ou misso autorizada;'
					},
					"4":{
						"caput":'que perder ou tiver suspensos os direitos polticos;'
					},
					"5":{
						"caput":'quando o decretar a Justia Eleitoral;'
					},
					"6":{
						"caput":'que sofrer condenao criminal em sentena definitiva e irrecorrvel.'
					},
			}
		}]
	},
	"33":{
		"caput":'Admitida a representao pelo voto do Plenrio, o Presidente designar comisso composta de nove membros para instruo da matria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Recebida e processada, ser fornecida cpia da representao ao acusado, que ter o prazo de quinze dias teis, prorrogvel por igual perodo, para apresentar,  comisso, sua defesa escrita.'
					},
					"2":{
						"caput":'Apresentada ou no a defesa, a comisso, aps proceder s diligncias que entender necessrias, emitir parecer, concluindo por projeto de resoluo, no sentido da perda do mandato ou do arquivamento definitivo do processo.'
					},
					"3":{
						"caput":'Para falar sobre o parecer, ser concedida vista do processo ao acusado pelo prazo de dez dias teis.'
					},
			}
		}]
	},
	"34":{
		"caput":'O acusado poder assistir, pessoalmente ou por procurador, a todos os atos e diligncias, e requerer o que julgar conveniente aos interesses da defesa.'
	},
	"35":{
		"caput":'O projeto de resoluo, depois de lido no Perodo do Expediente, publicado no Dirio do Senado Federal em avulso eletrnico, ser includo em Ordem do Dia e submetido  votao.'
	},
	"36":{
		"caput":'As imunidades dos Senadores subsistiro durante o estado de stio, s podendo ser suspensas mediante voto de dois teros dos membros da Casa, nos casos de atos praticados fora do recinto do Congresso Nacional, que sejam incompatveis com a execuo da medida (Const., art. 53,  8).'
	},
	"37":{
		"caput":'Sero observadas, na decretao da suspenso das imunidades, as disposies do captulo VIII no que forem aplicveis.'
	},
	"38":{
		"caput":'Considerar-se- como ausente, para efeito do disposto no art. 55, III, da Constituio, o Senador cujo nome no conste das listas de comparecimento das sesses deliberativas ordinrias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para os efeitos deste artigo, aplica-se o disposto no art. 13, no sendo, ainda, considerada a ausncia do Senador nos sessenta dias anteriores s eleies gerais.'
					},
			}
		}]
	},
	"39":{
		"caput":'O Senador dever comunicar ao Presidente sempre que:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ausentar-se do Pas;'
					},
					"2":{
						"caput":'assumir cargo de Ministro de Estado, de Governador de Territrio, de Secretrio de Estado, do Distrito Federal, de Territrio, de Prefeitura de Capital ou de chefe de misso diplomtica temporria (Const., art. 56, I).'
					},
			}
		}]
	},
	"40":{
		"caput":'A ausncia do Senador, quando incumbido de representao da Casa ou, ainda, no desempenho de misso no Pas ou no exterior, dever ser autorizada mediante deliberao do Plenrio, se houver nus para o Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A autorizao poder ser:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'solicitada pelo interessado;'
									},
									"2":{
										"caput":'proposta:',
										"enumeravel": [{
											"tipo":"letra",
											"lista":{
													"1":{
														"caput":'Presidncia, quando de sua autoria a indicao;'
													},
													"2":{
														"caput":'Comisso de Relaes Exteriores e Defesa Nacional, no caso de misso a realizar-se no estrangeiro;'
													},
													"3":{
														"caput":'comisso que tiver maior pertinncia, no caso de misso a realizar-se no Pas;'
													},
													"4":{
														"caput":'lder do bloco parlamentar ou do partido a que pertena o interessado.',
														"enumeravel": [{
															"tipo":"paragrafo",
															"lista":{
																	"1":{
																		"caput":'Na solicitao ou na proposta dever ser mencionado o prazo de afastamento do Senador.'
																	},
																	"2":{
																		"caput":'A solicitao ou proposta ser lida no Perodo do Expediente e votada em seguida  Ordem do Dia da mesma sesso.'
																	},
																	"3":{
																		"caput":'No caso do  1, I e II, d, ser ouvida a Comisso de Relaes Exteriores e Defesa Nacional ou a que tiver maior pertinncia, sendo o parecer oferecido, imediatamente, por escrito ou oralmente, podendo o relator solicitar prazo no excedente a duas horas.'
																	},
																	"4":{
																		"caput":'Os casos de licena sero decididos pela Mesa com recurso para o Plenrio.'
																	},
															}
														}]
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"41":{
		"caput":'Nos casos do art. 40, se no for possvel, por falta de nmero, realizar-se a votao em duas sesses deliberativas ordinrias consecutivas, ou se o Senado estiver em recesso, o pedido ser despachado pelo Presidente, retroagindo os efeitos da licena  data do requerimento.'
	},
	"42":{
		"caput":'O Senador afastado do exerccio do mandato no poder ser incumbido de representao da Casa, de comisso, ou de grupo parlamentar.'
	},
	"43":{
		"caput":'Para os efeitos do disposto no art. 55, III, da Constituio, o Senador poder:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando, por motivo de doena, se encontre impossibilitado de comparecer s sesses do Senado, requerer licena, instruda com laudo de inspeo de sade (Const., art. 56, II);'
					},
					"2":{
						"caput":'solicitar licena para tratar de interesses particulares, desde que o afastamento no ultrapasse cento e vinte dias por sesso legislativa (Const., art. 56, II).'
					},
			}
		}]
	},
	"44":{
		"caput":'Considerar-se- como licena concedida, para os efeitos do art. 55, III, da Constituio, o no comparecimento s sesses do Senador temporariamente privado da liberdade, em virtude de processo criminal em curso.'
	},
	"44-A":{
		"caput":'Considerar-se- como licena autorizada, para os fins do disposto no art. 55, III, da Constituio, e no art. 38, pargrafo nico, deste Regimento, a ausncia s sesses de Senador candidato  Presidncia ou Vice-Presidncia da Repblica, no perodo compreendido entre o registro da candidatura no Tribunal Superior Eleitoral e a apurao do respectivo pleito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O disposto neste artigo aplica-se aos candidatos que concorrerem ao segundo turno.'
					},
					"2":{
						"caput":'Para os fins do disposto neste artigo o Senador dever encaminhar  Mesa certido comprobatria do registro de sua candidatura perante a Justia Eleitoral.'
					},
			}
		}]
	},
	"45":{
		"caput":'Dar-se- a convocao de Suplente nos casos de vaga, de afastamento do exerccio do mandato para investidura nos cargos referidos no art. 39, II, ou de licena por prazo superior a cento e vinte dias (Const., art. 56,  1).'
	},
	"46":{
		"caput":'A Mesa se compe de Presidente, dois Vice-Presidentes e quatro Secretrios.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os Secretrios substituir-se-o conforme a numerao ordinal e, nesta ordem, substituiro o Presidente, na falta dos Vice-Presidentes.'
					},
					"2":{
						"caput":'Os Secretrios sero substitudos, em seus impedimentos, por Suplentes em nmero de quatro.'
					},
					"3":{
						"caput":'O Presidente convidar quaisquer Senadores para substiturem, em sesso, os Secretrios, na ausncia destes e dos Suplentes.'
					},
					"4":{
						"caput":'No se achando presentes o Presidente e seus substitutos legais, inclusive os Suplentes, assumir a Presidncia o Senador mais idoso.'
					},
			}
		}]
	},
	"47":{
		"caput":'A assuno a cargo de Ministro de Estado, de Governador de Territrio e de Secretrio de Estado, do Distrito Federal, de Territrio, de Prefeitura de Capital, ou de chefe de misso diplomtica temporria, implica renncia ao cargo que o Senador exera na Mesa.'
	},
	"48":{
		"caput":'Ao Presidente compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer as atribuies previstas nos arts. 57,  6, I e II, 66,  7, e 80 da Constituio;'
					},
					"2":{
						"caput":'velar pelo respeito s prerrogativas do Senado e s imunidades dos Senadores;'
					},
					"3":{
						"caput":'convocar e presidir as sesses do Senado e as sesses conjuntas do Congresso Nacional;'
					},
					"4":{
						"caput":'propor a transformao de sesso pblica em secreta;'
					},
					"5":{
						"caput":'propor a prorrogao da sesso;'
					},
					"6":{
						"caput":'designar a Ordem do Dia das sesses deliberativas e retirar matria da pauta para cumprimento de despacho, correo de erro ou omisso no avulso eletrnico e para sanar falhas da instruo;'
					},
					"7":{
						"caput":'fazer ao Plenrio, em qualquer momento, comunicao de interesse do Senado e do Pas;'
					},
					"8":{
						"caput":'fazer observar na sesso a Constituio, as leis e este Regimento;'
					},
					"9":{
						"caput":'assinar as atas das sesses secretas, uma vez aprovadas;'
					},
					"10":{
						"caput":'determinar o destino do expediente lido e distribuir as matrias s comisses;'
					},
					"11":{
						"caput":'impugnar as proposies que lhe paream contrrias  Constituio, s leis, ou a este Regimento, ressalvado ao autor recurso para o Plenrio, que decidir aps audincia da Comisso de Constituio, Justia e Cidadania;'
					},
					"12":{
						"caput":'declarar prejudicada proposio que assim deva ser considerada, na conformidade regimental;'
					},
					"13":{
						"caput":'decidir as questes de ordem;'
					},
					"14":{
						"caput":'orientar as discusses e fixar os pontos sobre que devam versar, podendo, quando conveniente, dividir as proposies para fins de votao;'
					},
					"15":{
						"caput":'dar posse aos Senadores;'
					},
					"16":{
						"caput":'convocar Suplente de Senador;'
					},
					"17":{
						"caput":'comunicar ao Tribunal Superior Eleitoral a ocorrncia de vaga de Senador, quando no haja Suplente a convocar e faltarem mais de quinze meses para o trmino do mandato (Const., art. 56, II,  2);'
					},
					"18":{
						"caput":'propor ao Plenrio a indicao de Senador para desempenhar misso temporria no Pas ou no exterior;'
					},
					"19":{
						"caput":'propor ao Plenrio a constituio de comisso para a representao externa do Senado;'
					},
					"20":{
						"caput":'designar oradores para as sesses especiais do Senado e sesses solenes do Congresso Nacional;'
					},
					"21":{
						"caput":'designar substitutos de membros das comisses e nomear relator em plenrio;'
					},
					"22":{
						"caput":'convidar, se necessrio, o relator ou o Presidente da comisso a explicar as concluses de seu parecer;'
					},
					"23":{
						"caput":'desempatar as votaes, quando ostensivas;'
					},
					"24":{
						"caput":'proclamar o resultado das votaes;'
					},
					"25":{
						"caput":'despachar, de acordo com o disposto no art. 41, requerimento de licena de Senador;'
					},
					"26":{
						"caput":'despachar os requerimentos constantes do pargrafo nico do art. 214 e do inciso II do art. 215;'
					},
					"27":{
						"caput":'assinar os autgrafos dos projetos e emendas a serem remetidos  Cmara dos Deputados, e dos projetos destinados  sano;'
					},
					"28":{
						"caput":'promulgar as resolues do Senado e os decretos legislativos;'
					},
					"29":{
						"caput":'assinar a correspondncia dirigida pelo Senado s seguintes autoridades:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'da Repblica;'
									},
									"2":{
										"caput":'da Repblica;'
									},
									"3":{
										"caput":'da Cmara dos Deputados;'
									},
									"4":{
										"caput":'do Supremo Tribunal Federal, dos Tribunais Superiores do Pas e do Tribunal de Contas da Unio;'
									},
									"5":{
										"caput":'de Governos estrangeiros e seus representantes no Brasil;'
									},
									"6":{
										"caput":'das Casas de Parlamento estrangeiro;'
									},
									"7":{
										"caput":'dos Estados, do Distrito Federal e dos Territrios Federais;'
									},
									"8":{
										"caput":'das Assembleias Legislativas dos Estados;'
									},
									"9":{
										"caput":'judicirias, em resposta a pedidos de informaes sobre assuntos pertinentes ao Senado, no curso de feitos judiciais;'
									},
							}
						}]
					},
					"30":{
						"caput":'autorizar a divulgao das sesses, nos termos do disposto no art. 186;'
					},
					"31":{
						"caput":'promover a publicao dos debates e de todos os trabalhos e atos do Senado, impedindo a de expresses vedadas por este Regimento, inclusive quando constantes de documento lido pelo orador;'
					},
					"32":{
						"caput":'avocar a representao do Senado quando se trate de atos pblicos de especial relevncia, e no seja possvel designar comisso ou Senador para esse fim;'
					},
					"33":{
						"caput":'resolver, ouvido o Plenrio, qualquer caso no previsto neste Regimento;'
					},
					"34":{
						"caput":'presidir as reunies da Mesa e da Comisso Diretora, podendo discutir e votar;'
					},
					"35":{
						"caput":'exercer a competncia fixada no Regulamento Administrativo do Senado Federal.'
					},
			}
		}]
	},
	"49":{
		"caput":'Na distribuio das matrias subordinadas, na forma do art. 91,  apreciao terminativa das comisses, o Presidente do Senado, quando a proposio tiver seu mrito vinculado a mais de uma comisso, poder:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'definir qual a comisso de maior pertinncia que deva sobre ela decidir;'
					},
					"2":{
						"caput":'determinar que o seu estudo seja feito em reunio conjunta das comisses, observado, no que couber, o disposto no art. 113.'
					},
			}
		}]
	},
	"50":{
		"caput":'O Presidente somente se dirigir ao Plenrio da cadeira presidencial, no lhe sendo lcito dialogar com os Senadores nem os apartear, podendo, entretanto, interromp-los nos casos previstos no art. 18, I.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Presidente deixar a cadeira presidencial sempre que, como Senador, quiser participar ativamente dos trabalhos da sesso.'
					},
			}
		}]
	},
	"51":{
		"caput":'O Presidente ter apenas voto de desempate nas votaes ostensivas, contando-se, porm, a sua presena para efeito de quorum e podendo, em escrutnio secreto, votar como qualquer Senador.'
	},
	"52":{
		"caput":'Ao Primeiro Vice-Presidente compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'substituir o Presidente nas suas faltas ou impedimentos;'
					},
					"2":{
						"caput":'exercer as atribuies estabelecidas no art. 66,  7, da Constituio, quando no as tenha exercido o Presidente.'
					},
			}
		}]
	},
	"53":{
		"caput":'Ao Segundo Vice-Presidente compete substituir o Primeiro Vice-Presidente nas suas faltas ou impedimentos.'
	},
	"54":{
		"caput":'Ao Primeiro-Secretrio compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ler em plenrio, na ntegra ou em resumo, a correspondncia oficial recebida pelo Senado, os pareceres das comisses, as proposies apresentadas quando os seus autores no as tiverem lido, e quaisquer outros documentos que devam constar do expediente da sesso;'
					},
					"2":{
						"caput":'despachar a matria do expediente que lhe for distribuda pelo Presidente;'
					},
					"3":{
						"caput":'assinar a correspondncia do Senado Federal, salvo nas hipteses do art. 48, inciso XXIX, e fornecer certides;'
					},
					"4":{
						"caput":'receber a correspondncia dirigida ao Senado e tomar as providncias dela decorrentes;'
					},
					"5":{
						"caput":'assinar, depois do Presidente, as atas das sesses secretas;'
					},
					"6":{
						"caput":'rubricar a listagem especial com o resultado da votao realizada atravs do sistema eletrnico, e determinar sua anexao ao processo da matria respectiva;'
					},
					"7":{
						"caput":'promover a guarda das proposies em curso;'
					},
					"8":{
						"caput":'determinar a entrega aos Senadores dos avulsos eletrnicos relativos  matria da Ordem do Dia;'
					},
					"9":{
						"caput":'encaminhar os papis distribudos s comisses;'
					},
					"10":{
						"caput":'expedir as carteiras de identidade dos Senadores (art. 11).'
					},
			}
		}]
	},
	"55":{
		"caput":'Ao Segundo-Secretrio compete lavrar as atas das sesses secretas, proceder-lhes a leitura e assin-las depois do Primeiro-Secretrio.'
	},
	"56":{
		"caput":'Ao Terceiro e Quarto-Secretrios compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fazer a chamada dos Senadores, nos casos determinados neste Regimento;'
					},
					"2":{
						"caput":'contar os votos, em verificao de votao;'
					},
					"3":{
						"caput":'auxiliar o Presidente na apurao das eleies, anotando os nomes dos votados e organizando as listas respectivas.'
					},
			}
		}]
	},
	"57":{
		"caput":'Os Secretrios, ao lerem qualquer documento, conservar-se-o de p e permanecero sentados ao procederem  chamada dos Senadores.'
	},
	"58":{
		"caput":'Ao integrarem a Mesa, os Secretrios no podero usar da palavra seno para a chamada dos Senadores ou para a leitura de documentos, ordenada pelo Presidente.'
	},
	"59":{
		"caput":'Os membros da Mesa sero eleitos para mandato de dois anos, vedada a reeleio para o perodo imediatamente subsequente (Const., art. 57,  4).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na constituio da Mesa  assegurada, tanto quanto possvel, a representao proporcional dos partidos e blocos parlamentares que participam do Senado (Const., art. 58,  1).'
					},
					"2":{
						"caput":'Para os fins do clculo de proporcionalidade, as bancadas partidrias so consideradas pelos seus quantitativos  data da diplomao.'
					},
					"3":{
						"caput":'No caso de vaga definitiva, o preenchimento far-se-, dentro de cinco dias teis, pela forma estabelecida no art. 60, salvo se faltarem menos de cento e vinte dias para o trmino do mandato da Mesa.'
					},
					"4":{
						"caput":'Enquanto no eleito o novo Presidente, os trabalhos do Senado sero dirigidos pela Mesa do perodo anterior.'
					},
			}
		}]
	},
	"60":{
		"caput":'A eleio dos membros da Mesa ser feita em escrutnio secreto, exigida maioria de votos, presente a maioria da composio do Senado e assegurada, tanto quanto possvel, a participao proporcional das representaes partidrias ou dos blocos parlamentares com atuao no Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A eleio far-se- em quatro escrutnios, na seguinte ordem, para:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o Presidente;'
									},
									"2":{
										"caput":'os Vice-Presidentes;'
									},
									"3":{
										"caput":'os Secretrios;'
									},
									"4":{
										"caput":'os Suplentes de Secretrios.'
									},
							}
						}]
					},
					"2":{
						"caput":'A eleio, para os cargos constantes dos incisos II a IV do  1, far-se- com cdulas uninominais, contendo a indicao do cargo a preencher, e colocadas, as referentes a cada escrutnio, na mesma sobrecarta.'
					},
					"3":{
						"caput":'Na apurao, o Presidente far, preliminarmente, a separao das cdulas referentes ao mesmo cargo, lendo-as, em seguida, uma a uma, e passando-as ao Segundo-Secretrio, que anotar o resultado.'
					},
					"4":{
						"caput":'Por proposta de um tero dos Senadores ou de lder que represente este nmero, a eleio para o preenchimento dos cargos constantes do  1, II e III, poder ser feita em um nico escrutnio, obedecido o disposto nos  2 e 3.'
					},
			}
		}]
	},
	"61":{
		"caput":'As representaes partidrias podero constituir bloco parlamentar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Somente ser admitida a formao de bloco parlamentar que represente, no mnimo, um dcimo da composio do Senado.'
					},
			}
		}]
	},
	"62":{
		"caput":'O bloco parlamentar ter lder, a ser indicado dentre os lderes das representaes partidrias que o compem.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os demais lderes assumiro, preferencialmente, as funes de vice-lderes do bloco parlamentar, na ordem indicada pelo titular da liderana.'
					},
					"2":{
						"caput":'As lideranas dos partidos que se coligarem em bloco parlamentar perdem suas atribuies e prerrogativas regimentais.'
					},
			}
		}]
	},
	"63":{
		"caput":'(Revogado).'
	},
	"64":{
		"caput":'Aplica-se ao lder de bloco parlamentar o disposto no art. 66.'
	},
	"65":{
		"caput":'A Maioria, a Minoria e as representaes partidrias tero lderes e vice-lderes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A Maioria  integrada por bloco parlamentar ou representao partidria que represente a maioria absoluta da Casa.'
					},
					"2":{
						"caput":'Formada a Maioria, a Minoria ser aquela integrada pelo maior bloco parlamentar ou representao partidria que se lhe opuser.'
					},
					"3":{
						"caput":'A constituio da Maioria e da Minoria ser comunicada  Mesa pelos lderes dos blocos parlamentares ou das representaes partidrias que as compem.'
					},
					"4":{
						"caput":'O lder da maioria e o da minoria sero os lderes dos blocos parlamentares ou das representaes partidrias que as compem, e as funes de vice-liderana sero exercidas pelos demais lderes das representaes partidrias que integrem os respectivos blocos parlamentares.'
					},
					"5":{
						"caput":'As vantagens administrativas adicionais estabelecidas para os gabinetes das lideranas somente sero admitidas s representaes partidrias que tiverem, no mnimo, um vinte e sete avos da composio do Senado Federal.'
					},
					"6":{
						"caput":'Na hiptese de nenhum bloco parlamentar alcanar maioria absoluta, assume as funes constitucionais e regimentais da maioria o lder do bloco parlamentar ou representao partidria que tiver o maior nmero de integrantes, e da minoria, o lder do bloco parlamentar ou representao partidria que se lhe seguir em nmero de integrantes e que se lhe opuser.'
					},
					"7":{
						"caput":'A indicao dos lderes partidrios ser feita no incio da primeira e da terceira sesses legislativas de cada legislatura, e comunicada  Mesa em documento subscrito pela maioria dos membros da respectiva bancada, podendo a mesma maioria substitu-los em qualquer oportunidade.'
					},
					"8":{
						"caput":'Os vice-lderes das representaes partidrias sero indicados pelos respectivos lderes, na proporo de um vice-lder para cada grupo de trs integrantes de bloco parlamentar ou representao partidria, assegurado pelo menos um vice-lder e no computada a frao inferior a trs.'
					},
			}
		}]
	},
	"66":{
		"caput":' da competncia dos lderes das representaes partidrias, alm de outras atribuies regimentais, indicar os representantes das respectivas agremiaes nas comisses.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ausente ou impedido o lder, as suas atribuies sero exercidas pelo vice-lder.'
					},
			}
		}]
	},
	"66-A":{
		"caput":'O Presidente da Repblica poder indicar Senador para exercer a funo de lder do governo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O lder do governo poder indicar vice-lderes dentre os integrantes das representaes partidrias que apoiem o governo.'
					},
			}
		}]
	},
	"67":{
		"caput":'O Senado, atendendo a convite, poder se fazer representar em ato ou solenidade de cunho internacional, nacional ou regional, mediante deliberao do Plenrio por proposta do Presidente ou a requerimento de qualquer Senador ou comisso.'
	},
	"68":{
		"caput":'A representao externa far-se- por comisso ou por um Senador.'
	},
	"69":{
		"caput":' lcito ao Presidente avocar a representao do Senado quando se trate de ato de excepcional relevo.'
	},
	"70":{
		"caput":'Na impossibilidade de o Plenrio deliberar sobre a matria, ser facultado ao Presidente autorizar representao externa para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'chegada ou partida de personalidade de destaque na vida pblica nacional ou internacional;'
					},
					"2":{
						"caput":'solenidade de relevante expresso nacional ou internacional;'
					},
					"3":{
						"caput":'funeral ou cerimnia fnebre em que, regimentalmente, caiba essa representao.'
					},
			}
		}]
	},
	"71":{
		"caput":'O Senado ter comisses permanentes e temporrias (Const., art. 58).'
	},
	"72":{
		"caput":'As comisses permanentes, alm da Comisso Diretora, so as seguintes:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'Comisso de Assuntos Econmicos (CAE);'
					},
					"2":{
						"caput":'Comisso de Assuntos Sociais (CAS);'
					},
					"3":{
						"caput":'Comisso de Constituio, Justia e Cidadania (CCJ);'
					},
					"4":{
						"caput":'Comisso de Educao, Cultura e Esporte (CE);'
					},
					"5":{
						"caput":'Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor (CTFC);'
					},
					"6":{
						"caput":'Comisso de Direitos Humanos e Legislao Participativa (CDH);'
					},
					"7":{
						"caput":'Comisso de Relaes Exteriores e Defesa Nacional (CRE);'
					},
					"8":{
						"caput":'Comisso de Servios de Infraestrutura (CI);'
					},
					"9":{
						"caput":'Comisso de Desenvolvimento Regional e Turismo (CDR);'
					},
					"10":{
						"caput":'Comisso de Agricultura e Reforma Agrria (CRA);'
					},
					"11":{
						"caput":'Comisso de Cincia, Tecnologia, Inovao, Comunicao e Informtica (CCT);'
					},
					"12":{
						"caput":'Comisso Senado do Futuro (CSF);'
					},
					"13":{
						"caput":'Comisso de Meio Ambiente (CMA).'
					},
			}
		}]
	},
	"73":{
		"caput":'Ressalvada a Comisso Diretora, cabe s comisses permanentes, no mbito das respectivas competncias, criar subcomisses permanentes ou temporrias, at o mximo de quatro, mediante proposta de qualquer de seus integrantes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao funcionamento das subcomisses aplicar-se-o, no que couber, as disposies deste Regimento relativas ao funcionamento das comisses permanentes.'
					},
					"2":{
						"caput":'Os relatrios aprovados nas subcomisses sero submetidos  apreciao do Plenrio da respectiva comisso, sendo a deciso final, para todos os efeitos, proferida em nome desta.'
					},
			}
		}]
	},
	"74":{
		"caput":'As comisses temporrias sero:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'internas - as previstas no Regimento para finalidade especfica;'
					},
					"2":{
						"caput":'externas - destinadas a representar o Senado em congressos, solenidades e outros atos pblicos;'
					},
					"3":{
						"caput":'parlamentares de inqurito - criadas nos termos do art. 58,  3, da Constituio.'
					},
			}
		}]
	},
	"75":{
		"caput":'As comisses externas sero criadas por deliberao do Plenrio, a requerimento de qualquer Senador ou comisso, ou por proposta do Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento ou a proposta dever indicar o objetivo da comisso e o nmero dos respectivos membros.'
					},
			}
		}]
	},
	"76":{
		"caput":'As comisses temporrias se extinguem:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela concluso da sua tarefa; ou'
					},
					"2":{
						"caput":'ao trmino do respectivo prazo; e'
					},
					"3":{
						"caput":'ao trmino da sesso legislativa ordinria.'
					},
			}
		}]
	},
	"77":{
		"caput":'A Comisso Diretora  constituda dos titulares da Mesa, tendo as demais comisses permanentes o seguinte nmero de membros:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'Comisso de Assuntos Econmicos, 27;'
					},
					"2":{
						"caput":'Comisso de Assuntos Sociais, 21;'
					},
					"3":{
						"caput":'Comisso de Constituio, Justia e Cidadania, 27;'
					},
					"4":{
						"caput":'Comisso de Educao, Cultura e Esporte, 27;'
					},
					"5":{
						"caput":'Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor, 17.'
					},
					"6":{
						"caput":'Comisso de Direitos Humanos e Legislao Participativa, 19;'
					},
					"7":{
						"caput":'Comisso de Relaes Exteriores e Defesa Nacional, 19;'
					},
					"8":{
						"caput":'Comisso de Servios de Infraestrutura, 23;'
					},
					"9":{
						"caput":'Comisso de Desenvolvimento Regional e Turismo, 17;'
					},
					"10":{
						"caput":'Comisso de Agricultura e Reforma Agrria, 17;'
					},
					"11":{
						"caput":'Comisso de Cincia, Tecnologia, Inovao, Comunicao e Informtica, 17;'
					},
					"12":{
						"caput":'Comisso Senado do Futuro, 11;'
					},
					"13":{
						"caput":'Comisso de Meio Ambiente, 17.'
					},
			}
		}]
	},
	"78":{
		"caput":'Os membros das comisses sero designados pelo Presidente, por indicao escrita dos respectivos lderes, assegurada, tanto quanto possvel, a participao proporcional das representaes partidrias ou dos blocos parlamentares com atuao no Senado Federal (Const., art. 58,  1).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para fins de proporcionalidade, as representaes partidrias so fixadas pelos seus quantitativos  data da diplomao, salvo nos casos de posterior criao, fuso ou incorporao de partidos.'
					},
			}
		}]
	},
	"79":{
		"caput":'No incio de cada legislatura, os lderes, uma vez indicados, reunir-se-o para fixar a representao numrica dos partidos e dos blocos parlamentares nas comisses permanentes.'
	},
	"80":{
		"caput":'Fixada a representao prevista no art. 79, os lderes entregaro  Mesa, nos dois dias teis subsequentes, as indicaes dos titulares das comisses e, em ordem numrica, as dos respectivos suplentes.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Recebidas as indicaes, o Presidente far a designao das comisses.'
					},
			}
		}]
	},
	"81":{
		"caput":'O lugar na comisso pertence ao partido ou bloco parlamentar, competindo ao lder respectivo pedir, em documento escrito, a substituio, em qualquer circunstncia ou oportunidade, de titular ou suplente por ele indicado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A substituio de membro da comisso que se desligar do partido ao qual pertence o lugar na comisso no alterar a proporcionalidade estabelecida nos termos do pargrafo nico do art. 78 e do art. 79.'
					},
					"2":{
						"caput":'A substituio de Senador que exera a presidncia de comisso dever ser precedida de autorizao da maioria da respectiva bancada, salvo na hiptese de seu desligamento do partido que ali representar.'
					},
			}
		}]
	},
	"82":{
		"caput":'A designao dos membros das comisses temporrias ser feita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para as internas, nas oportunidades estabelecidas neste Regimento;'
					},
					"2":{
						"caput":'para as externas, imediatamente aps a aprovao do requerimento que der motivo  sua criao.'
					},
			}
		}]
	},
	"83":{
		"caput":'As comisses permanentes, exceto a Diretora, tero suplentes em nmero igual ao de titulares.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"84":{
		"caput":'Compete ao suplente substituir o membro da comisso:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'eventualmente, nos seus impedimentos, para quorum nas reunies;'
					},
					"2":{
						"caput":'por determinados perodos, nas hipteses previstas nos arts. 39, 40 e 43.'
					},
			}
		}]
	},
	"85":{
		"caput":'Em caso de impedimento temporrio de membro da comisso e no havendo suplente a convocar, o Presidente desta solicitar  Presidncia da Mesa a designao de substituto, devendo a escolha recair em Senador do mesmo partido ou bloco parlamentar do substitudo, salvo se os demais representantes do partido ou bloco no puderem ou no quiserem aceitar a designao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ausentes o Presidente e o Vice-Presidente da comisso, o Presidente do Senado poder designar, de ofcio, substitutos eventuais a fim de possibilitar o funcionamento do rgo.'
					},
					"2":{
						"caput":'Cessar o exerccio do substituto desde que o substitudo comparea  reunio da respectiva comisso.'
					},
			}
		}]
	},
	"86":{
		"caput":'A renncia a lugar em comisso far-se- em comunicao escrita  Mesa.'
	},
	"87":{
		"caput":'Impossibilitado de comparecer a qualquer reunio de comisso a que pertena, o Senador dever comunicar o fato ao Presidente a tempo de ser tomada a providncia regimental para a sua substituio.'
	},
	"88":{
		"caput":'No incio da legislatura, nos cinco dias teis que se seguirem  designao de seus membros, e na terceira sesso legislativa, nos cinco dias teis que se seguirem  indicao dos lderes, cada comisso reunir-se- para instalar seus trabalhos e eleger, em escrutnio secreto, o seu Presidente e o Vice-Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em caso do no cumprimento do disposto neste artigo, ficaro investidos nos cargos os dois titulares mais idosos, at que se realize a eleio.'
					},
					"2":{
						"caput":'Ocorrendo empate, a eleio ser repetida no dia seguinte; verificando-se novo empate, ser considerado eleito o mais idoso.'
					},
					"3":{
						"caput":'Na ausncia do Presidente e do Vice-Presidente, presidir a comisso o mais idoso dos titulares.'
					},
					"4":{
						"caput":'Em caso de vaga dos cargos de Presidente ou de Vice-Presidente, far-se- o preenchimento por meio de eleio realizada nos cinco dias teis que se seguirem  vacncia, salvo se faltarem sessenta dias ou menos para o trmino dos respectivos mandatos.'
					},
					"5":{
						"caput":'Aceitar funo prevista no art. 39, II, importa em renncia ao cargo de Presidente ou de Vice-Presidente de comisso.'
					},
					"6":{
						"caput":'Ao mandato de Presidente e de Vice-Presidente das comisses permanentes e de suas subcomisses aplica-se o disposto no art. 59.'
					},
			}
		}]
	},
	"89":{
		"caput":'Ao Presidente de comisso compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ordenar e dirigir os trabalhos da comisso;'
					},
					"2":{
						"caput":'dar-lhe conhecimento de toda a matria recebida;'
					},
					"3":{
						"caput":'designar, na comisso, relatores para as matrias;'
					},
					"4":{
						"caput":'designar, dentre os componentes da comisso, os membros das subcomisses e fixar a sua composio;'
					},
					"5":{
						"caput":'resolver as questes de ordem;'
					},
					"6":{
						"caput":'ser o elemento de comunicao da comisso com a Mesa, com as outras comisses e suas respectivas subcomisses e com os lderes;'
					},
					"7":{
						"caput":'convocar as suas reunies extraordinrias, de ofcio ou a requerimento de qualquer de seus membros, aprovado pela comisso;'
					},
					"8":{
						"caput":'promover a publicao das atas das reunies no Dirio do Senado Federal;'
					},
					"9":{
						"caput":'solicitar, em virtude de deliberao da comisso, os servios de funcionrios tcnicos para estudo de determinado trabalho, sem prejuzo das suas atividades nas reparties a que pertenam;'
					},
					"10":{
						"caput":'convidar, para o mesmo fim e na forma do inciso IX, tcnicos ou especialistas particulares e representantes de entidades ou associaes cientficas;'
					},
					"11":{
						"caput":'desempatar as votaes quando ostensivas;'
					},
					"12":{
						"caput":'distribuir matrias s subcomisses;'
					},
					"13":{
						"caput":'assinar o expediente da comisso.'
					},
			}
		}]
	},
	"90":{
		"caput":'s comisses compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'discutir e votar projeto de lei nos termos do art. 91 (Const., art. 58,  2, I);'
					},
					"2":{
						"caput":'realizar audincias pblicas com entidades da sociedade civil (Const., art. 58,  2, II);'
					},
					"3":{
						"caput":'convocar Ministros de Estado ou quaisquer titulares de rgos diretamente subordinados  Presidncia da Repblica para prestarem informaes sobre assuntos inerentes a suas atribuies e ouvir os Ministros quando no exerccio da faculdade prevista no art. 50,  1, da Constituio (Const., arts. 50 e 58,  2, III);'
					},
					"4":{
						"caput":'receber peties, reclamaes, representaes ou queixas de qualquer pessoa contra atos ou omisses das autoridades ou entidades pblicas (Const., art. 58,  2, IV);'
					},
					"5":{
						"caput":'solicitar depoimento de qualquer autoridade ou cidado (Const., art. 58,  2, V);'
					},
					"6":{
						"caput":'apreciar programas de obras, planos nacionais, regionais e setoriais de desenvolvimento e sobre eles emitir parecer (Const., art. 58,  2, VI);'
					},
					"7":{
						"caput":'propor a sustao dos atos normativos do Poder Executivo que exorbitem do poder regulamentar (Const., art. 49, V);'
					},
					"8":{
						"caput":'acompanhar junto ao Governo a elaborao da proposta oramentria, bem como sua execuo;'
					},
					"9":{
						"caput":'acompanhar, fiscalizar e controlar as polticas governamentais pertinentes s reas de sua competncia;'
					},
					"10":{
						"caput":'exercer a fiscalizao e controle dos atos do Poder Executivo, includos os da administrao indireta, e quanto s questes relativas  competncia privativa do Senado (Const., arts. 49, X, e 52, V a IX);'
					},
					"11":{
						"caput":'estudar qualquer assunto compreendido nas atribuies do Senado, propondo as medidas legislativas cabveis;'
					},
					"12":{
						"caput":'opinar sobre o mrito das proposies submetidas ao seu exame, emitindo o respectivo parecer;'
					},
					"13":{
						"caput":'realizar diligncia.'
					},
			}
		}]
	},
	"91":{
		"caput":'s comisses, no mbito de suas atribuies, cabe, dispensada a competncia do Plenrio, nos termos do art. 58,  2, I, da Constituio, discutir e votar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'projetos de lei ordinria de autoria de Senador, ressalvado projeto de cdigo;'
					},
					"2":{
						"caput":'projetos de resoluo que versem sobre a suspenso da execuo, no todo ou em parte, de lei declarada inconstitucional por deciso definitiva do Supremo Tribunal Federal (Const., art. 52, X).'
					},
					"3":{
						"caput":'projetos de decreto legislativo de que trata o  1 do art. 223 da Constituio Federal.'
					},
			}
		}]
	},
	"92":{
		"caput":'Aplicam-se  tramitao dos projetos e demais proposies submetidas  deliberao terminativa das comisses as disposies relativas a turnos, prazos, emendas e demais formalidades e ritos exigidos para as matrias submetidas  apreciao do Plenrio do Senado.'
	},
	"93":{
		"caput":'A audincia pblica ser realizada pela comisso para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'instruir matria sob sua apreciao;'
					},
					"2":{
						"caput":'tratar de assunto de interesse pblico relevante.'
					},
			}
		}]
	},
	"94":{
		"caput":'Os depoimentos sero prestados por escrito e de forma conclusiva.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na hiptese de haver defensores e opositores, relativamente  matria objeto de exame, a comisso proceder de forma que possibilite a audincia de todas as partes interessadas.'
					},
					"2":{
						"caput":'Os membros da comisso podero, terminada a leitura, interpelar o orador exclusivamente sobre a exposio lida, por prazo nunca superior a trs minutos.'
					},
					"3":{
						"caput":'O orador ter o mesmo prazo para responder a cada Senador, sendo lhe vedado interpelar os membros da comisso.'
					},
			}
		}]
	},
	"95":{
		"caput":'Da reunio de audincia pblica ser lavrada ata, arquivando-se, no mbito da comisso, os pronunciamentos escritos e documentos que os acompanharem.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ser admitido, a qualquer tempo, a requerimento de Senador, o traslado de peas.'
					},
			}
		}]
	},
	"96":{
		"caput":'A comisso receber peties, reclamaes, representaes ou queixas de qualquer pessoa contra ato ou omisso de autoridade ou entidade pblica sobre assunto de sua competncia.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os expedientes referidos neste artigo devero ser encaminhados por escrito, com identificao do autor e sero distribudos a um relator que os apreciar e apresentar relatrio com sugestes quanto s providncias a serem tomadas pela comisso, pela Mesa ou pelo Ministrio Pblico.'
					},
					"2":{
						"caput":'O relatrio ser discutido e votado na comisso, devendo concluir por projeto de resoluo se contiver providncia a ser tomada por outra instncia que no a da prpria comisso.'
					},
			}
		}]
	},
	"96-A":{
		"caput":'Os dirigentes mximos das agncias reguladoras comparecero ao Senado Federal, em periodicidade anual, para prestar contas sobre o exerccio de suas atribuies e o desempenho da agncia, bem como para apresentar avaliao das polticas pblicas no mbito de suas competncias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O comparecimento de que trata o caput ocorrer em reunio conjunta da comisso temtica pertinente e das Comisses de Assuntos Econmicos e de Constituio, Justia e Cidadania.'
					},
			}
		}]
	},
	"96-B":{
		"caput":'No desempenho da competncia prevista no inciso IX do art. 90, as comisses permanentes selecionaro, na rea de sua competncia, polticas pblicas desenvolvidas no mbito do Poder Executivo, para serem avaliadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Cada comisso permanente selecionar as polticas pblicas at o ltimo dia til do ms de maro de cada ano.'
					},
					"2":{
						"caput":'Para realizar a avaliao de que trata o caput, que se estender aos impactos das polticas pblicas e s atividades meio de suporte para sua execuo, podero ser solicitadas informaes e documentos a rgos do Poder Executivo e ao Tribunal de Contas da Unio, bem como a entidades da sociedade civil, nos termos do art. 50 da Constituio Federal.'
					},
					"3":{
						"caput":'Ao final da sesso legislativa, a comisso apresentar relatrio com as concluses da avaliao realizada.'
					},
					"4":{
						"caput":'A Consultoria Legislativa e a Consultoria de Oramentos, Fiscalizao e Controle do Senado Federal elaboraro estudos e relatrios tcnicos que subsidiaro os trabalhos da avaliao de que trata o caput.'
					},
					"5":{
						"caput":'O Instituto de Pesquisa DataSenado produzir anlises e relatrios estatsticos para subsidiar a avaliao de que trata o caput.'
					},
			}
		}]
	},
	"97":{
		"caput":'s comisses permanentes compete estudar e emitir parecer sobre os assuntos submetidos ao seu exame.'
	},
	"98":{
		"caput":' Comisso Diretora compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer a administrao interna do Senado nos termos das atribuies fixadas no seu Regulamento Administrativo;'
					},
					"2":{
						"caput":'regulamentar a polcia interna;'
					},
					"3":{
						"caput":'propor ao Senado projeto de resoluo dispondo sobre sua organizao, funcionamento, polcia, criao, transformao ou extino de cargos, empregos e funes de seus servios e a iniciativa de lei para a fixao da respectiva remunerao, observados os parmetros estabelecidos na lei de diretrizes oramentrias (Const., art. 52, XIII);'
					},
					"4":{
						"caput":'emitir, obrigatoriamente, parecer sobre as proposies que digam respeito ao servio e ao pessoal da Secretaria do Senado e as que alterem este Regimento, salvo o disposto no art. 401,  2, II;'
					},
					"5":{
						"caput":'elaborar a redao final das proposies de iniciativa do Senado e das emendas e projetos da Cmara dos Deputados aprovados pelo Plenrio, escoimando-os dos vcios de linguagem, das impropriedades de expresso, defeitos de tcnica legislativa, clusulas de justificao e palavras desnecessrias.'
					},
					"6":{
						"caput":'apreciar requerimento de tramitao em conjunto de proposio regulando a mesma matria e o recurso de que trata o art. 48,  3, exceto se a proposio constar da Ordem do Dia ou for objeto de parecer aprovado em comisso (art. 258).'
					},
			}
		}]
	},
	"99":{
		"caput":' Comisso de Assuntos Econmicos compete opinar sobre proposies pertinentes aos seguintes assuntos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'aspecto econmico e financeiro de qualquer matria que lhe seja submetida por despacho do Presidente, por deliberao do Plenrio, ou por consulta de comisso, e, ainda, quando, em virtude desses aspectos, houver recurso de deciso terminativa de comisso para o Plenrio;'
					},
					"2":{
						"caput":'(Revogado);'
					},
					"3":{
						"caput":'problemas econmicos do Pas, poltica de crdito, cmbio, seguro e transferncia de valores, comrcio exterior e interestadual, sistema monetrio, bancrio e de medidas, ttulos e garantia dos metais, sistema de poupana, consrcio e sorteio e propaganda comercial;'
					},
					"4":{
						"caput":'tributos, tarifas, emprstimos compulsrios, finanas pblicas, normas gerais sobre direito tributrio, financeiro e econmico; oramento, juntas comerciais, conflitos de competncia em matria tributria entre a Unio, os Estados, o Distrito Federal e os Municpios, dvida pblica e fiscalizao das instituies financeiras;'
					},
					"5":{
						"caput":'escolha dos Ministros do Tribunal de Contas da Unio (Const., arts. 49, XIII, e 52, III, b), e do presidente e diretores do Banco Central (Const., art. 52, III, d);'
					},
					"6":{
						"caput":'matrias a que se referem os arts. 389, 393 e 394;'
					},
					"7":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"99-A":{
		"caput":' Comisso de Assuntos Econmicos compete, ainda, avaliar periodicamente a funcionalidade do Sistema Tributrio Nacional, em sua estrutura e seus componentes, e o desempenho das administraes tributrias da Unio, dos Estados, do Distrito Federal e dos Municpios.'
	},
	"100":{
		"caput":' Comisso de Assuntos Sociais compete opinar sobre proposies que digam respeito a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'relaes de trabalho, organizao do sistema nacional de emprego e condio para o exerccio de profisses, seguridade social, previdncia social, populao indgena e assistncia social;'
					},
					"2":{
						"caput":'proteo e defesa da sade, condies e requisitos para remoo de rgos, tecidos e substncias humanas para fins de transplante, pesquisa, tratamento e coleta de sangue humano e seus derivados, produo, controle e fiscalizao de medicamentos, saneamento, inspeo e fiscalizao de alimentos e competncia do Sistema nico de Sade;'
					},
					"3":{
						"caput":'(Revogado);'
					},
					"4":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"101":{
		"caput":' Comisso de Constituio, Justia e Cidadania compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'opinar sobre a constitucionalidade, juridicidade e regimentalidade das matrias que lhe forem submetidas por deliberao do Plenrio, por despacho da Presidncia, por consulta de qualquer comisso, ou quando em virtude desses aspectos houver recurso de deciso terminativa de comisso para o Plenrio;'
					},
					"2":{
						"caput":'ressalvadas as atribuies das demais comisses, emitir parecer, quanto ao mrito, sobre as matrias de competncia da Unio, especialmente as seguintes:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de Estado e Territrios, incorporao ou desmembramento de reas a eles pertencentes;'
									},
									"2":{
										"caput":'de defesa, estado de stio e interveno federal (Const., art. 49, IV), requisies civis e anistia;'
									},
									"3":{
										"caput":'pblica, corpos de bombeiros militares, polcia, inclusive martima, area de fronteiras, rodoviria e ferroviria;'
									},
									"4":{
										"caput":'civil, comercial, penal, processual, eleitoral, aeronutico, espacial, martimo e penitencirio;'
									},
									"5":{
										"caput":'dos smbolos nacionais, nacionalidade, cidadania e naturalizao, extradio e expulso de estrangeiros, emigrao e imigrao;'
									},
									"6":{
										"caput":'do servio pblico civil da Unio e servidores da administrao direta e indireta do Poder Judicirio, do Ministrio Pblico e dos Territrios;'
									},
									"7":{
										"caput":'gerais de licitao e contratao, em todas as modalidades, para as administraes pblicas diretas, autrquicas e fundacionais da Unio, Estados, Distrito Federal e Municpios, obedecido o disposto no art. 37, XXI, da Constituio, e para as empresas pblicas e sociedades de economia mista, nos termos do art. 173,  1, III, tambm da Constituio (Const., art. 22, XXVII);'
									},
									"8":{
										"caput":'de mandato de Senador (Const., art. 55), pedido de licena de incorporao de Senador s Foras Armadas (Const., art. 53,  7);'
									},
									"9":{
										"caput":'de Ministro do Supremo Tribunal Federal, dos Tribunais Superiores e de Governador de Territrio, escolha e destituio do Procurador-Geral da Repblica (Const., art. 52, III, a, c e e);'
									},
									"10":{
										"caput":'temporria da sede do Governo Federal;'
									},
									"11":{
										"caput":'pblicos, organizao administrativa e judiciria do Ministrio Pblico e Defensoria Pblica da Unio e dos Territrios, organizao judiciria do Ministrio Pblico e da Defensoria Pblica do Distrito Federal;'
									},
									"12":{
										"caput":'dos Estados e bens do domnio da Unio;'
									},
									"13":{
										"caput":'e inquilinato;'
									},
									"14":{
										"caput":'funcionamento e processo do juizado de pequenas causas, assistncia jurdica e defensoria pblica, custas dos servios forenses;'
									},
									"15":{
										"caput":'a que se refere o art. 96, II, da Constituio Federal;'
									},
							}
						}]
					},
					"3":{
						"caput":'propor, por projeto de resoluo, a suspenso, no todo ou em parte, de leis declaradas inconstitucionais pelo Supremo Tribunal Federal (Const., art. 52, X);'
					},
					"4":{
						"caput":'opinar, em cumprimento a despacho da Presidncia, sobre as emendas apresentadas como de redao, nas condies previstas no pargrafo nico do art. 234;'
					},
					"5":{
						"caput":'opinar sobre assunto de natureza jurdica ou constitucional que lhe seja submetido, em consulta, pelo Presidente, de ofcio, ou por deliberao do Plenrio, ou por outra comisso;'
					},
					"6":{
						"caput":'opinar sobre recursos interpostos s decises da Presidncia;'
					},
					"7":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"101-A":{
		"caput":'O Ministro de Estado da Justia comparecer anualmente  Comisso de Constituio, Justia e Cidadania do Senado Federal para prestar informaes e esclarecimentos a respeito da atuao de sua Pasta, bem como para apresentar avaliao das polticas pblicas no mbito de suas competncias.'
	},
	"102":{
		"caput":' Comisso de Educao, Cultura e Esporte compete opinar sobre proposies que versem sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'normas gerais sobre educao, cultura, ensino e desportos, instituies educativas e culturais, diretrizes e bases da educao nacional e salrio-educao;'
					},
					"2":{
						"caput":'diverso e espetculos pblicos, criaes artsticas, datas comemorativas e homenagens cvicas;'
					},
					"3":{
						"caput":'formao e aperfeioamento de recursos humanos;'
					},
					"4":{
						"caput":'(Revogado);'
					},
					"5":{
						"caput":'(Revogado);'
					},
					"6":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"102-A":{
		"caput":' Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor, alm da aplicao, no que couber, do disposto no art. 90 e sem prejuzo das atribuies das demais comisses, compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'exercer a fiscalizao e o controle dos atos do Poder Executivo, includos os da administrao indireta, podendo, para esse fim:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'a eficcia, eficincia e economicidade dos projetos e programas de governo no plano nacional, no regional e no setorial de desenvolvimento, emitindo parecer conclusivo;'
									},
									"2":{
										"caput":'a compatibilidade da execuo oramentria com os planos e programas governamentais e destes com os objetivos aprovados em lei;'
									},
									"3":{
										"caput":'por escrito, informaes  administrao direta e indireta, bem como requisitar documentos pblicos necessrios  elucidao do ato objeto de fiscalizao;'
									},
									"4":{
										"caput":'as contas dos administradores e demais responsveis por dinheiros, bens e valores pblicos da administrao direta e indireta, includas as fundaes e sociedades institudas e mantidas pelo poder pblico federal, notadamente quando houver indcios de perda, extravio ou irregularidade de qualquer natureza de que resulte prejuzo ao Errio;'
									},
									"5":{
										"caput":'a efetivao de percias, bem como solicitar ao Tribunal de Contas da Unio que realize inspees ou auditorias de natureza contbil, financeira, oramentria, operacional e patrimonial nas unidades administrativas da Unio e demais entidades referidas na alnea d;'
									},
									"6":{
										"caput":'as contas nacionais das empresas supranacionais de cujo capital social a Unio participe de forma direta ou indireta, bem assim a aplicao de quaisquer recursos repassados mediante convnio, acordo, ajuste ou outros instrumentos congneres, a Estado, ao Distrito Federal ou a Municpio;'
									},
									"7":{
										"caput":'a interao do Senado Federal com os rgos do Poder Executivo que, pela natureza de suas atividades, possam dispor ou gerar dados de que necessite para o exerccio de fiscalizao e controle;'
									},
									"8":{
										"caput":'a interao do Senado Federal com os rgos do Poder Judicirio e do Ministrio Pblico que, pela natureza de suas atividades, possam propiciar ou gerar dados de que necessite para o exerccio de fiscalizao e controle;'
									},
									"9":{
										"caput":'ao Plenrio do Senado as providncias cabveis em relao aos resultados da avaliao, inclusive quanto ao resultado das diligncias realizadas pelo Tribunal de Contas da Unio;'
									},
							}
						}]
					},
					"2":{
						"caput":'opinar sobre matrias pertinentes aos seguintes temas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":' corrupo;'
									},
									"2":{
										"caput":'e modernizao das prticas gerenciais na administrao pblica federal direta e indireta;'
									},
									"3":{
										"caput":'eficaz, efetiva e eficiente de servios pblicos;'
									},
									"4":{
										"caput":'e prestao de contas e de informaes  populao, com foco na responsabilidade da gesto fiscal e dos gastos pblicos, bem como nas necessidades dos cidados;'
									},
									"5":{
										"caput":'e incentivo, na administrao pblica, de novos meios de prestao de informaes  sociedade, tais como redes, stios e portais eletrnicos, e apoio a Estados e Municpios para a implantao desses meios;'
									},
							}
						}]
					},
					"3":{
						"caput":'opinar sobre assuntos pertinentes  defesa do consumidor, especialmente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'elaborar e propor normas e medidas voltadas  melhoria contnua das relaes de mercado, em especial as que envolvem fornecedores e consumidores;'
									},
									"2":{
										"caput":'os instrumentos legislativos reguladores, contratuais e penais, referentes aos direitos dos consumidores e dos fornecedores, com nfase em condies, limites e uso de informaes, responsabilidade civil, respeito  privacidade, aos direitos autorais, s patentes e similares;'
									},
									"3":{
										"caput":'as polticas e as aes desenvolvidas pelo Poder Pblico relativas  defesa dos direitos do consumidor,  defesa da concorrncia e  represso da formao e da atuao ilcita de monoplios;'
									},
									"4":{
										"caput":'denncias e denunciar prticas referentes a abuso do poder econmico, qualidade e apresentao de produtos, tcnicas de propaganda e publicidade nocivas ou enganosas;'
									},
									"5":{
										"caput":'as relaes entre custo e preo de produtos, bens e servios, com vistas a estabelecer normas de represso  usura, aos lucros excessivos, ao aumento indiscriminado de preos e  cartelizao de segmentos do mercado;'
									},
									"6":{
										"caput":'as condies de concorrncia com nfase na defesa dos produtores e dos fornecedores nacionais, considerados os interesses dos consumidores e a soberania nacional;'
									},
									"7":{
										"caput":'e disponibilizar estudos, dados estatsticos e informaes, no mbito de suas competncias.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'No exerccio da competncia de fiscalizao e controle prevista no inciso I do caput, a Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor:',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'remeter cpia da documentao pertinente ao Ministrio Pblico, a fim de que este promova a ao cabvel, de natureza cvel ou penal, se for constatada a existncia de irregularidade;'
																	},
																	"2":{
																		"caput":'poder atuar, mediante solicitao, em colaborao com as comisses permanentes e temporrias, includas as comisses parlamentares de inqurito, com vistas ao adequado exerccio de suas atividades.'
																	},
															}
														}]
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"102-B":{
		"caput":'A fiscalizao e o controle dos atos do Poder Executivo, inclusive os da administrao indireta, pela Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor obedecero s seguintes regras:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a proposta de fiscalizao e controle poder ser apresentada por qualquer membro ou Senador  Comisso, com especfica indicao do ato e fundamentao da providncia objetivada;'
					},
					"2":{
						"caput":'a proposta ser relatada previamente, quanto  oportunidade e convenincia da medida e ao alcance jurdico, administrativo, poltico, econmico, social ou oramentrio do ato impugnado, definindo-se o plano de execuo e a metodologia de avaliao;'
					},
					"3":{
						"caput":'aprovado o relatrio prvio pela Comisso, o relator poder solicitar os recursos e o assessoramento necessrios ao bom desempenho da Comisso, incumbindo  Mesa e  Administrao da Casa o atendimento preferencial das providncias requeridas. Rejeitado o relatrio, a matria ser encaminhada ao Arquivo;'
					},
					"4":{
						"caput":'o relatrio final da fiscalizao e controle, em termos de comprovao da legalidade do ato, avaliao poltica, administrativa, social e econmica de sua edio, e quanto  eficcia dos'
					},
					"5":{
						"caput":'a gesto oramentria, financeira e patrimonial, obedecer, no que concerne  tramitao, as normas do art. 102-C.'
					},
			}
		}]
	},
	"102-C":{
		"caput":'Ao termo dos trabalhos, a Comisso apresentar relatrio circunstanciado, com suas concluses, que ser publicado no Dirio do Senado Federal e encaminhado.',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":' Mesa, para as providncias de alada desta, ou ao Plenrio, oferecendo, conforme o caso, projeto de lei, de decreto legislativo, de resoluo ou indicao;'
					},
					"2":{
						"caput":'ao Ministrio Pblico ou  Advocacia-Geral da Unio, com cpia da documentao, para que promova a responsabilidade civil ou criminal por infraes apuradas e adote outras medidas decorrentes de suas funes institucionais;'
					},
					"3":{
						"caput":'ao Poder Executivo, para adotar as providncias saneadoras de carter disciplinar e administrativo decorrentes do disposto no art. 37,  2 a 6, da Constituio Federal, e demais disposies constitucionais e legais aplicveis;'
					},
					"4":{
						"caput":' comisso permanente que tenha maior pertinncia com a matria, a qual incumbir o atendimento do prescrito no inciso III;'
					},
					"5":{
						"caput":' Comisso Mista de Planos, Oramentos Pblicos e Fiscalizao e ao Tribunal de Contas da Unio, para as providncias previstas no art. 71 da Constituio Federal.'
					},
			}
		}]
	},
	"102-D":{
		"caput":'Aplicam-se  Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor as normas regimentais pertinentes s demais comisses permanentes, no que no conflitarem com os termos das disposies constantes dos arts. 102-A a 102-C.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ocorrendo a hiptese de exerccio concorrente de competncia fiscalizadora por duas ou mais comisses sobre os mesmos fatos, os trabalhos se desdobraro em reunies conjuntas, por iniciativa do Presidente de um dos rgos ou de um ou mais de seus membros.'
					},
					"2":{
						"caput":'A Comisso de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor poder, se houver motivo suficiente, comunicar fatos investigados  comisso correspondente da Cmara dos Deputados, para que esta adote a providncia que considerar cabvel.'
					},
			}
		}]
	},
	"102-E":{
		"caput":' Comisso de Direitos Humanos e Legislao Participativa compete opinar sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sugestes legislativas apresentadas por associaes e rgos de classe, sindicatos e entidades organizadas da sociedade civil, exceto partidos polticos com representao poltica no Congresso Nacional;'
					},
					"2":{
						"caput":'pareceres tcnicos, exposies e propostas oriundas de entidades cientficas e culturais e de qualquer das entidades mencionadas no inciso I;'
					},
					"3":{
						"caput":'garantia e promoo dos direitos humanos;'
					},
					"4":{
						"caput":'direitos da mulher;'
					},
					"5":{
						"caput":'proteo  famlia;'
					},
					"6":{
						"caput":'proteo e integrao social das pessoas portadoras de deficincias e de proteo  infncia,  juventude e aos idosos;'
					},
					"7":{
						"caput":'fiscalizao, acompanhamento, avaliao e controle das polticas governamentais relativas aos direitos humanos, aos direitos da mulher, aos direitos das minorias sociais ou tnicas, aos direitos dos estrangeiros,  proteo e integrao das pessoas portadoras de deficincia e  proteo  infncia,  juventude e aos idosos.'
					},
			}
		}]
	},
	"102-F":{
		"caput":' Comisso de Meio Ambiente compete opinar sobre assuntos pertinentes  defesa do meio ambiente, especialmente:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proteo do meio ambiente, controle da poluio, conservao da natureza e defesa do solo, dos recursos naturais e genticos, das florestas, da caa, da pesca, da fauna, da flora e dos recursos hdricos;'
					},
					"2":{
						"caput":'poltica e sistema nacional de meio ambiente;'
					},
					"3":{
						"caput":'preservao, conservao, explorao e manejo de florestas e da biodiversidade;'
					},
					"4":{
						"caput":'conservao e gerenciamento do uso do solo e dos recursos hdricos, no tocante ao meio ambiente e ao desenvolvimento sustentvel;'
					},
					"5":{
						"caput":'fiscalizao dos alimentos e dos produtos e insumos agrcolas e pecurios, no tocante ao meio ambiente e ao desenvolvimento sustentvel;'
					},
					"6":{
						"caput":'direito ambiental;'
					},
					"7":{
						"caput":'agncias reguladoras na rea de meio ambiente, inclusive a Agncia Nacional de guas (ANA);'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"103":{
		"caput":' Comisso de Relaes Exteriores e Defesa Nacional compete emitir parecer sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proposies referentes aos atos e relaes internacionais (Const., art. 49, I) e ao Ministrio das Relaes Exteriores;'
					},
					"2":{
						"caput":'comrcio exterior;'
					},
					"3":{
						"caput":'indicao de nome para chefe de misso diplomtica de carter permanente junto a governos estrangeiros e das organizaes internacionais de que o Brasil faa parte (Const., art. 52, IV);'
					},
					"4":{
						"caput":'(Revogado);'
					},
					"5":{
						"caput":'Foras Armadas de terra, mar e ar, requisies militares, passagem de foras estrangeiras e sua permanncia no territrio nacional, questes de fronteiras e limites do territrio nacional, espao areo e martimo, declarao de guerra e celebrao de paz (Const., art. 49, II);'
					},
					"6":{
						"caput":'assuntos referentes  Organizao das Naes Unidas e entidades internacionais de qualquer natureza;'
					},
					"7":{
						"caput":'autorizao para o Presidente ou o Vice-Presidente da Repblica se ausentarem do territrio nacional (Const., art. 49, III);'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104":{
		"caput":' Comisso de Servios de Infraestrutura compete opinar sobre matrias pertinentes a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'transportes de terra, mar e ar, obras pblicas em geral, minas, recursos geolgicos, servios de telecomunicaes, parcerias pblico-privadas e agncias reguladoras pertinentes;'
					},
					"2":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-A":{
		"caput":' Comisso de Desenvolvimento Regional e Turismo compete opinar sobre matrias pertinentes a:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'proposies que tratem de assuntos referentes s desigualdades regionais e s polticas de desenvolvimento regional, dos Estados e dos Municpios;'
					},
					"2":{
						"caput":'planos regionais de desenvolvimento econmico e social;'
					},
					"3":{
						"caput":'programas, projetos, investimentos e incentivos voltados para o desenvolvimento regional;'
					},
					"4":{
						"caput":'integrao regional;'
					},
					"5":{
						"caput":'agncias e organismos de desenvolvimento regional;'
					},
					"6":{
						"caput":'proposies que tratem de assuntos referentes ao turismo;'
					},
					"7":{
						"caput":'polticas relativas ao turismo;'
					},
					"8":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-B":{
		"caput":' Comisso de Agricultura e Reforma Agrria compete opinar sobre proposies pertinentes aos seguintes temas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'direito agrrio;'
					},
					"2":{
						"caput":'planejamento, acompanhamento e execuo da poltica agrcola e fundiria;'
					},
					"3":{
						"caput":'agricultura, pecuria e abastecimento;'
					},
					"4":{
						"caput":'agricultura familiar e segurana alimentar;'
					},
					"5":{
						"caput":'silvicultura, aquicultura e pesca;'
					},
					"6":{
						"caput":'comercializao e fiscalizao de produtos e insumos, inspeo e fiscalizao de alimentos, vigilncia e defesa sanitria animal e vegetal;'
					},
					"7":{
						"caput":'irrigao e drenagem;'
					},
					"8":{
						"caput":'uso e conservao do solo na agricultura;'
					},
					"9":{
						"caput":'utilizao e conservao, na agricultura, dos recursos hdricos e genticos;'
					},
					"10":{
						"caput":'poltica de investimentos e financiamentos agropecurios, seguro rural e endividamento rural;'
					},
					"11":{
						"caput":'tributao da atividade rural;'
					},
					"12":{
						"caput":'alienao ou concesso de terras pblicas com rea superior a dois mil e quinhentos hectares, aquisio ou arrendamento de propriedade rural por pessoa fsica ou jurdica estrangeira, definio da pequena e da mdia propriedade rural;'
					},
					"13":{
						"caput":'uso ou posse temporria da terra e regularizao dominial de terras rurais e de sua ocupao;'
					},
					"14":{
						"caput":'colonizao e reforma agrria;'
					},
					"15":{
						"caput":'cooperativismo e associativismo rurais;'
					},
					"16":{
						"caput":'emprego, previdncia e renda rurais;'
					},
					"17":{
						"caput":'polticas de apoio s pequenas e mdias propriedades rurais;'
					},
					"18":{
						"caput":'poltica de desenvolvimento tecnolgico da agropecuria, mediante estmulos fiscais, financeiros e creditcios  pesquisa e experimentao agrcola, pesquisa, plantio e comercializao de organismos geneticamente modificados;'
					},
					"19":{
						"caput":'extenso rural;'
					},
					"20":{
						"caput":'organizao do ensino rural;'
					},
					"21":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-C":{
		"caput":' Comisso de Cincia, Tecnologia, Inovao, Comunicao e Informtica compete opinar sobre proposies pertinentes aos seguintes temas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'desenvolvimento cientfico, tecnolgico e inovao tecnolgica;'
					},
					"2":{
						"caput":'poltica nacional de cincia, tecnologia, inovao, comunicao e informtica;'
					},
					"3":{
						"caput":'organizao institucional do setor;'
					},
					"4":{
						"caput":'acordos de cooperao e inovao com outros pases e organismos internacionais na rea;'
					},
					"5":{
						"caput":'propriedade intelectual;'
					},
					"6":{
						"caput":'criaes cientficas e tecnolgicas, informtica, atividades nucleares de qualquer natureza, transporte e utilizao de materiais radioativos, apoio e estmulo  pesquisa e criao de tecnologia;'
					},
					"7":{
						"caput":'comunicao, imprensa, radiodifuso, televiso, outorga e renovao de concesso, permisso e autorizao para servios de radiodifuso sonora e de sons e imagens;'
					},
					"8":{
						"caput":'regulamentao, controle e questes ticas referentes a pesquisa e desenvolvimento cientfico e tecnolgico, inovao tecnolgica, comunicao e informtica;'
					},
					"9":{
						"caput":'outros assuntos correlatos.'
					},
			}
		}]
	},
	"104-D":{
		"caput":' Comisso Senado do Futuro compete promover discusses sobre grandes temas e o futuro do Pas, bem como aprimorar a atuao do Senado nessas questes.'
	},
	"104-E":{
		"caput":'(Revogado).'
	},
	"105":{
		"caput":'s comisses temporrias compete o desempenho das atribuies que lhes forem expressamente deferidas.'
	},
	"106":{
		"caput":'As comisses reunir-se-o nas dependncias do edifcio do Senado Federal.'
	},
	"107":{
		"caput":'As reunies das comisses permanentes realizar-se-o:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se ordinrias, semanalmente, durante a sesso legislativa ordinria, nos seguintes dias e horrios:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de Assuntos Econmicos: s teras-feiras, dez horas;'
									},
									"2":{
										"caput":'de Servios de Infraestrutura: s teras-feiras, quatorze horas;'
									},
									"3":{
										"caput":'de Constituio, Justia e Cidadania: s quartas-feiras, dez horas;'
									},
									"4":{
										"caput":'de Assuntos Sociais: s quintas-feiras, onze horas e trinta minutos;'
									},
									"5":{
										"caput":'de Relaes Exteriores e Defesa Nacional: s quintas-feiras, dez horas;'
									},
									"6":{
										"caput":'de Educao, Cultura e Esporte: s teras-feiras, onze horas;'
									},
									"7":{
										"caput":'de Transparncia, Governana, Fiscalizao e Controle e Defesa do Consumidor: s teras-feiras, onze horas e trinta minutos;'
									},
									"8":{
										"caput":'de Direitos Humanos e Legislao Participativa: s teras-feiras, doze horas;'
									},
									"9":{
										"caput":'de Desenvolvimento Regional e Turismo: s quartas-feiras, quatorze horas;'
									},
									"10":{
										"caput":'de Agricultura e Reforma Agrria: s quintas-feiras, doze horas;'
									},
									"11":{
										"caput":'de Cincia, Tecnologia, Inovao, Comunicao e Informtica: s quartas-feiras, dezoito horas;'
									},
									"12":{
										"caput":'de Meio Ambiente: s quartas-feiras, s onze horas e trinta minutos.'
									},
							}
						}]
					},
					"2":{
						"caput":'se extraordinrias, mediante convocao especial para dia, horrio e fim indicados, observando-se, no que for aplicvel, o disposto neste Regimento sobre a convocao de sesses extraordinrias do Senado;'
					},
					"3":{
						"caput":'as comisses parlamentares de inqurito reunir-se-o em horrio diverso do estabelecido para o funcionamento das Comisses Permanentes.'
					},
			}
		}]
	},
	"108":{
		"caput":'As reunies das comisses sero iniciadas com a presena de, no mnimo, um quinto de sua composio, salvo o disposto no  3 do art. 93.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A pauta dos trabalhos das comisses, salvo em caso de urgncia, ser disponibilizada em meio eletrnico no portal do Senado Federal, com antecedncia mnima de 2 (dois) dias teis.'
					},
					"2":{
						"caput":' facultada a utilizao de sistema biomtrico de identificao no registro de presena dos membros da comisso.'
					},
					"3":{
						"caput":'A suspenso de reunio de comisso somente ser permitida quando sua continuao ocorrer em data e hora previamente estabelecidas.'
					},
			}
		}]
	},
	"109":{
		"caput":'A comisso deliberar por maioria de votos, presente a maioria de seus membros, sendo as deliberaes terminativas tomadas pelo processo nominal.'
	},
	"110":{
		"caput":'As reunies sero pblicas, salvo os casos expressos neste Regimento ou quando o deliberar a comisso.'
	},
	"111":{
		"caput":'Os trabalhos das comisses iniciar-se-o, salvo deliberao em contrrio, pela leitura e discusso da ata da reunio anterior que, se aprovada, ser assinada pelo Presidente.'
	},
	"112":{
		"caput":' facultado a qualquer Senador assistir s reunies das comisses, discutir o assunto em debate, pelo prazo por elas prefixado, e enviar-lhes, por escrito, informaes ou esclarecimentos.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'As informaes ou esclarecimentos apresentados sero impressos com os pareceres, se o autor o requerer e a comisso o deferir.'
					},
			}
		}]
	},
	"113":{
		"caput":'O estudo de qualquer matria poder ser feito em reunio conjunta de duas ou mais comisses, por iniciativa de qualquer delas, aceita pelas demais, sob a direo do Presidente mais idoso, ou ainda, nos termos do art. 49, II.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nas reunies conjuntas observar-se-o as seguintes normas:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'cada comisso dever estar presente pela maioria absoluta de seus membros;'
									},
									"2":{
										"caput":'o estudo da matria ser em conjunto, mas a votao far-se- separadamente, na ordem constante do despacho da Mesa;'
									},
									"3":{
										"caput":'cada comisso poder ter o seu relator se no preferir relator nico;'
									},
									"4":{
										"caput":'o parecer das comisses poder ser em conjunto, desde que consigne a manifestao de cada uma delas, ou em separado, se essa for a orientao preferida, mencionando, em qualquer caso, os votos vencidos, os em separado, os pelas concluses e os com restries.'
									},
							}
						}]
					},
			}
		}]
	},
	"114":{
		"caput":'As comisses permanentes e temporrias sero secretariadas por servidores da Secretaria do Senado e tero assessoramento prprio, constitudo de at trs assessores, designados pelo respectivo Presidente, ouvida a Consultoria Legislativa ou a de Oramentos, conforme o caso.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao secretrio da comisso compete:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'redigir as atas;'
									},
									"2":{
										"caput":'organizar a pauta do dia e do protocolo dos trabalhos com o seu andamento;'
									},
									"3":{
										"caput":'manter atualizados os registros necessrios ao controle de designao de relatores.'
									},
							}
						}]
					},
			}
		}]
	},
	"115":{
		"caput":'Das reunies das comisses lavrar-se-o atas em folhas avulsas rubricadas pelo Presidente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando, pela importncia do assunto em estudo, convier o registro taquigrfico dos debates, o Presidente solicitar ao Primeiro Secretrio as providncias necessrias.'
					},
					"2":{
						"caput":'Das atas constaro:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o dia, a hora e o local da reunio;'
									},
									"2":{
										"caput":'os nomes dos membros presentes e os dos ausentes com causa justificada ou sem ela;'
									},
									"3":{
										"caput":'a distribuio das matrias por assuntos e relatores;'
									},
									"4":{
										"caput":'as concluses dos pareceres lidos;'
									},
									"5":{
										"caput":'referncias sucintas aos debates;'
									},
									"6":{
										"caput":'os pedidos de vista, adiamento, diligncias e outras providncias, salvo quando no se considere conveniente a divulgao da matria.'
									},
							}
						}]
					},
					"3":{
						"caput":'As atas sero publicadas no Dirio do Senado Federal, dentro dos dois dias teis que se seguirem  reunio, podendo, em casos excepcionais, a juzo do Presidente da comisso, ser essa publicao adiada por igual prazo.'
					},
			}
		}]
	},
	"116":{
		"caput":'Sero secretas as reunies para deliberar sobre:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'declarao de guerra ou celebrao de paz (Const., art. 49, II);'
					},
					"2":{
						"caput":'trnsito ou permanncia temporria de foras estrangeiras no territrio nacional (Const., art. 49, II);'
					},
					"3":{
						"caput":'escolha de chefe de misso diplomtica de carter permanente (Const., art. 52, IV);'
					},
			}
		}]
	},
	"117":{
		"caput":'Nas reunies secretas, alm dos membros da comisso, s ser admitida a presena de Senadores e das pessoas a serem ouvidas sobre a matria em debate.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os Deputados Federais podero assistir s reunies secretas que no tratarem de matria da competncia privativa do Senado Federal.'
					},
			}
		}]
	},
	"118":{
		"caput":'O exame das comisses sobre as proposies, excetuadas as emendas e os casos em que este Regimento determine em contrrio, obedecer aos seguintes prazos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'vinte dias teis para a Comisso de Constituio, Justia e Cidadania;'
					},
					"2":{
						"caput":'quinze dias teis para as demais comisses.'
					},
			}
		}]
	},
	"119":{
		"caput":'Quando a matria for despachada a mais de uma comisso e a primeira esgotar o prazo sem sobre ela se manifestar, poder ser dispensado o seu parecer, por deliberao do Plenrio, a requerimento de qualquer Senador.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se uma das comisses considerar indispensvel, antes de proferir o seu parecer, o exame da que houver excedido o prazo, proposta neste sentido ser submetida  deliberao do Plenrio.'
					},
			}
		}]
	},
	"120":{
		"caput":'O relator tem, para apresentar o relatrio, a metade do prazo atribudo  comisso.'
	},
	"121":{
		"caput":'O Presidente da comisso, ex officio ou a requerimento de Senador, poder mandar incluir, na pauta dos trabalhos, matria que, distribuda, no tenha sido relatada no prazo regimental, devendo dar conhecimento da deciso ao relator.'
	},
	"122":{
		"caput":'Perante as comisses, podero apresentar emendas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'qualquer de seus membros, em todos os casos;'
					},
					"2":{
						"caput":'qualquer Senador:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos de cdigo;'
									},
									"2":{
										"caput":'projetos de iniciativa do Presidente da Repblica com tramitao urgente (Const., art. 64,  1);'
									},
									"3":{
										"caput":'projetos referidos no art. 91.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'No caso do inciso II, o prazo para a apresentao de emenda contar-se- a partir da publicao da matria no Dirio do Senado Federal, sendo de vinte dias teis para os projetos de Cdigo e de cinco dias teis para os demais projetos.'
													},
													"2":{
														"caput":'No avulso eletrnico da Ordem do Dia consignar-se- a existncia de projetos em fase de recebimento de emendas, com a indicao da comisso que dever receb-las, do prazo e do nmero de dias transcorridos.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"123":{
		"caput":'Considera-se emenda de comisso a proposta por qualquer de seus membros e por ela adotada.'
	},
	"124":{
		"caput":'Ter o seguinte tratamento a emenda apresentada na forma do art. 122:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do inciso I, ser considerada inexistente quando no adotada pela comisso;'
					},
					"2":{
						"caput":'no caso do inciso II, alnea a, ser encaminhada  deliberao do Plenrio do Senado, com parecer favorvel ou contrrio;'
					},
					"3":{
						"caput":'no caso do inciso II, alnea b, ser final o pronunciamento, salvo recurso interposto por um dcimo dos membros do Senado no sentido de ser a emenda submetida ao Plenrio, sem discusso;'
					},
					"4":{
						"caput":'no caso do inciso II, alnea c, ser final o pronunciamento da comisso, salvo recurso interposto para discusso e votao da proposio principal.'
					},
			}
		}]
	},
	"125":{
		"caput":'Quando a proposio estiver sujeita, na forma deste Regimento, a parecer em Plenrio, o relator, ao proferi-lo, poder oferecer emenda ou subemenda.'
	},
	"126":{
		"caput":'A designao de relator, independente da matria e de reunio da comisso, obedecer  proporo das representaes partidrias ou dos blocos parlamentares nela existentes, ser alternada entre os seus membros e far-se- em dois dias teis aps o recebimento do projeto, salvo nos casos em que este Regimento fixe outro prazo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O relator do projeto ser o das emendas a este oferecidas em plenrio, salvo ausncia ou recusa.'
					},
					"2":{
						"caput":'Quando se tratar de emenda oferecida pelo relator, em plenrio, o Presidente da comisso designar outro Senador para relat-la, sendo essa circunstncia consignada no parecer.'
					},
			}
		}]
	},
	"127":{
		"caput":'No poder funcionar como relator o autor da proposio.'
	},
	"128":{
		"caput":'Vencido o relator, o Presidente da comisso designar um dos membros, em maioria, para suceder-lhe, exceto se o fato ocorrer apenas em relao a parte da proposio ou emenda, quando permanecer o mesmo relator, consignando-se no parecer, pormenorizadamente, o objeto do dissenso na deliberao da comisso.'
	},
	"129":{
		"caput":'O Presidente poder, excepcionalmente, funcionar como relator.'
	},
	"130":{
		"caput":'As matrias que, em cada reunio, devam ser objeto de estudo, constaro de pauta previamente organizada, sendo relatadas na ordem em que nela figurarem, salvo preferncia concedida para qualquer delas.'
	},
	"131":{
		"caput":'O relatrio dever ser oferecido por escrito.'
	},
	"132":{
		"caput":'Lido o relatrio, desde que a maioria se manifeste de acordo com o relator, passar ele a constituir parecer.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O pedido de vista do processo somente poder ser aceito por uma nica vez e pelo prazo mximo e improrrogvel de cinco dias, devendo ser formulado na oportunidade em que for conhecido o voto proferido pelo relator, obedecido o disposto no  4.'
					},
					"2":{
						"caput":'Estando a matria em regime de urgncia, a vista somente poder ser concedida:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'por meia hora, no caso do art. 336, I;'
									},
									"2":{
										"caput":'por vinte e quatro horas, nos casos do art. 336, II e III.'
									},
							}
						}]
					},
					"3":{
						"caput":'Quando se tratar de proposio com prazo determinado, a vista, desde que no ultrapasse os ltimos dez dias de sua tramitao, poder ser concedida por vinte e quatro horas.'
					},
					"4":{
						"caput":'Os prazos a que se referem os  1 a 3 correro em conjunto se a vista for requerida por mais de um Senador.'
					},
					"5":{
						"caput":'Verificando-se a hiptese prevista no art. 128, o parecer vencedor dever ser apresentado na reunio ordinria imediata, salvo deliberao em contrrio.'
					},
					"6":{
						"caput":'Os membros da comisso que no concordarem com o relatrio podero:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'dar voto em separado;'
									},
									"2":{
										"caput":'assin-lo, uma vez constitudo parecer, com restries, pelas concluses, ou declarando-se vencidos.'
									},
							}
						}]
					},
					"7":{
						"caput":'Contam-se como favorveis os votos pelas concluses ou com restries.'
					},
					"8":{
						"caput":'(Revogado).'
					},
					"9":{
						"caput":'Em caso de empate na votao, o Presidente a desempatar.'
					},
			}
		}]
	},
	"133":{
		"caput":'Todo parecer deve ser conclusivo em relao  matria a que se referir, podendo a concluso ser:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela aprovao, total ou parcial;'
					},
					"2":{
						"caput":'pela rejeio;'
					},
					"3":{
						"caput":'pelo arquivamento;'
					},
					"4":{
						"caput":'pelo destaque, para proposio em separado, de parte da proposio principal, quando originria do Senado, ou de emenda;'
					},
					"5":{
						"caput":'pela apresentao de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
									"3":{
										"caput":'ou subemenda;'
									},
									"4":{
										"caput":'a seguir em relao  matria.'
									},
									"5":{
										"caput":'nos termos do art. 227-A, inciso II; (Includo pela Resoluo n. 14, de 2019)',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'Considera-se pela rejeio o parecer pelo arquivamento quando se referir a proposio legislativa.'
													},
													"2":{
														"caput":'Nas hipteses do inciso V, alneas a, b e c, o parecer  considerado justificao da proposio apresentada.'
													},
													"3":{
														"caput":'Nas hipteses do inciso V, alneas "a", "b", "c" e "e", o parecer  considerado justificao da proposio apresentada. (Redao dada pela Resoluo n. 14, de 2019)'
													},
													"4":{
														"caput":'Sendo favorvel o parecer apresentado sobre indicao, ofcio, memorial ou outro documento contendo sugesto ou solicitao que dependa de proposio legislativa, esta dever ser formalizada em concluso.'
													},
													"5":{
														"caput":'Quando se tratar de parecer sobre matria que deva ser apreciada em sesso secreta (art. 197), proceder-se- de acordo com o disposto no art. 116,  1.'
													},
													"6":{
														"caput":'Quando o parecer se referir a emendas ou subemendas, dever oferecer concluso relativamente a cada uma.'
													},
													"7":{
														"caput":'A comisso, ao se manifestar sobre emendas, poder reunir a matria da proposio principal e das emendas com parecer favorvel num nico texto, com os acrscimos e alteraes que visem ao seu aperfeioamento.'
													},
													"8":{
														"caput":'As emendas com parecer contrrio das comisses sero submetidas ao Plenrio, desde que a deciso do rgo tcnico no alcance unanimidade de votos, devendo esta circunstncia constar expressamente do parecer.'
													},
													"9":{
														"caput":'Toda vez que a comisso concluir o seu parecer com sugesto ou proposta que envolva matria de requerimento ou emenda, formalizar a proposio correspondente.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"134":{
		"caput":'O parecer conter ementa indicativa da matria a que se referir.'
	},
	"135":{
		"caput":'As comisses podero, em seus pareceres, propor seja o assunto apreciado pelo Senado em sesso secreta, caso em que o respectivo processo ser entregue ao Presidente da Mesa com o devido sigilo.'
	},
	"136":{
		"caput":'Uma vez assinados pelo Presidente e pelo relator e instrudos com a lista de presena dos membros da comisso, os pareceres sero enviados  Mesa, juntamente com as emendas relatadas, declaraes de votos e votos em separado.'
	},
	"137":{
		"caput":'Os pareceres sero lidos em plenrio, publicados no Dirio do Senado Federal e em avulso eletrnico, aps manifestao das comisses a que tenha sido despachada a matria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'As comisses podero promover, para estudos, a publicao de seus pareceres ao p da ata da reunio ou em avulsos eletrnicos especiais.'
					},
			}
		}]
	},
	"138":{
		"caput":'Se o parecer concluir por pedido de providncias:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ser despachado pelo Presidente da comisso quando solicitar audincia de outra comisso, reunio conjunta com outra comisso ou diligncia interna de qualquer natureza;'
					},
					"2":{
						"caput":'ser encaminhado  Mesa para despacho da Presidncia ou deliberao do Plenrio, nos demais casos.'
					},
			}
		}]
	},
	"139":{
		"caput":'No caso do art. 133, IV, a proposta ser submetida ao Plenrio antes do prosseguimento do estudo da matria.'
	},
	"140":{
		"caput":'Os pareceres podero ser proferidos oralmente, em plenrio, por relator designado pelo Presidente da Mesa:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nas matrias em regime de urgncia;'
					},
					"2":{
						"caput":'nas matrias includas em Ordem do Dia, nos termos do art. 172;'
					},
					"3":{
						"caput":'nas demais matrias em que este Regimento expressamente o permita.'
					},
			}
		}]
	},
	"141":{
		"caput":'Se o parecer oral concluir pela apresentao de requerimento, projeto ou emenda, o texto respectivo dever ser remetido  Mesa, por escrito, assinado pelo relator.'
	},
	"142":{
		"caput":'Quando as comisses se ocuparem de assuntos de interesse particular, procederem a inqurito, tomarem depoimentos e informaes, ou praticarem outras diligncias semelhantes, podero solicitar, das autoridades legislativas, judicirias ou administrativas, das entidades autrquicas, sociedades de economia mista e empresas concessionrias de servios pblicos, quaisquer documentos ou informaes e permitir s pessoas diretamente interessadas a defesa dos seus direitos, por escrito ou oralmente.'
	},
	"143":{
		"caput":'Quando a comisso julgar que a petio, memorial, representao ou outro documento no deva ter andamento, mand-lo- arquivar, por proposta de qualquer de seus membros, comunicando o fato  Mesa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comunicao ser lida no perodo do Expediente, publicada no Dirio do Senado Federal e encaminhada ao arquivo com o documento que lhe deu origem.'
					},
					"2":{
						"caput":'O exame do documento poder ser reaberto se o Plenrio o deliberar, a requerimento de qualquer Senador.'
					},
					"3":{
						"caput":'A comisso no poder encaminhar  Cmara dos Deputados ou a outro rgo do Poder Pblico qualquer documento que lhe tenha sido enviado.'
					},
			}
		}]
	},
	"144":{
		"caput":'Quanto ao documento de natureza sigilosa, observar-se-o, no trabalho das comisses, as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no ser lcito transcrev-lo, no todo ou em parte, nos pareceres e expediente de curso ostensivo;'
					},
					"2":{
						"caput":'se houver sido encaminhado ao Senado em virtude de requerimento formulado perante a comisso, o seu Presidente dele dar conhecimento ao requerente, em particular;'
					},
					"3":{
						"caput":'se a matria interessar  comisso, ser-lhe- dada a conhecer em reunio secreta;'
					},
					"4":{
						"caput":'se destinado a instruir o estudo de matria em curso no Senado, ser encerrado em sobrecarta, rubricada pelo Presidente da comisso, que acompanhar o processo em toda a sua tramitao;'
					},
					"5":{
						"caput":'quando o parecer contiver matria de natureza sigilosa, ser objeto das cautelas descritas no inciso IV.'
					},
			}
		}]
	},
	"145":{
		"caput":'A criao de comisso parlamentar de inqurito ser feita mediante requerimento de um tero dos membros do Senado Federal.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento de criao da comisso parlamentar de inqurito determinar o fato a ser apurado, o nmero de membros, o prazo de durao da comisso e o limite das despesas a serem realizadas.'
					},
					"2":{
						"caput":'Recebido o requerimento, o Presidente ordenar que seja numerado e publicado.'
					},
					"3":{
						"caput":'O Senador s poder integrar duas comisses parlamentares de inqurito, uma como titular, outra como suplente.'
					},
					"4":{
						"caput":'A comisso ter suplentes, em nmero igual  metade do nmero dos titulares mais um, escolhidos no ato da designao destes, observadas as normas constantes do art. 78.'
					},
			}
		}]
	},
	"146":{
		"caput":'No se admitir comisso parlamentar de inqurito sobre matrias pertinentes:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":' Cmara dos Deputados;'
					},
					"2":{
						"caput":'s atribuies do Poder Judicirio;'
					},
					"3":{
						"caput":'aos Estados.'
					},
			}
		}]
	},
	"147":{
		"caput":'Na hiptese de ausncia do relator a qualquer ato do inqurito, poder o Presidente da comisso designar-lhe substituto para a ocasio, mantida a escolha na mesma representao partidria ou bloco parlamentar.'
	},
	"148":{
		"caput":'No exerccio das suas atribuies, a comisso parlamentar de inqurito ter poderes de investigao prprios das autoridades judiciais, facultada a realizao de diligncias que julgar necessrias, podendo convocar Ministros de Estado, tomar o depoimento de qualquer autoridade, inquirir testemunhas, sob compromisso, ouvir indiciados, requisitar de rgo pblico informaes ou documentos de qualquer natureza, bem como requerer ao Tribunal de Contas da Unio a realizao de inspees e auditorias que entender necessrias.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No dia previamente designado, se no houver nmero para deliberar, a comisso parlamentar de inqurito poder tomar depoimento das testemunhas ou autoridades convocadas, desde que estejam presentes o Presidente e o relator.'
					},
					"2":{
						"caput":'Os indiciados e testemunhas sero intimados de acordo com as prescries estabelecidas na legislao processual penal, aplicando-se, no que couber, a mesma legislao, na inquirio de testemunhas e autoridades.'
					},
			}
		}]
	},
	"149":{
		"caput":'O Presidente da comisso parlamentar de inqurito, por deliberao desta, poder incumbir um dos seus membros ou funcionrios da Secretaria do Senado da realizao de qualquer sindicncia ou diligncia necessria aos seus trabalhos.'
	},
	"150":{
		"caput":'Ao trmino de seus trabalhos, a comisso parlamentar de inqurito enviar  Mesa, para conhecimento do Plenrio, seu relatrio e concluses.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comisso poder concluir seu relatrio por projeto de resoluo se o Senado for competente para deliberar a respeito.'
					},
					"2":{
						"caput":'Sendo diversos os fatos objeto de inqurito, a comisso dir, em separado, sobre cada um, podendo faz-lo antes mesmo de finda a investigao dos demais.'
					},
			}
		}]
	},
	"151":{
		"caput":'A comisso parlamentar de inqurito encaminhar suas concluses, se for o caso, ao Ministrio Pblico, para que promova a responsabilidade civil ou criminal dos infratores.'
	},
	"152":{
		"caput":'O prazo da comisso parlamentar de inqurito poder ser prorrogado, automaticamente, a requerimento de um tero dos membros do Senado, comunicado por escrito  Mesa, lido em plenrio e publicado no Dirio do Senado Federal, observado o disposto no art. 76,  4.'
	},
	"153":{
		"caput":'Nos atos processuais, aplicar-se-o, subsidiariamente, as disposies do Cdigo de Processo Penal.'
	},
	"154":{
		"caput":'As sesses do Senado podem ser:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'deliberativas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'no deliberativas;'
					},
					"3":{
						"caput":'especiais; e'
					},
					"4":{
						"caput":'de debates temticos.'
					},
			}
		}]
	},
	"155":{
		"caput":'A sesso ter incio de segunda a quinta-feira, s quatorze horas, e, s sextas-feiras, s nove horas, pelo relgio do plenrio, presentes no recinto pelo menos um vigsimo da composio do Senado, e ter a durao mxima de quatro horas e trinta minutos, salvo prorrogao, ou no caso do disposto nos arts. 178 e 179.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao declarar aberta a sesso, o Presidente proferir as seguintes palavras: Sob a proteo de Deus, iniciamos nossos trabalhos.'
					},
					"2":{
						"caput":'Nos casos dos incisos I e IV do  6 do art. 154, o Presidente declarar que no pode ser realizada a sesso, designando a Ordem do Dia para a seguinte, e despachando, independentemente de leitura, o expediente que ir integrar a ata da reunio a ser publicada no Dirio do Senado Federal.'
					},
					"3":{
						"caput":'Havendo na Ordem do Dia matria relevante que o justifique, a Presidncia poder adiar por at trinta minutos a abertura da sesso.'
					},
					"4":{
						"caput":'Em qualquer fase da sesso, estando em plenrio menos de um vigsimo da composio da Casa, o Presidente a suspender, fazendo acionar as campainhas durante dez minutos, e se, ao fim desse prazo, permanecer a inexistncia de nmero, a sesso ser encerrada.'
					},
					"5":{
						"caput":'Do perodo do tempo da sesso descontar-se-o as suspenses ocorridas.'
					},
			}
		}]
	},
	"156":{
		"caput":'A primeira parte da sesso, que ter a durao de cento e vinte minutos, ser destinada  leitura do expediente e aos oradores inscritos na forma do disposto no art. 17.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Constituem matria do Perodo do Expediente:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'a apresentao de projeto, indicao, parecer ou requerimento no relacionado com as proposies constantes da Ordem do Dia;'
									},
									"2":{
										"caput":'as comunicaes enviadas  Mesa pelos Senadores;'
									},
									"3":{
										"caput":'os pedidos de licena dos Senadores;'
									},
									"4":{
										"caput":'os ofcios, moes, mensagens, telegramas, cartas, memoriais e outros documentos recebidos.'
									},
							}
						}]
					},
					"2":{
						"caput":'O expediente ser lido pelo Primeiro Secretrio, na ntegra ou em resumo, a juzo do Presidente, ressalvado a qualquer Senador o direito de requerer sua leitura integral.'
					},
			}
		}]
	},
	"157":{
		"caput":'No ser lido, nem constituir objeto de comunicao em sesso pblica, documento de carter sigiloso, observando-se, quanto ao expediente dessa natureza, as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se houver sido remetido ao Senado a requerimento de Senador, ainda que em cumprimento  manifestao do Plenrio, o Presidente da Mesa dele dar conhecimento, em particular, ao requerente;'
					},
					"2":{
						"caput":'se a solicitao houver sido formulada por comisso, ao Presidente desta ser encaminhado em sobrecarta fechada e rubricada pelo Presidente da Mesa;'
					},
					"3":{
						"caput":'se o documento se destinar a instruir o estudo de matria em curso no Senado, tramitar em sobrecarta fechada, rubricada pelo Presidente da Mesa e pelos presidentes das comisses que dele tomarem conhecimento, feita na capa do processo a devida anotao.'
					},
			}
		}]
	},
	"158":{
		"caput":'O tempo que se seguir  leitura do expediente ser destinado aos oradores do Perodo do Expediente, podendo cada um dos inscritos usar da palavra pelo prazo mximo de dez minutos nas sesses deliberativas e por vinte minutos nas sesses no deliberativas, sendo cabvel a intercalao com as comunicaes inadiveis, o uso da palavra pelas lideranas ou as delegaes delas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Perodo do Expediente poder ser prorrogado pelo Presidente, uma s vez, para que o orador conclua o seu discurso caso no tenha esgotado o tempo de que disponha, aps o que a Ordem do Dia ter incio impreterivelmente.'
					},
					"2":{
						"caput":'Se algum Senador, antes do trmino do Perodo do Expediente, solicitar  Mesa inscrio para manifestao de pesar, comemorao, comunicao inadivel ou explicao pessoal, o Presidente lhe assegurar o uso da palavra durante o Perodo do Expediente, sendo cabvel a intercalao com oradores inscritos, o uso da palavra pelas lideranas ou as delegaes destas.'
					},
					"3":{
						"caput":'No caso do  2, somente podero usar da palavra trs Senadores, por cinco minutos cada um, durante o Perodo do Expediente.'
					},
					"4":{
						"caput":'As inscries que no puderem ser atendidas em virtude do levantamento ou da no realizao da sesso, ou em virtude do disposto no  5, transferir-se-o para a sesso do dia seguinte e as desta para a subsequente.'
					},
					"5":{
						"caput":'Havendo, na Ordem do Dia, matria urgente compreendida no art. 336, I, no sero permitidos oradores no Perodo do Expediente.'
					},
					"6":{
						"caput":'Ressalvado o disposto no  1 deste artigo, no haver prorrogao do Perodo do Expediente.'
					},
			}
		}]
	},
	"159":{
		"caput":'No Perodo do Expediente, s podero ser objeto de deliberao requerimentos que no dependam de parecer das comisses, que no digam respeito a proposies constantes da Ordem do Dia ou os que o Regimento no determine sejam submetidos em outra fase da sesso.'
	},
	"160":{
		"caput":'(Revogado).'
	},
	"161":{
		"caput":'Terminados os discursos do Perodo do Expediente, sero lidos os documentos que ainda existirem sobre a mesa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando houver, entre os documentos a serem lidos, requerimentos a votar, e se mais de um Senador pedir a palavra para encaminhar a votao, esta ficar adiada para o fim da Ordem do Dia.'
					},
			}
		}]
	},
	"162":{
		"caput":'A Ordem do Dia ter incio, impreterivelmente, s dezesseis horas, salvo prorrogao nos termos do art. 158,  6.'
	},
	"163":{
		"caput":'As matrias sero includas em Ordem do Dia, a juzo do Presidente, segundo sua antiguidade e importncia, observada a seguinte sequncia:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'medida provisria, a partir do 46 (quadragsimo sexto) dia de sua vigncia (Const., art. 62,  6);'
					},
					"2":{
						"caput":'matria urgente de iniciativa do Presidente da Repblica, com prazo de tramitao esgotado (Const., art. 64,  2);'
					},
					"3":{
						"caput":'matria em regime de urgncia do art. 336, I;'
					},
					"4":{
						"caput":'matria preferencial constante do art. 172, II, segundo os prazos ali previstos;'
					},
					"5":{
						"caput":'matria em regime de urgncia do art. 336, II;'
					},
					"6":{
						"caput":'matria em regime de urgncia do art. 336, III;'
					},
					"7":{
						"caput":'matria em tramitao normal.'
					},
			}
		}]
	},
	"164":{
		"caput":'Os projetos regulando a mesma matria (art. 258) figuraro na Ordem do Dia em srie, iniciada pela proposio preferida pela comisso competente, de maneira que a deciso do Plenrio sobre esta prejulgue as demais.'
	},
	"165":{
		"caput":'Os pareceres sobre escolha de autoridades (art. 383) sero includos, em srie, no final da Ordem do Dia.'
	},
	"166":{
		"caput":'Constaro da Ordem do Dia as matrias no apreciadas da pauta da sesso deliberativa ordinria anterior, com precedncia sobre outras dos grupos a que pertenam.'
	},
	"167":{
		"caput":'Ao ser designada a Ordem do Dia, qualquer Senador poder sugerir ao Presidente a incluso de matrias em condies de nela figurar (art. 171).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nenhuma matria poder ser includa em Ordem do Dia sem que tenha sido efetivamente publicada no Dirio do Senado Federal e em avulso eletrnico, no mnimo, com dez dias de antecedncia.'
					},
			}
		}]
	},
	"168":{
		"caput":'Salvo em casos especiais, assim considerados pela Presidncia, no constaro, das Ordens do Dia das sesses das segundas e sextas-feiras, matrias em votao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O princpio estabelecido neste artigo aplica-se ainda s matrias que tenham sua discusso encerrada nas sesses ordinrias das segundas e sextas-feiras.'
					},
			}
		}]
	},
	"169":{
		"caput":'Somente podero ser includas na Ordem do Dia, para deliberao do Plenrio, em cada sesso legislativa, as proposies protocoladas junto  Secretaria-Geral da Mesa at a data de 30 de novembro.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ficam ressalvadas do disposto neste artigo as matrias da competncia privativa do Senado Federal relacionadas no art. 52 da Constituio e, em casos excepcionais, at trs matrias, por deciso da Presidncia e consenso das lideranas.'
					},
			}
		}]
	},
	"170":{
		"caput":'A Ordem do Dia ser anunciada ao trmino da sesso anterior, publicada no Dirio do Senado Federal e em avulso eletrnico antes de iniciar-se a sesso respectiva.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No ser designada Ordem do Dia para a primeira sesso de cada sesso legislativa.'
					},
					"2":{
						"caput":'No avulso eletrnico da Ordem do Dia dever constar:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'os projetos em fase de recebimento de emendas perante a Mesa ou comisso;'
									},
									"2":{
										"caput":'os projetos em fase de apresentao do recurso a que se refere o art. 91,  4;'
									},
									"3":{
										"caput":'as proposies que devero figurar em Ordem do Dia nas trs sesses deliberativas ordinrias seguintes.'
									},
							}
						}]
					},
					"3":{
						"caput":'Nos dados referidos no  2, haver indicao expressa dos prazos, nmero de dias transcorridos e, no caso do inciso I, da comisso que dever receber as emendas.'
					},
			}
		}]
	},
	"171":{
		"caput":'A matria dependente de exame das comisses s ser includa em Ordem do Dia depois de emitidos os pareceres, lidos no Perodo do Expediente, publicados no Dirio do Senado Federal e em avulso eletrnico, observado o interstcio regimental (arts. 280 e 281).'
	},
	"172":{
		"caput":'A incluso em Ordem do Dia de proposio em rito normal, sem que esteja instruda com pareceres das comisses a que houver sido distribuda, s  admissvel nas seguintes hipteses:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por deliberao do Plenrio, se a nica ou a ltima comisso a que estiver distribuda no proferir o seu parecer no prazo regimental;'
					},
					"2":{
						"caput":'por ato do Presidente, quando se tratar:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'projeto de lei nua ou que tenha por fim prorrogar prazo de lei, se faltarem dez dias, ou menos, para o trmino de sua vigncia ou da sesso legislativa, quando o fato deva ocorrer em perodo de recesso do Congresso, ou nos dez dias que se seguirem  instalao da sesso legislativa subsequente;'
									},
									"3":{
										"caput":'projeto de decreto legislativo referente a tratado, convnio ou acordo internacional, se faltarem dez dias, ou menos, para o trmino do prazo no qual o Brasil deva manifestar-se sobre o ato em apreo;'
									},
									"4":{
										"caput":'projetos com prazo, se faltarem vinte dias para o seu trmino.',
										"enumeravel": [{
											"tipo":"paragrafo",
											"lista":{
													"1":{
														"caput":'Nas hipteses do inciso II, c e d, o projeto emendado voltar  Ordem do Dia na segunda sesso deliberativa ordinria subsequente, salvo se o encerramento da discusso se der no penltimo dia do prazo ou da sesso legislativa, hiptese em que a matria ter a mesma tramitao prevista para o caso do art. 336, II.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"173":{
		"caput":'Nenhum projeto poder ficar sobre a mesa por mais de um ms sem figurar em Ordem do Dia, salvo para diligncia aprovada pelo Plenrio.'
	},
	"174":{
		"caput":'Em casos excepcionais, assim considerados pela Mesa, e nos sessenta dias que precederem as eleies gerais, podero ser dispensadas, ouvidas as lideranas partidrias, as fases da sesso correspondentes ao Perodo do Expediente ou  Ordem do Dia.'
	},
	"175":{
		"caput":'A sequncia dos trabalhos da Ordem do Dia no poder ser alterada seno:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para posse de Senador;'
					},
					"2":{
						"caput":'para leitura de mensagem, ofcio ou documento sobre matria urgente;'
					},
					"3":{
						"caput":'para pedido de urgncia nos casos do art. 336, I;'
					},
					"4":{
						"caput":'em virtude de deliberao do Senado, no sentido de adiamento ou inverso da Ordem do Dia;'
					},
					"5":{
						"caput":'pela retirada de qualquer matria, para cumprimento de despacho, correo de erro ou omisso no avulso eletrnico e para sanar falhas de instruo;'
					},
					"6":{
						"caput":'para constituio de srie, em caso de votao secreta;'
					},
					"7":{
						"caput":'nos casos previstos no art. 304.'
					},
			}
		}]
	},
	"176":{
		"caput":'Esgotada a Ordem do Dia, o tempo que restar para o trmino da sesso ser destinado, preferencialmente, ao uso da palavra pelas lideranas e, havendo tempo, pelos oradores inscritos na forma do disposto no art. 17.'
	},
	"177":{
		"caput":'Esgotado o tempo da sesso ou ultimados a Ordem do Dia e os discursos posteriores a esta, o Presidente a encerrar.'
	},
	"178":{
		"caput":'Se o trmino do tempo da sesso ocorrer quando iniciada uma votao, esta ser ultimada independentemente de pedido de prorrogao.'
	},
	"179":{
		"caput":'Estando em apreciao matria constante do art. 336, I e II, a sesso s poder ser encerrada quando ultimada a deliberao.'
	},
	"180":{
		"caput":'A prorrogao da sesso poder ser concedida pelo Plenrio, em votao simblica, antes do trmino do tempo regimental:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por proposta do Presidente;'
					},
					"2":{
						"caput":'a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"181":{
		"caput":'O tempo que restar para o trmino da prorrogao ser destinado  votao de matrias cuja discusso esteja encerrada.'
	},
	"182":{
		"caput":'Em sesses pblicas, alm dos Senadores, s sero admitidos no plenrio os Suplentes de Senadores, os Deputados Federais, os ex-Senadores, entre estes includos os Suplentes de Senador que tenham exercido o mandato, os Ministros de Estado, quando comparecerem para os fins previstos neste Regimento, e os funcionrios do Senado em objeto de servio.'
	},
	"183":{
		"caput":'Durante as sesses pblicas, no  permitida a presena, na bancada da imprensa, de pessoa a ela estranha.'
	},
	"184":{
		"caput":' permitido a qualquer pessoa assistir s sesses pblicas, do lugar que lhe for reservado, desde que se encontre desarmada e se conserve em silncio, sem dar qualquer sinal de aplauso ou de reprovao ao que nelas se passar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A qualquer pessoa  vedado fumar no recinto do plenrio.'
					},
			}
		}]
	},
	"185":{
		"caput":'Em sesso secreta, somente os Senadores tero ingresso no plenrio e dependncias anexas, ressalvados o disposto no pargrafo nico do art. 192 e os casos em que o Senado conceda autorizao a outras pessoas para a ela assistirem, mediante proposta da Presidncia ou de lder.'
	},
	"186":{
		"caput":'A reportagem fotogrfica no recinto, a irradiao sonora, a filmagem e a transmisso em televiso das sesses dependem de autorizao do Presidente do Senado.'
	},
	"187":{
		"caput":'A sesso deliberativa extraordinria, convocada de ofcio pelo Presidente ou por deciso do Senado, ter o mesmo rito e durao da ordinria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Perodo do Expediente de sesso deliberativa extraordinria no exceder a trinta minutos.'
					},
			}
		}]
	},
	"188":{
		"caput":'Em sesso deliberativa extraordinria, s haver oradores, antes da Ordem do Dia, caso no haja nmero para as deliberaes.'
	},
	"189":{
		"caput":'O Presidente prefixar dia, horrio e Ordem do Dia para a sesso deliberativa extraordinria, dando-os a conhecer, previamente, ao Senado, em sesso ou atravs de qualquer meio de comunicao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No  obrigatria a incluso, na Ordem do Dia de sesso deliberativa extraordinria, de matria no ultimada na sesso anterior, ainda que em regime de urgncia ou em curso de votao.'
					},
			}
		}]
	},
	"190":{
		"caput":'A sesso secreta ser convocada pelo Presidente, de ofcio ou mediante requerimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A finalidade da sesso secreta dever figurar expressamente no requerimento, mas no ser divulgada, assim como o nome do requerente.'
					},
			}
		}]
	},
	"191":{
		"caput":'Recebido o requerimento a que se refere o art. 190, o Senado passar a funcionar secretamente para a sua votao; se aprovado, e desde que no haja data prefixada, a sesso secreta ser convocada para o mesmo dia ou para o dia seguinte.'
	},
	"192":{
		"caput":'Na sesso secreta, antes de se iniciarem os trabalhos, o Presidente determinar a sada do plenrio, tribunas, galerias e respectivas dependncias, de todas as pessoas estranhas, inclusive funcionrios da Casa.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O Presidente poder admitir na sesso, a seu juzo, a presena dos servidores que julgar necessrios.'
					},
			}
		}]
	},
	"193":{
		"caput":'No incio dos trabalhos de sesso secreta, deliberar-se- se o assunto que motivou a convocao dever ser tratado secreta ou publicamente, no podendo esse debate exceder a quinze minutos, sendo permitido a cada orador usar da palavra por trs minutos, de uma s vez. No primeiro caso, prosseguiro os trabalhos secretamente; no segundo, sero levantados para que o assunto seja, oportunamente, apreciado em sesso pblica.'
	},
	"194":{
		"caput":'Antes de encerrar-se uma sesso secreta, o Plenrio resolver, por simples votao e sem debate, se devero ser conservados em sigilo ou publicados o resultado, o nome',
		"enumeravel": [{
			"tipo":"None",
			"lista":{
					"1":{
						"caput":'requereram a convocao e, nos casos do art. 135, os pareceres e demais documentos constantes do processo.'
					},
			}
		}]
	},
	"195":{
		"caput":'Ao Senador que houver participado dos debates em sesso secreta  permitido reduzir por escrito o seu discurso, no prazo de vinte e quatro horas, para ser arquivado com a ata.'
	},
	"196":{
		"caput":'A sesso secreta ter a durao de quatro horas e trinta minutos, salvo prorrogao.'
	},
	"197":{
		"caput":'Transformar-se- em secreta a sesso:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'obrigatoriamente, quando o Senado tiver de se manifestar sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de guerra (Const., art. 49, II);'
									},
									"2":{
										"caput":'sobre a paz (Const., art. 49, II);'
									},
									"3":{
										"caput":''
									},
									"4":{
										"caput":'de chefe de misso diplomtica de carter permanente (Const., art. 52, IV);'
									},
									"5":{
										"caput":'para realizao de sesso secreta (art. 191);'
									},
							}
						}]
					},
					"2":{
						"caput":'por deliberao do Plenrio, mediante proposta da Presidncia ou a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"198":{
		"caput":'Somente em sesso secreta poder ser dado a conhecer, ao Plenrio, documento de natureza sigilosa.'
	},
	"199":{
		"caput":'O Senado poder interromper a sesso ou realizar sesso especial para comemorao ou recepo de altas personalidades, a juzo do Presidente ou por deliberao do Plenrio, mediante requerimento de 6 (seis) senadores.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Salvo o caso de recepo a Chefe de Estado ou de Governo ou autoridade equivalente, a sesso especial somente poder ocorrer 2 (duas) vezes por ms, s segundas ou sextas-feiras, e quando no houver Ordem do Dia previamente agendada para esses dias.'
					},
					"2":{
						"caput":'A homenagem  mesma efemride ou personalidade somente poder ocorrer 1 (uma) vez a cada 10 (dez) anos.'
					},
					"3":{
						"caput":'A primeira comemorao das homenagens somente poder ocorrer aps 25 (vinte e cinco) anos do fato.'
					},
					"4":{
						"caput":'A sesso especial ter a durao mxima de 2 (duas) horas.'
					},
					"5":{
						"caput":'Em sesso especial, podero ser admitidos convidados  Mesa e no Plenrio.'
					},
					"6":{
						"caput":'O parlamentar estrangeiro s ser recebido em plenrio se o Parlamento do seu pas der tratamento igual aos congressistas brasileiros que o visitem.'
					},
			}
		}]
	},
	"200":{
		"caput":'A sesso especial, que independe de nmero, ser convocada em sesso, atravs do Dirio do Senado Federal, ou por outro meio oficial de comunicao, e nela somente usaro da',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'senadores previamente designados pelo Presidente ou por lder de partido ou bloco parlamentar.'
					},
					"2":{
						"caput":'No sero concedidos apartes nas sesses especiais.'
					},
			}
		}]
	},
	"201":{
		"caput":'Ser elaborada ata circunstanciada de cada sesso, contendo, entre outros, os incidentes, debates, declaraes da Presidncia, listas de presena e chamada, texto das matrias lidas ou votadas e os discursos, a qual constar, salvo se secreta, do Dirio do Senado Federal, que ser publicado diariamente, durante as sesses legislativas ordinrias e extraordinrias, e, eventualmente, nos perodos de recesso, sempre que houver matria para publicao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No havendo sesso, nos casos do art. 154,  6, I e IV, ser publicada ata da reunio, que conter os nomes do Presidente, dos Secretrios e dos Senadores presentes, e o expediente despachado.'
					},
					"2":{
						"caput":'Quando o discurso, requisitado para reviso, no for restitudo  Taquigrafia at as dezoito horas do dia seguinte, deixar de ser includo na ata da sesso respectiva, onde figurar nota explicativa a respeito, no lugar a ele correspondente.'
					},
					"3":{
						"caput":'Se, ao fim de trinta dias, o discurso no houver sido restitudo, a publicao se far pela cpia arquivada nos servios taquigrficos, com nota de que no foi revisto pelo orador.'
					},
			}
		}]
	},
	"202":{
		"caput":'Constaro, tambm, da ata:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por extenso:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'mensagens ou ofcios do Governo ou da Cmara dos Deputados, salvo quando relativos  sano de projetos, devoluo de autgrafos ou agradecimento de comunicao;'
									},
									"2":{
										"caput":'proposies legislativas e declaraes de voto;'
									},
							}
						}]
					},
					"2":{
						"caput":'em smula, todos os demais documentos lidos no Perodo do Expediente, salvo deliberao do Senado ou determinao da Presidncia.'
					},
			}
		}]
	},
	"203":{
		"caput":' permitido ao Senador enviar  Mesa, para publicao no Dirio do Senado Federal e incluso nos Anais, o discurso que deseje proferir na sesso, dispensada a sua leitura.'
	},
	"204":{
		"caput":'Quando o esclarecimento da Presidncia sobre questo regimental ou discurso de algum Senador forem lidos, constar da ata a indicao de o terem sido.'
	},
	"205":{
		"caput":'A ata registrar, em cada momento, a substituio ocorrida em relao  Presidncia da sesso.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando a substituio na Presidncia se der durante discurso, far-se- o registro no fim deste.'
					},
			}
		}]
	},
	"206":{
		"caput":'Na ata, o nome do Presidente ser registrado, entre parnteses, em seguida s palavras: O Sr. Presidente.'
	},
	"207":{
		"caput":'Os pedidos de retificao e as questes de ordem sobre a ata sero decididos pela Presidncia.'
	},
	"208":{
		"caput":'A ata de sesso secreta ser redigida pelo Segundo-Secretrio, aprovada com qualquer nmero, antes de levantada a sesso, assinada pelo Presidente, Primeiro e Segundo',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'em sobrecarta lacrada, datada e rubricada pelos Secretrios, e recolhida ao arquivo.'
					},
					"2":{
						"caput":'O discurso a que se refere o art. 195 ser arquivado com a ata e os documentos referentes  sesso, em segunda sobrecarta, igualmente lacrada.'
					},
					"3":{
						"caput":'O desarquivamento dos documentos referidos no  1 s poder ser feito mediante requisio da Presidncia.'
					},
			}
		}]
	},
	"209":{
		"caput":'Os trabalhos das sesses sero organizados em anais, por ordem cronolgica, para distribuio aos Senadores.'
	},
	"210":{
		"caput":'A transcrio de documento no Dirio do Senado Federal, para que conste dos Anais,  permitida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando constituir parte integrante de discurso de Senador;'
					},
					"2":{
						"caput":'quando aprovada pelo Presidente do Senado, a requerimento de qualquer Senador.'
					},
			}
		}]
	},
	"211":{
		"caput":'Consistem as proposies em:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'propostas de emenda  Constituio;'
					},
					"2":{
						"caput":'projetos;'
					},
					"3":{
						"caput":'requerimentos;'
					},
					"4":{
						"caput":'indicaes;'
					},
					"5":{
						"caput":'pareceres;'
					},
					"6":{
						"caput":'emendas.'
					},
			}
		}]
	},
	"212":{
		"caput":'Podero ter tramitao iniciada no Senado propostas de emenda  Constituio de iniciativa:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de um tero, no mnimo, de seus membros (Const., art. 60, I);'
					},
					"2":{
						"caput":'de mais da metade das Assembleias Legislativas das Unidades da Federao, manifestando-se, cada uma delas, pela maioria relativa de seus membros (Const., art. 60, III).'
					},
			}
		}]
	},
	"213":{
		"caput":'Os projetos compreendem:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'projeto de lei, referente a matria da competncia do Congresso Nacional, com sano do Presidente da Repblica (Const., art. 48);'
					},
					"2":{
						"caput":'projeto de decreto legislativo, referente  matria da competncia exclusiva do Congresso Nacional (Const., art. 49);'
					},
					"3":{
						"caput":'projeto de resoluo sobre matria da competncia privativa do Senado (Const., art. 52).'
					},
			}
		}]
	},
	"213-A":{
		"caput":' facultado a qualquer Senador ou comisso oferecer projeto de lei de consolidao, atendidos os princpios de que tratam os arts. 13, 14 e 15 da Lei Complementar n 95, de 26 de fevereiro de 1998, vedada a alterao no mrito das normas que serviram de base para a consolidao.'
	},
	"213-B":{
		"caput":'O projeto recebido ser lido, numerado, publicado e distribudo  comisso que guardar maior pertinncia quanto  matria, que se pronunciar sobre o atendimento ao princpio de preservao do contedo original das normas consolidadas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Qualquer Senador ou comisso poder, no prazo de trinta dias aps a publicao do projeto de lei de consolidao, oferecer  comisso encarregada de seu exame:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'sugestes de redao, vedadas alteraes que envolvam o mrito da matria original;'
									},
									"2":{
										"caput":'sugestes de incorporao de normas que no foram objeto de consolidao;'
									},
									"3":{
										"caput":'sugestes de retirada de normas que foram objeto de consolidao.'
									},
							}
						}]
					},
					"2":{
						"caput":'As sugestes que envolverem alterao no mrito da proposio que serviu de base  formulao do projeto de lei de consolidao sero dadas como rejeitadas.'
					},
					"3":{
						"caput":'As disposies referentes  tramitao dos projetos de lei aplicam-se  tramitao e  aprovao do projeto de lei de consolidao, nos termos do que preceitua o Regimento Interno do Senado Federal, ressalvados os procedimentos exclusivos aplicveis  subespcie, constantes deste Regimento.'
					},
			}
		}]
	},
	"213-C":{
		"caput":'Aprovado o projeto de lei de consolidao na comisso, ser ele encaminhado ao Plenrio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Podero ser oferecidas, em plenrio, emendas destinadas  correo de redao que afronte o mrito da matria, que sero submetidas  deliberao da Comisso de Constituio, Justia e Cidadania.'
					},
					"2":{
						"caput":'As emendas de correo de erro de redao julgadas improcedentes pela Comisso de Constituio, Justia e Cidadania sero dadas como rejeitadas.'
					},
			}
		}]
	},
	"213-D":{
		"caput":'Aps a entrada em vigor da lei de consolidao, devero fazer-lhe expressa remisso todos os projetos vinculados  matria.'
	},
	"213-E":{
		"caput":'Aplicam-se os mesmos procedimentos previstos nos arts. 213-B, 213-C e 213-D aos projetos de lei de consolidao originrios da Cmara dos Deputados em reviso no Senado Federal e s emendas da Cmara dos Deputados a projeto de lei de consolidao originrio do Senado.'
	},
	"214":{
		"caput":'O requerimento poder ser oral ou escrito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":' oral e despachado pelo Presidente o requerimento:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'de leitura de qualquer matria sujeita ao conhecimento do Plenrio;'
									},
									"2":{
										"caput":'de retificao da ata;'
									},
									"3":{
										"caput":'de incluso em Ordem do Dia de matria em condies regimentais de nela figurar;'
									},
									"4":{
										"caput":'de permisso para falar sentado.'
									},
							}
						}]
					},
			}
		}]
	},
	"215":{
		"caput":'So escritos os requerimentos no referidos no art. 214 e dependem de votao por maioria simples, presente a maioria da composio do Senado, salvo os abaixo especificados:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'dependentes de deciso da Mesa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'informao a Ministro de Estado ou a qualquer titular de rgo diretamente subordinado  Presidncia da Repblica (Const., art. 50,  2);'
									},
									"2":{
										"caput":'licena (arts. 13 e 43);'
									},
									"3":{
										"caput":'tramitao em conjunto de proposio regulando a mesma matria, exceto se a proposio constar da Ordem do Dia ou for objeto de parecer aprovado em comisso.'
									},
							}
						}]
					},
					"2":{
						"caput":'dependentes de despacho do Presidente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'publicao de informaes oficiais no Dirio do Senado Federal;'
									},
									"2":{
										"caput":'esclarecimentos sobre atos da administrao interna do Senado;'
									},
									"3":{
										"caput":'retirada de indicao ou requerimento;'
									},
									"4":{
										"caput":'reconstituio de proposio;'
									},
									"5":{
										"caput":'retirada de proposio, desde que no tenha recebido parecer de comisso e no conste de Ordem do Dia (art. 256,  2);'
									},
									"6":{
										"caput":'publicao de documentos no Dirio do Senado Federal para transcrio nos Anais (art. 210, II).'
									},
									"7":{
										"caput":'converso de proposio em indicao, nos termos do art. 227-A, inciso I; (Includo pela Resoluo n. 14, de 2019)'
									},
							}
						}]
					},
					"3":{
						"caput":'dependentes de votao com a presena, no mnimo, de um dcimo da composio do Senado:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'prorrogao do tempo da sesso;'
									},
									"3":{
										"caput":'homenagem de pesar, inclusive levantamento da sesso.'
									},
							}
						}]
					},
					"4":{
						"caput":'(Revogado).'
					},
			}
		}]
	},
	"216":{
		"caput":'Os requerimentos de informaes esto sujeitos s seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sero admissveis para esclarecimento de qualquer assunto submetido  apreciao do Senado ou atinente a sua competncia fiscalizadora;'
					},
					"2":{
						"caput":'no podero conter pedido de providncia, consulta, sugesto, conselho ou interrogao sobre propsito da autoridade a quem sem dirija;'
					},
					"3":{
						"caput":'lidos no Perodo do Expediente, sero despachados  Mesa para deciso;'
					},
					"4":{
						"caput":'se deferidos, sero solicitadas,  autoridade competente, as informaes requeridas, ficando interrompida a tramitao da matria que se pretende esclarecer. Se indeferidos, iro ao Arquivo, feita comunicao ao Plenrio;'
					},
					"5":{
						"caput":'as informaes recebidas, quando se destinarem  elucidao de matria pertinente a proposio em curso no Senado, sero incorporadas ao respectivo processo.'
					},
			}
		}]
	},
	"217":{
		"caput":'O requerimento de remessa de documentos equipara-se ao de pedido de informaes.'
	},
	"218":{
		"caput":'O requerimento de insero em ata de voto de pesar s  admissvel por motivo de luto nacional decretado pelo Poder Executivo, ou por falecimento de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pessoa que tenha exercido o cargo de Presidente ou Vice-Presidente da Repblica;'
					},
					"2":{
						"caput":'ex-membro do Congresso Nacional;'
					},
					"3":{
						"caput":'pessoa que exera ou tenha exercido o cargo de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'do Supremo Tribunal Federal;'
									},
									"2":{
										"caput":'de Tribunal Superior da Unio;'
									},
									"3":{
										"caput":'do Tribunal de Contas da Unio;'
									},
									"4":{
										"caput":'de Estado;'
									},
									"5":{
										"caput":'Presidente de Assembleia Legislativa ou de Tribunal de Justia estadual;'
									},
									"6":{
										"caput":'de Territrio ou do Distrito Federal;'
									},
							}
						}]
					},
					"4":{
						"caput":'Chefe de Estado ou de governo estrangeiro;'
					},
					"5":{
						"caput":'Chefe de Misso Diplomtica de pas estrangeiro acreditada junto ao Governo Brasileiro;'
					},
					"6":{
						"caput":'Chefe de Misso Diplomtica do Brasil junto a governo estrangeiro, falecido no posto;'
					},
					"7":{
						"caput":'personalidade de relevo na vida poltico-administrativa internacional.'
					},
			}
		}]
	},
	"219":{
		"caput":'Ao serem prestadas homenagens de pesar, poder ser observado um minuto de silncio, em memria do extinto, aps usarem da palavra todos os oradores.'
	},
	"220":{
		"caput":'O requerimento de levantamento da sesso, por motivo de pesar, s  permitido em caso de falecimento do Presidente da Repblica, do Vice-Presidente da Repblica ou de membro do Congresso Nacional.'
	},
	"221":{
		"caput":'Alm das homenagens previstas nos arts. 218 a 220, o Plenrio poder autorizar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a apresentao de condolncias  famlia do falecido, ao Estado do seu nascimento ou ao em que tenha exercido a sua atividade, ao partido poltico e a altas entidades culturais a que haja pertencido;'
					},
					"2":{
						"caput":'a representao nos funerais e cerimnias levadas a efeito em homenagem  memria do extinto.'
					},
			}
		}]
	},
	"222":{
		"caput":'O Senador poder apresentar requerimento de voto de aplauso, congratulaes, louvor, solidariedade ou censura, que ser, aps lido no Perodo do Expediente, encaminhado em nome do autor.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se disser respeito a ato pblico ou a acontecimento de alta significao nacional ou internacional, o voto de aplauso, congratulaes, louvor, solidariedade ou censura poder, mediante requerimento subscrito por um tero da composio da Casa, ser encaminhado em nome do Senado Federal, aps sua aprovao pelo Plenrio.'
					},
					"2":{
						"caput":'Aplica-se o disposto no  1 se o requerimento for de iniciativa de comisso permanente, observado o disposto no art. 245.'
					},
					"3":{
						"caput":'Os requerimentos referidos nos  1 e 2 no apreciados durante a sesso legislativa em que foram apresentados sero arquivados definitivamente.'
					},
			}
		}]
	},
	"223":{
		"caput":'(Revogado).'
	},
	"224-X":{
		"caput":'Indicao corresponde a sugesto de Senador ou comisso para que o assunto, nela focalizado, seja objeto de providncia ou estudo pelo rgo competente da Casa, com a finalidade do seu esclarecimento ou formulao de proposio legislativa.'
	},
	"224":{
		"caput":'Indicao  a proposio por meio da qual o Senador ou a comisso: (Redao dada pela Resoluo n. 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sugere a outro Poder a adoo de providncia, a realizao de ato administrativo ou de gesto ou o envio de projeto sobre matria de sua iniciativa exclusiva; (Includo pela Resoluo n. 14, de 2019)'
					},
					"2":{
						"caput":'sugere que o assunto focalizado seja objeto de providncia ou estudo pelo rgo ou pela comisso competente da Casa, com a finalidade de seu esclarecimento ou de formulao de proposio legislativa. (Includo pela Resoluo n. 14, de 2019)'
					},
			}
		}]
	},
	"225":{
		"caput":'A indicao no poder conter:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'consulta a qualquer comisso sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ou aplicao de lei;'
									},
									"2":{
										"caput":'de outro Poder;'
									},
									"3":{
										"caput":'de outro Poder ou de seus rgos e autoridades; (Redao dada pela Resoluo n. 14, de 2019)'
									},
							}
						}]
					},
					"2":{
						"caput":'sugesto ou conselho a qualquer Poder.'
					},
					"3":{
						"caput":'conselho a qualquer Poder. (Redao dada pela Resoluo n. 14, de 2019)'
					},
			}
		}]
	},
	"226-X":{
		"caput":'Lida no Perodo do Expediente, a indicao ser encaminhada  comisso competente.'
	},
	"226":{
		"caput":'Lida no Perodo do Expediente e publicada no Dirio do Senado Federal, a indicao ser: (Redao dada pela Resoluo n. 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 224, inciso I, encaminhada pelo Presidente  autoridade de outro Poder; (Includo pela Resoluo n. 14, de 2019)'
					},
					"2":{
						"caput":'no caso do art. 224, inciso II, encaminhada pelo Presidente: (Includo pela Resoluo n. 14, de 2019)',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'rgo competente da Casa; (Includa pela Resoluo n. 14, de 2019)'
									},
									"2":{
										"caput":'comisso ou s comisses competentes. (Includa pela Resoluo n. 14, de 2019)'
									},
							}
						}]
					},
			}
		}]
	},
	"227-X":{
		"caput":'A indicao no ser discutida nem votada pelo Senado. A deliberao tomar por base a concluso do parecer da comisso.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se a indicao for encaminhada a mais de uma comisso e os pareceres forem discordantes nas suas concluses, ser votado, preferencialmente, o da que tiver mais pertinncia regimental para se manifestar sobre a matria. Em caso de competncia concorrente, votar-se-, preferencialmente, o ltimo, salvo se o Plenrio decidir o contrrio, a requerimento de qualquer Senador ou comisso. (Suprimido pela Resoluo n. 14, de 2019)'
					},
			}
		}]
	},
	"227":{
		"caput":'A indicao no ser discutida nem votada pelo Senado. (Redao dada pela Resoluo n. 14, de 2019)',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No caso do art. 226, inciso II, alnea "a", se o rgo competente da Casa sugerir a apresentao de proposio legislativa, a indicao ser encaminhada ao seu autor para, se for o caso, apresentar a proposio, a qual seguir os trmites regimentais das proposies congneres. (Includo pela Resoluo n. 14, de 2019)'
					},
					"2":{
						"caput":'No caso do art. 226, inciso II, alnea "b": (Includo pela Resoluo n. 14, de 2019)',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'se o parecer da comisso competente concluir pela apresentao de proposio legislativa, esta seguir os trmites regimentais das proposies congneres; (Includo pela Resoluo n. 14, de 2019)'
									},
									"2":{
										"caput":'se a indicao for encaminhada a mais de uma comisso e os pareceres forem discordantes nas suas concluses: (Includo pela Resoluo n. 14, de 2019)',
										"enumeravel": [{
											"tipo":"letra",
											"lista":{
													"1":{
														"caput":'votado, preferencialmente, o da comisso que tiver mais pertinncia regimental para se manifestar sobre a matria; (Includa pela Resoluo n. 14, de 2019)'
													},
													"2":{
														"caput":'caso de competncia concorrente, ser votado, preferencialmente, o ltimo, salvo se o Plenrio decidir o contrrio, a requerimento de qualquer Senador ou comisso. (Includa pela Resoluo n. 14, de 2019)'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"227-A":{
		"caput":'A proposio na qual for verificado vcio insanvel de iniciativa poder ser convertida em indicao: (Includo pela Resoluo n. 14, de 2019)',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por requerimento de seu autor; (Includo pela Resoluo n. 14, de 2019)'
					},
					"2":{
						"caput":'por concluso do parecer da comisso incumbida de analisar sua constitucionalidade. (Includo pela Resoluo n. 14, de 2019)'
					},
			}
		}]
	},
	"228":{
		"caput":'Constitui proposio o parecer que deva ser discutido e votado pelo Plenrio, quando no concluir pela apresentao de projeto, requerimento ou emenda.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para discusso e votao, o parecer ser includo em Ordem do Dia.'
					},
			}
		}]
	},
	"229-X":{
		"caput":'Se houver mais de um parecer, de concluses discordantes, sobre a mesma matria, a ser submetida ao Plenrio, proceder-se- de acordo com a norma estabelecida no art. 227, pargrafo nico.'
	},
	"229":{
		"caput":'Se houver mais de um parecer, de concluses discordantes, sobre a mesma matria, a ser submetida ao Plenrio, proceder-se- de acordo com o disposto no art. 227,  2, inciso II. (Redao dada pela Resoluo n. 14, de 2019)'
	},
	"230":{
		"caput":'No se admitir emenda:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'sem relao com a matria da disposio que se pretenda emendar;'
					},
					"2":{
						"caput":'em sentido contrrio  proposio quando se trate de proposta de emenda  Constituio, projeto de lei ou de resoluo;'
					},
					"3":{
						"caput":'que diga respeito a mais de um dispositivo, a no ser que se trate de modificaes correlatas, de sorte que a aprovao, relativamente a um dispositivo, envolva a necessidade de se alterarem outros;'
					},
					"4":{
						"caput":'que importe aumento da despesa prevista (Const., art. 63):',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos de iniciativa do Presidente da Repblica, ressalvado o disposto no art. 166,  3 e 4, da Constituio (Const., art. 63, I);'
									},
									"2":{
										"caput":'projetos sobre organizao dos servios administrativos do Senado Federal, dos Tribunais Federais e do Ministrio Pblico (Const., art. 63, II).'
									},
							}
						}]
					},
			}
		}]
	},
	"231":{
		"caput":'s comisses  admitido oferecer subemendas, as quais no podero conter matria estranha  das respectivas emendas.'
	},
	"232":{
		"caput":'A emenda no adotada pela comisso (art. 124, I) poder ser renovada em plenrio, salvo sendo unnime o parecer pela rejeio.'
	},
	"233":{
		"caput":'Nenhuma emenda ser aceita sem que o autor a tenha justificado por escrito ou oralmente.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A justificao oral de emenda em plenrio dever ser feita no prazo que seu autor dispuser para falar no Perodo do Expediente da sesso.'
					},
			}
		}]
	},
	"234":{
		"caput":'A emenda que altere apenas a redao da proposio ser submetida s mesmas formalidades regimentais de que dependerem as pertinentes ao mrito.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando houver dvidas sobre se a emenda apresentada como de redao atinge a substncia da proposio, ouvir-se- a Comisso de Constituio, Justia e Cidadania.'
					},
			}
		}]
	},
	"235":{
		"caput":'A apresentao de proposio ser feita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'perante comisso, quando se tratar de emenda apresentada de acordo com o disposto no art. 122;'
					},
					"2":{
						"caput":'perante a Mesa, no prazo de cinco dias teis, quando se tratar de emenda a:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de alterao ou reforma do Regimento Interno;'
									},
									"2":{
										"caput":'de decreto legislativo referente a prestao de contas do Presidente da Repblica;'
									},
									"3":{
										"caput":'apreciados pelas comisses com poder terminativo, quando houver interposio de recurso;'
									},
									"4":{
										"caput":'em turno nico, que obtiver parecer favorvel, quanto ao mrito, das comisses;'
									},
									"5":{
										"caput":'em turno nico, que obtiver parecer contrrio, quanto ao mrito, das comisses, desde que admitido recurso para sua tramitao;'
									},
									"6":{
										"caput":'de autoria de comisso;'
									},
							}
						}]
					},
					"3":{
						"caput":'em plenrio, nos seguintes casos:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'Perodo do Expediente:'
									},
									"2":{
										"caput":'emenda a matria a ser votada nessa fase da sesso;'
									},
									"3":{
										"caput":'indicao;'
									},
									"4":{
										"caput":'projeto;'
									},
									"5":{
										"caput":'requerimento que, regimentalmente, no deva ser apresentado em outra fase da sesso;'
									},
									"6":{
										"caput":'Ordem do Dia:'
									},
									"7":{
										"caput":'requerimento que diga respeito  ordenao das matrias da Ordem do Dia ou a proposio dela constante;'
									},
									"8":{
										"caput":'emenda a projeto em turno suplementar, ao anunciar-se sua discusso;'
									},
									"9":{
										"caput":'a Ordem do Dia - requerimento de:'
									},
									"10":{
										"caput":'incluso, em Ordem do Dia, de matria em condies de nela figurar;'
									},
									"11":{
										"caput":'dispensa de publicao de redao final para imediata deliberao do Plenrio;'
									},
									"12":{
										"caput":'fase da sesso em que a matria respectiva foi anunciada - requerimento de:'
									},
									"13":{
										"caput":'adiamento de discusso ou votao;'
									},
									"14":{
										"caput":'encerramento de discusso;'
									},
									"15":{
										"caput":'dispensa de discusso;'
									},
									"16":{
										"caput":'votao por determinado processo;'
									},
									"17":{
										"caput":'votao em globo ou parcelada;'
									},
									"18":{
										"caput":'destaque de dispositivo ou emenda para aprovao, rejeio, votao em separado ou constituio de proposio autnoma;'
									},
									"19":{
										"caput":'retirada de proposio constante da Ordem do Dia;'
									},
									"20":{
										"caput":'qualquer fase da sesso - requerimento de:'
									},
									"21":{
										"caput":'leitura de qualquer matria sujeita ao conhecimento do Senado;'
									},
									"22":{
										"caput":'permisso para falar sentado;'
									},
									"23":{
										"caput":'do trmino da sesso, requerimento de prorrogao desta.'
									},
							}
						}]
					},
			}
		}]
	},
	"236":{
		"caput":'As proposies devem ser escritas em termos concisos e claros e divididas, sempre que possvel, em artigos, pargrafos, incisos e alneas.'
	},
	"237":{
		"caput":'Os projetos, pareceres e indicaes devem ser encimados por ementa.'
	},
	"238":{
		"caput":'As proposies, salvo os requerimentos, devem ser acompanhadas de justificao oral ou escrita, observado o disposto no pargrafo nico do art. 233.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Havendo vrias emendas do mesmo autor, dependentes de justificao oral,  lcito justific-las em conjunto.'
					},
			}
		}]
	},
	"239":{
		"caput":'Qualquer proposio autnoma ser sempre acompanhada de transcrio, na ntegra ou em resumo, das disposies de lei invocadas em seu texto.'
	},
	"240":{
		"caput":'As matrias constantes de projeto de lei rejeitado somente podero ser objeto de novo projeto, na mesma sesso legislativa, mediante proposta da maioria absoluta dos membros do Senado (Const., art. 67).'
	},
	"241":{
		"caput":'As proposies que devam ser objeto de imediata deliberao do Plenrio sero lidas integralmente, sendo as demais anunciadas em smula.'
	},
	"242":{
		"caput":'O projeto ou requerimento de autoria individual de Senador, salvo requerimento de licena e de autorizao para o desempenho de misso, s ser lido quando presente seu autor.'
	},
	"243":{
		"caput":'Considera-se autor da proposio o seu primeiro signatrio quando a Constituio ou este Regimento no exija, para a sua apresentao, nmero determinado de subscritores, no se considerando, neste ltimo caso, assinaturas de apoiamento.'
	},
	"244":{
		"caput":'Ao signatrio de proposio s  lcito dela retirar sua assinatura antes da publicao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos casos de proposio dependente de nmero mnimo de subscritores, se, com a retirada de assinatura, esse limite no for alcanado, o Presidente a devolver ao primeiro signatrio, dando conhecimento do fato ao Plenrio.'
					},
			}
		}]
	},
	"245":{
		"caput":'Considera-se de comisso a proposio que, com esse carter, for por ela apresentada.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A proposio de comisso deve ser assinada pelo seu Presidente e instruda com a lista dos presentes  reunio em que ocorreu sua apresentao, totalizando pelo menos a maioria de seus membros.'
					},
			}
		}]
	},
	"246":{
		"caput":'As proposies sero numeradas de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'tero numerao anual, em sries especficas:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'propostas de emenda  Constituio;'
									},
									"2":{
										"caput":'projetos de lei da Cmara;'
									},
									"3":{
										"caput":'projetos de lei do Senado;'
									},
									"4":{
										"caput":'projetos de decreto legislativo, com especificao da Casa de origem;'
									},
									"5":{
										"caput":'projetos de resoluo;'
									},
									"6":{
										"caput":'requerimentos;'
									},
									"7":{
										"caput":'indicaes;'
									},
									"8":{
										"caput":'pareceres.'
									},
							}
						}]
					},
					"2":{
						"caput":'as emendas sero numeradas, em cada turno, pela ordem dos artigos da proposio emendada, guardada a sequncia determinada pela sua natureza, a saber: supressivas, substitutivas, modificativas e aditivas;'
					},
					"3":{
						"caput":'as subemendas de comisso figuraro ao fim da srie das emendas de sua iniciativa, subordinadas ao ttulo subemendas, com a indicao das emendas a que correspondam. Quando'
					},
					"4":{
						"caput":'emenda forem apresentadas vrias subemendas, estas tero numerao ordinal em relao  emenda respectiva;'
					},
					"5":{
						"caput":'as emendas da Cmara sero anexadas ao processo do projeto primitivo e tramitaro com o nmero deste.'
					},
			}
		}]
	},
	"247":{
		"caput":'A proposio apresentada em plenrio s ser submetida a apoia-mento por solicitao de qualquer Senador.'
	},
	"248":{
		"caput":'A votao de apoiamento no ser encaminhada, salvo se algum Senador pedir a palavra para combat-lo, caso em que o encaminhamento ficar adstrito a um Senador de cada partido ou bloco parlamentar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O quorum para aprovao do apoiamento  de um dcimo da composio do Senado.'
					},
			}
		}]
	},
	"249":{
		"caput":'Toda proposio apresentada ao Senado ser publicada no Dirio do Senado Federal, na ntegra, acompanhada, quando for o caso, da justificao e da legislao citada.'
	},
	"250":{
		"caput":'Ser publicado em avulso eletrnico, para distribuio aos Senadores e comisses, o texto de toda proposio apresentada ao Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Ao fim da fase de instruo da matria, sero publicados em avulsos eletrnicos os pareceres proferidos, neles se incluindo:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'o texto das emendas, caso no tenham sido publicadas em avulso eletrnico especial;'
									},
									"2":{
										"caput":'os votos em separado;'
									},
									"3":{
										"caput":'as informaes prestadas sobre a matria pelos rgos consultados;'
									},
									"4":{
										"caput":'os relatrios e demais documentos referidos no art. 261,  1.'
									},
							}
						}]
					},
			}
		}]
	},
	"251":{
		"caput":'Cada proposio, salvo emenda, ter curso prprio.'
	},
	"252":{
		"caput":'Lida perante o Plenrio, a proposio ser objeto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de deciso da Mesa, no caso do art. 215, I;'
					},
					"2":{
						"caput":'de deciso do Presidente, nos casos do art. 214, pargrafo nico, e art. 215, II;'
					},
					"3":{
						"caput":'de deliberao de comisso, na forma do art. 91;'
					},
					"4":{
						"caput":'de deliberao do Plenrio, nos demais casos.'
					},
			}
		}]
	},
	"253":{
		"caput":'Antes da deliberao do Plenrio, haver manifestao das comisses competentes para estudo da matria.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando se tratar de requerimento, s sero submetidos  apreciao das comisses os seguintes:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'(Revogado);'
									},
									"2":{
										"caput":'de sobrestamento do estudo de proposio (art. 335, pargrafo nico).'
									},
							}
						}]
					},
			}
		}]
	},
	"254":{
		"caput":'Quando os projetos receberem pareceres contrrios, quanto ao mrito, sero tidos como rejeitados e arquivados definitivamente, salvo recurso de um dcimo dos membros do Senado no sentido de sua tramitao.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A comunicao do arquivamento ser feita pelo Presidente, em plenrio, podendo o recurso ser apresentado no prazo de dois dias teis contado da comunicao.'
					},
			}
		}]
	},
	"255":{
		"caput":'A deliberao do Senado ser:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'na mesma sesso, aps a matria constante da Ordem do Dia, nos requerimentos que solicitem:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'no caso do art. 336, II;'
									},
									"2":{
										"caput":'de sesso deliberativa extraordinria, especial ou secreta;'
									},
									"3":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'mediante incluso em Ordem do Dia, quando se tratar de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
									"3":{
										"caput":'de:'
									},
									"4":{
										"caput":'urgncia do art. 336, III;'
									},
									"5":{
										"caput":'(Revogado);'
									},
									"6":{
										"caput":'incluso em Ordem do Dia de matria que no tenha recebido parecer no prazo regimental (art. 172, I);'
									},
									"7":{
										"caput":'audincia de comisso que no tenha oferecido parecer no prazo regimental (art. 119, pargrafo nico);'
									},
									"8":{
										"caput":'dispensa de parecer da comisso que haja esgotado o prazo a ela destinado (art. 119, caput);'
									},
									"9":{
										"caput":'constituio de comisso temporria;'
									},
									"10":{
										"caput":'voto de aplauso, congratulaes, louvor, solidariedade ou censura (arts. 222);'
									},
									"11":{
										"caput":'tramitao em conjunto, de projetos regulando a mesma matria, quando houver parecer aprovado em comisso (art. 258, pargrafo nico, in fine);'
									},
									"12":{
										"caput":'comparecimento, ao plenrio, de Ministro de Estado e titular de rgo diretamente subordinado ao Presidente da Repblica (Const., art. 50);',
										"enumeravel": [{
											"tipo":"numero",
											"lista":{
													"1":{
														"caput":'retirada de proposio com parecer de comisso;'
													},
													"2":{
														"caput":'sobrestamento do estudo de proposio;'
													},
													"3":{
														"caput":'remessa a determinada comisso de matria despachada a outra.',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'imediata, nos requerimentos no constantes dos incisos I e II.'
																	},
															}
														}]
													},
													"4":{
														"caput":'Ao ser anunciado o requerimento constante do inciso II, c, 3, ser dada a palavra ao Presidente da Comisso em que se ache o projeto para se manifestar sobre a providncia requerida.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"256":{
		"caput":'A retirada de proposies em curso no Senado  permitida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a de autoria de um ou mais Senadores, mediante requerimento do nico signatrio ou do primeiro deles;'
					},
					"2":{
						"caput":'a de autoria de comisso, mediante requerimento de seu Presidente ou do Relator da matria, com a declarao expressa de que assim procede devidamente autorizado.'
					},
			}
		}]
	},
	"257":{
		"caput":'Quando, na Comisso de Constituio, Justia e Cidadania, o relator se pronunciar pela inconstitucionalidade ou injuridicidade da proposio,  permitida sua retirada, antes de proferido o parecer definitivo, mediante requerimento ao Presidente da Comisso, que, o deferindo, encaminhar a matria  Mesa, atravs de ofcio, a fim de ser arquivada.'
	},
	"258":{
		"caput":'Havendo em curso no Senado duas ou mais proposies regulando a mesma matria,  lcito promover sua tramitao em conjunto a partir de requerimento de comisso ou de Senador, mediante deliberao da Mesa, salvo as que j foram objeto de parecer aprovado em comisso ou que constem da Ordem do Dia.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Os requerimentos de tramitao conjunta de matrias que j constem da Ordem do Dia ou que tenham parecer aprovado em comisso sero submetidos  deliberao do Plenrio.'
					},
			}
		}]
	},
	"259":{
		"caput":'Aprovado o requerimento de tramitao conjunta, os projetos sero remetidos  Comisso de Constituio, Justia e Cidadania, se sobre algum deles for necessria a apreciao dos aspectos constitucional e jurdico, ou  comisso a que tenham sido distribudos, para apreciao do mrito.'
	},
	"260":{
		"caput":'Na tramitao em conjunto, sero obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ao processo do projeto que deva ter precedncia sero apensos, sem incorporaes, os dos demais;'
					},
					"2":{
						"caput":'ter precedncia:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projeto da Cmara sobre o do Senado;'
									},
									"2":{
										"caput":'mais antigo sobre o mais recente, quando originrios da mesma Casa;'
									},
							}
						}]
					},
					"3":{
						"caput":'em qualquer caso, a proposio ser includa, em srie, com as demais, na Ordem do Dia, obedecido, no processamento dos pareceres, o disposto no art. 268.'
					},
			}
		}]
	},
	"261":{
		"caput":'O processo referente a cada proposio, salvo emenda, ser organizado de acordo com as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ser autuada a proposio principal, consignando-se na respectiva capa, no ato da organizao do processo:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'natureza da proposio;'
									},
									"2":{
										"caput":'Casa de origem;'
									},
									"3":{
										"caput":'nmero;'
									},
									"4":{
										"caput":'ano de apresentao;'
									},
									"5":{
										"caput":'ementa completa;'
									},
									"6":{
										"caput":'autor, quando do Senado;'
									},
							}
						}]
					},
					"2":{
						"caput":'em seguida  capa figuraro folhas avulsas, de impresso especial, conforme modelo aprovado pela Comisso Diretora, em duas vias, para original e cpia, constituindo estas ltimas os boletins de ao legislativa que iro fornecer informaes ao Centro de Processamento de Dados, para registro das matrias em tramitao; e ainda:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'projetos da Cmara:'
									},
									"2":{
										"caput":'o ofcio de encaminhamento;'
									},
									"3":{
										"caput":'o autgrafo recebido e os documentos que o tiverem acompanhado;'
									},
									"4":{
										"caput":'o resumo da tramitao na Casa de origem;'
									},
									"5":{
										"caput":'um exemplar de cada avulso eletrnico;'
									},
									"6":{
										"caput":'as demais vias dos avulsos eletrnicos e de outros documentos, em sobrecarta anexada ao processo;'
									},
									"7":{
										"caput":'projetos do Senado:'
									},
									"8":{
										"caput":'o texto, a justificao e a legislao citada, quando houver;'
									},
									"9":{
										"caput":'o recorte do Dirio do Senado Federal, com a justificao oral, quando houver;'
									},
									"10":{
										"caput":'os documentos que o acompanhem;'
									},
									"11":{
										"caput":'as duplicatas do projeto e dos demais documentos, em sobrecarta anexada ao processo;'
									},
							}
						}]
					},
					"3":{
						"caput":'as peas do processo sero numeradas e rubricadas no Servio de Protocolo Legislativo antes de seu encaminhamento  Secretaria-Geral da Mesa, para leitura da matria em plenrio;'
					},
					"4":{
						"caput":'sero ainda registradas, no impresso especial, pelo funcionrio do rgo por onde passar o processo, todas as aes legislativas e administrativas que ocorrerem durante sua tramitao:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ocorrncias da tramitao em cada comisso, o encaminhamento  seguinte e, finalmente,  Mesa;'
									},
									"2":{
										"caput":'incluso em Ordem do Dia;'
									},
									"3":{
										"caput":'tramitao em plenrio;'
									},
									"4":{
										"caput":'manifestao do Senado sobre a matria;'
									},
									"5":{
										"caput":'remessa  sano,  promulgao ou  Cmara;'
									},
									"6":{
										"caput":'transformao em lei, decreto legislativo ou resoluo, com o nmero e data respectivos;'
									},
									"7":{
										"caput":'houver veto, todas as ocorrncias a ele relacionadas;'
									},
									"8":{
										"caput":'despacho do arquivamento;'
									},
									"9":{
										"caput":'desarquivamentos e novos incidentes;'
									},
							}
						}]
					},
					"5":{
						"caput":'o Servio de Protocolo Legislativo, ao receber o processo, em qualquer oportunidade, atualizar a numerao das pginas que devero ser rubricadas pelo funcionrio responsvel.'
					},
			}
		}]
	},
	"262":{
		"caput":'Relativamente aos documentos de natureza sigilosa, observar-se-o as normas constantes dos arts. 144 e 157, II e III, e, terminado o curso da matria, sero recolhidos ao arquivo especial dos documentos com esse carter, em sobrecarta fechada, rubricada pelo Presidente da Mesa, feita na capa do processo a devida anotao.'
	},
	"263":{
		"caput":'As representaes dirigidas  Mesa, contendo observaes, sugestes ou solicitaes sobre proposies em curso no Senado, sero lidas no Perodo do Expediente, publicadas, em smula ou na ntegra, no Dirio do Senado Federal, reunidas em processo especial e encaminhadas s respectivas comisses para conhecimento dos relatores e consulta dos demais membros, acompanhando a proposio em todas as suas fases.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":' facultado aos Senadores encaminhar ao rgo competente as representaes que receberem, para anexao ao processo.'
					},
			}
		}]
	},
	"264":{
		"caput":'Ao ser arquivada a proposio, ser-lhe- anexada uma coleo dos avulsos eletrnicos publicados para sua instruo no Senado e na Cmara, quando for o caso.'
	},
	"265":{
		"caput":'A deciso do Plenrio, apoiando, aprovando, rejeitando proposio ou destacando emenda para constituir projeto em separado, ser anotada, com a data respectiva, no texto votado, e assinada pela Presidncia.'
	},
	"266":{
		"caput":'O processo da proposio ficar sobre a mesa durante sua tramitao em plenrio.'
	},
	"267":{
		"caput":'Ocorrendo extravio de qualquer proposio, a Presidncia determinar providncias objetivando sua reconstituio, de ofcio ou mediante requerimento de qualquer Senador ou comisso, independentemente de deliberao do Plenrio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando se tratar de projeto da Cmara, a Mesa solicitar, da Casa de origem, a remessa de cpias autenticadas dos respectivos autgrafos e documentos que o tenham acompanhado.'
					},
					"2":{
						"caput":'Os pareceres j proferidos no Senado sero anexados ao novo processo em cpias autenticadas pelos Presidentes das respectivas comisses.'
					},
					"3":{
						"caput":'A reconstituio do processo dever ser feita pelo rgo onde este se encontrava por ocasio de seu extravio.'
					},
			}
		}]
	},
	"268":{
		"caput":'Quando a comisso, no mesmo parecer, se referir a vrias proposies autnomas, o original dele instruir o processo da proposio preferencial, sendo aos demais anexadas cpias autenticadas pelo respectivo Presidente.'
	},
	"269":{
		"caput":'A Presidncia far publicar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no princpio de cada sesso legislativa, a sinopse de todas as proposies em curso ou resolvidas pelo Senado na sesso anterior;'
					},
					"2":{
						"caput":'mensalmente, a resenha das matrias rejeitadas e as enviadas, no ms anterior,  sano,  promulgao e  Cmara.'
					},
			}
		}]
	},
	"270":{
		"caput":'As proposies em curso no Senado so subordinadas, em sua apreciao, a um nico turno de discusso e votao, salvo proposta de emenda  Constituio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Havendo substitutivo integral, aprovado pelo Plenrio no turno nico, o projeto ser submetido a turno suplementar.'
					},
			}
		}]
	},
	"271":{
		"caput":'Cada turno  constitudo de discusso e votao.'
	},
	"272":{
		"caput":'A discusso da proposio principal e das emendas ser em conjunto.'
	},
	"273":{
		"caput":'Anunciada a matria, ser dada a palavra aos oradores para a discusso.'
	},
	"274":{
		"caput":'A discusso no ser interrompida, salvo para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'formulao de questo de ordem;'
					},
					"2":{
						"caput":'adiamento para os fins previstos no art. 279;'
					},
					"3":{
						"caput":'tratar de proposio compreendida no art. 336, I;'
					},
					"4":{
						"caput":'os casos previstos no art. 305;'
					},
					"5":{
						"caput":'comunicao importante ao Senado;'
					},
					"6":{
						"caput":'recepo de visitante;'
					},
					"7":{
						"caput":'votao de requerimento de prorrogao da sesso;'
					},
					"8":{
						"caput":'ser suspensa a sesso (art. 18, I, f).'
					},
			}
		}]
	},
	"275":{
		"caput":'Encerra-se a discusso:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pela ausncia de oradores;'
					},
					"2":{
						"caput":'por deliberao do Plenrio, a requerimento de qualquer Senador, quando j houverem falado, pelo menos, trs Senadores a favor e trs contra.'
					},
			}
		}]
	},
	"276":{
		"caput":'As proposies com pareceres favorveis podero ter a discusso dispensada por deliberao do Plenrio, mediante requerimento de lder.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A dispensa da discusso dever ser requerida ao ser anunciada a matria.'
					},
			}
		}]
	},
	"277":{
		"caput":'Lidos os pareceres das comisses sobre as proposies, em turno nico, e publicados em avulsos eletrnicos, abrir-se- o prazo de cinco dias teis para apresentao de emendas, findo o qual a matria, se emendada, voltar s comisses para exame.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No sendo emendada, a proposio estar em condies de figurar em Ordem do Dia, obedecido o interstcio regimental.'
					},
			}
		}]
	},
	"278":{
		"caput":'Lidos os pareceres sobre as emendas, publicados no Dirio do Senado Federal e em avulso eletrnico, estar a matria em condies de figurar em Ordem do Dia, obedecido o interstcio regimental.'
	},
	"279":{
		"caput":'A discusso, salvo nos projetos em regime de urgncia e o disposto no art. 349, poder ser adiada, mediante deliberao do Plenrio, a requerimento de qualquer Senador ou comisso, para os seguintes fins:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'audincia de comisso que sobre ela no se tenha manifestado;'
					},
					"2":{
						"caput":'reexame por uma ou mais comisses por motivo justificado;'
					},
					"3":{
						"caput":'ser realizada em dia determinado;'
					},
					"4":{
						"caput":'preenchimento de formalidade essencial;'
					},
					"5":{
						"caput":'diligncia considerada imprescindvel ao seu esclarecimento.'
					},
			}
		}]
	},
	"280":{
		"caput":' de trs dias teis o interstcio entre a publicao de avulsos eletrnicos dos pareceres das comisses e o incio da discusso ou votao correspondente.'
	},
	"281":{
		"caput":'A dispensa de interstcio e prvia publicao de avulso eletrnico, para incluso de matria em Ordem do Dia, poder ser concedida por deliberao do Plenrio, a requerimento de qualquer Senador, desde que a proposio esteja h mais de cinco dias em tramitao no Senado.'
	},
	"282":{
		"caput":'Sempre que for aprovado substitutivo integral a projeto de lei, de decreto legislativo ou de resoluo em turno nico, ser ele submetido a turno suplementar.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos projetos sujeitos a prazo de tramitao, o turno suplementar realizar-se- dois dias teis aps a aprovao do substitutivo, se faltarem oito dias, ou menos, para o trmino do referido prazo.'
					},
					"2":{
						"caput":'Podero ser oferecidas emendas no turno suplementar, por ocasio da discusso da matria, vedada a apresentao de novo substitutivo integral.'
					},
			}
		}]
	},
	"283":{
		"caput":'Se forem oferecidas emendas, na discusso suplementar, a matria ir s comisses competentes, que no podero concluir seu parecer por novo substitutivo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Nos projetos sujeitos a prazo de tramitao, a matria ser includa em Ordem do Dia na sesso deliberativa ordinria seguinte se faltarem cinco dias, ou menos, para o trmino do referido prazo, podendo o parecer ser proferido em plenrio.'
					},
			}
		}]
	},
	"284":{
		"caput":'No sendo oferecidas emendas na discusso suplementar, o substitutivo ser dado como definitivamente adotado sem votao.'
	},
	"285":{
		"caput":'A emenda da Cmara a projeto do Senado no  suscetvel de modificao por meio de subemenda.'
	},
	"286":{
		"caput":'A discusso e a votao das emendas da Cmara a projeto do Senado far-se-o em globo, exceto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'se qualquer comisso manifestar-se favoravelmente a umas e contrariamente a outras, caso em que a votao se far em grupos, segundo os pareceres;'
					},
					"2":{
						"caput":'se for aprovado destaque para a votao de qualquer emenda.'
					},
			}
		}]
	},
	"287":{
		"caput":'O substitutivo da Cmara a projeto do Senado ser considerado srie de emendas e votado, separadamente, por artigos, pargrafos, incisos, alneas e itens, em correspondncia aos do projeto emendado, salvo aprovao de requerimento para votao em globo ou por grupos de dispositivos, obedecido o disposto no pargrafo nico do art. 286.'
	},
	"288":{
		"caput":'As deliberaes do Senado sero tomadas por maioria de votos, presente a maioria absoluta dos seus membros (Const., art. 47), salvo nos seguintes casos, em que sero:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por voto favorvel de dois teros da composio da Casa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'condenatria nos casos previstos no art. 52, I e II, da Constituio;'
									},
									"2":{
										"caput":'de alquotas mximas nas operaes internas, para resolver conflito especfico que envolva interesse de Estados e do Distrito Federal (Const., art. 155,  2, V, b);'
									},
									"3":{
										"caput":'de imunidade de Senadores, durante o estado de stio (Const., art. 53,  8);'
									},
							}
						}]
					},
					"2":{
						"caput":'por voto favorvel de trs quintos da composio da Casa, proposta de emenda  Constituio (Const., art. 60,  2);'
					},
					"3":{
						"caput":'por voto favorvel da maioria absoluta da composio da Casa:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de lei complementar (Const., art. 69);'
									},
									"2":{
										"caput":'de ofcio, do Procurador-Geral da Repblica (Const., art. 52, XI);'
									},
									"3":{
										"caput":'de mandato de Senador, nos casos previstos no art. 55,  2, da Constituio;'
									},
									"4":{
										"caput":'de nome indicado para Ministro do Supremo Tribunal Federal (Const., art. 101, pargrafo nico) para Procurador-Geral da Repblica (Const., art. 128,  1); para Ministro do Superior Tribunal de Justia (Const., art. 104, pargrafo nico) e para Ministro do Tribunal Superior do Trabalho (Const., art. 111-A);'
									},
									"5":{
										"caput":'de ato do Presidente da Repblica que decretar o estado de defesa (Const., art. 136,  4);'
									},
									"6":{
										"caput":'para o Presidente da Repblica decretar o estado de stio (Const., art. 137, pargrafo nico);'
									},
									"7":{
										"caput":'de alquotas aplicveis s operaes e prestaes interestaduais e de exportao (Const., art. 155,  2, IV);'
									},
									"8":{
										"caput":'de alquotas mnimas nas operaes internas (Const., art. 155,  2, V, a);'
									},
									"9":{
										"caput":'de operaes de crdito que excedam o montante das despesas de capital, mediante crditos suplementares ou especiais especficos (Const. art. 167, III);'
									},
									"10":{
										"caput":'de nome indicado para Defensor Pblico-Geral Federal;'
									},
									"11":{
										"caput":''
									},
									"12":{
										"caput":'de nome indicado para o Conselho Nacional de Justia (Const., art. 103-B, caput e  2);'
									},
									"13":{
										"caput":'de nome indicado para o Conselho Nacional do Ministrio Pblico (Const., art. 130-A, caput);'
									},
							}
						}]
					},
					"4":{
						"caput":'por voto favorvel de dois quintos da composio da Casa, aprovao da no renovao da concesso ou permisso para o servio de radiodifuso sonora e de sons e imagens (Const., art. 223,  2);'
					},
					"5":{
						"caput":'por maioria de votos, presentes um dcimo dos Senadores, nos requerimentos compreendidos no art. 215, III.'
					},
			}
		}]
	},
	"289":{
		"caput":'A votao poder ser ostensiva ou secreta.'
	},
	"290":{
		"caput":'Ser ostensiva a votao das proposies em geral.'
	},
	"291":{
		"caput":'Ser secreta a votao:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando o Senado tiver que deliberar sobre:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de ofcio, do Procurador-Geral da Repblica (Const., art. 52, XI);'
									},
									"2":{
										"caput":'de mandato de Senador, nos casos previstos no art. 55,  2, da Constituio;'
									},
									"3":{
										"caput":'de Senador e autorizao da formao de culpa, no caso de flagrante de crime inafianvel (Const., art. 53,  2);'
									},
									"4":{
										"caput":'das imunidades de Senador durante o estado de stio (Const., art. 53,  8);'
									},
									"5":{
										"caput":'de autoridades (Const., art. 52, III);'
									},
							}
						}]
					},
					"2":{
						"caput":'nas eleies;'
					},
					"3":{
						"caput":'por determinao do Plenrio.'
					},
			}
		}]
	},
	"292":{
		"caput":'Na votao, sero adotados os seguintes processos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'ostensiva:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":''
									},
							}
						}]
					},
					"2":{
						"caput":'secreta:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":''
									},
									"2":{
										"caput":'meio de cdulas;'
									},
									"3":{
										"caput":'meio de esfera.'
									},
							}
						}]
					},
			}
		}]
	},
	"293":{
		"caput":'No processo simblico observar-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'os Senadores que aprovarem a matria devero permanecer sentados, levantando-se os que votarem pela rejeio;'
					},
					"2":{
						"caput":'o voto dos lderes representar o de seus liderados presentes, permitida a declarao de voto em documento escrito a ser encaminhado  Mesa para publicao;'
					},
					"3":{
						"caput":'se for requerida verificao da votao, ser ela repetida pelo processo nominal;'
					},
					"4":{
						"caput":'o requerimento de verificao de votao s ser admissvel se apoiado por trs Senadores;'
					},
					"5":{
						"caput":'procedida a verificao de votao e constatada a existncia de nmero, no ser permitida nova verificao antes do decurso de uma hora;'
					},
					"6":{
						"caput":'no ser admitido requerimento de verificao se a Presidncia j houver anunciado a matria seguinte;'
					},
					"7":{
						"caput":'antes de anunciado o resultado, ser lcito tomar o voto do Senador que penetrar no recinto aps a votao;'
					},
					"8":{
						"caput":'verificada a falta de quorum, o Presidente suspender a sesso, fazendo acionar as campainhas durante dez minutos, aps o que esta ser reaberta, procedendo-se a nova votao;'
					},
					"9":{
						"caput":'confirmada a falta de nmero, ficar adiada a votao, que ser reiniciada ao voltar a matria  deliberao do Plenrio;'
					},
					"10":{
						"caput":'se, ao processar-se a verificao, os requerentes no estiverem presentes ou deixarem de votar, considerar-se- como tendo dela desistido.'
					},
			}
		}]
	},
	"294":{
		"caput":'O processo nominal, que se utilizar nos casos em que seja exigido quorum especial de votao ou por deliberao do Plenrio, a requerimento de qualquer Senador, ou ainda, quando houver pedido de verificao, far-se- pelo registro eletrnico dos votos, obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'os nomes dos Senadores constaro de apregoadores instalados, lateralmente, no plenrio, onde sero registrados individualmente:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'sinal verde, os votos favorveis;'
									},
									"2":{
										"caput":'sinal amarelo, as abstenes;'
									},
									"3":{
										"caput":'sinal vermelho, os votos contrrios;'
									},
							}
						}]
					},
					"2":{
						"caput":'cada Senador ter lugar fixo, numerado, que ocupar ao ser anunciada a votao, devendo acionar dispositivo prprio de uso individual, localizado na respectiva bancada;'
					},
					"3":{
						"caput":'os lderes votaro em primeiro lugar;'
					},
					"4":{
						"caput":'conhecido o voto das lideranas, votaro os demais Senadores;'
					},
					"5":{
						"caput":'verificado, pelo registro no painel de controle localizado na mesa, que houve empate na votao, o Presidente comunicar o fato ao Plenrio e a desempatar, transferindo, em seguida, o resultado aos apregoadores;'
					},
					"6":{
						"caput":'concluda a votao, o Presidente desligar o quadro, liberando o sistema para o processamento de nova votao;'
					},
					"7":{
						"caput":'o resultado da votao ser encaminhado  Mesa em listagem especial, onde estaro registrados:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'matria objeto da deliberao;'
									},
									"2":{
										"caput":'data em que se procedeu a votao;'
									},
									"3":{
										"caput":'voto individual de cada Senador;'
									},
									"4":{
										"caput":'resultado da votao;'
									},
									"5":{
										"caput":'total dos votantes;'
									},
							}
						}]
					},
					"8":{
						"caput":'o Primeiro-Secretrio rubricar a listagem especial, determinando sua anexao ao processo da matria respectiva.'
					},
			}
		}]
	},
	"295":{
		"caput":'A votao secreta realizar-se- pelo sistema eletrnico, salvo nas eleies.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Anunciada a votao, o Presidente convidar os Senadores a acionarem o dispositivo prprio, dando, em seguida, incio  fase de apurao.'
					},
					"2":{
						"caput":'Verificada a falta de quorum, proceder-se- na forma do art. 293, VIII, ficando adiada a votao se ocorrer, novamente, falta de nmero.'
					},
			}
		}]
	},
	"296":{
		"caput":'A votao por meio de cdulas far-se- nas eleies.'
	},
	"297":{
		"caput":'A votao por meio de esferas realizar-se- quando o equipamento de votao eletrnico no estiver em condies de funcionar, obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'utilizar-se-o esferas brancas, representando votos favorveis, e pretas, representando votos contrrios;'
					},
					"2":{
						"caput":'a esfera que for utilizada para exprimir voto ser lanada em uma urna e a que no for usada, em outra que servir para conferir o resultado da votao.'
					},
			}
		}]
	},
	"298":{
		"caput":'Terminada a apurao, o Presidente proclamar o resultado da votao, especificando os votos favorveis, contrrios, em branco, nulos e as abstenes.'
	},
	"299":{
		"caput":'A votao realizar-se- imediatamente aps a discusso, se este Regimento no dispuser noutro sentido.'
	},
	"300":{
		"caput":'Na votao, sero obedecidas as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'votar-se- em primeiro lugar o projeto, ressalvados os destaques dele requeridos e as emendas;'
					},
					"2":{
						"caput":'a votao do projeto, salvo deliberao do Plenrio, ser em globo, podendo a Presidncia dividir a proposio, quando conveniente;'
					},
					"3":{
						"caput":'a votao das emendas que tenham pareceres concordantes de todas as comisses ser feita em grupos, segundo o sentido dos pareceres, ressalvados os destaques; as demais e as destacadas sero votadas uma a uma, classificadas segundo a ordem estabelecida no art. 246, II;'
					},
					"4":{
						"caput":'no grupo das emendas de parecer favorvel incluem-se as de comisso, quando sobre elas no haja manifestao em contrrio de outra;'
					},
					"5":{
						"caput":'sero includas no grupo das emendas de parecer contrrio aquelas sobre as quais se tenham manifestado pela rejeio as comisses competentes para o exame do mrito, embora consideradas constitucionais;'
					},
					"6":{
						"caput":'as emendas com subemendas sero votadas uma a uma, salvo deliberao do Plenrio, mediante proposta de qualquer Senador ou comisso; aprovado o grupo, sero consideradas aprovadas as emendas com modificaes constantes das respectivas subemendas;'
					},
					"7":{
						"caput":'a emenda com subemenda, quando votada separadamente, s-lo- antes e com ressalva desta, exceto nos seguintes casos, em que a subemenda ter precedncia:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'for supressiva;'
									},
									"2":{
										"caput":'for substitutiva de todo o texto da emenda;'
									},
									"3":{
										"caput":'for substitutiva de artigo da emenda e a votao desta se fizer por artigo;'
									},
							}
						}]
					},
					"8":{
						"caput":'o Plenrio poder conceder, a requerimento de qualquer Senador, que a votao das emendas se faa destacadamente, ou uma a uma;'
					},
					"9":{
						"caput":'sero votadas, destacadamente, as emendas com parecer no sentido de constiturem projeto em separado;'
					},
					"10":{
						"caput":'quando, ao mesmo dispositivo, forem apresentadas vrias emendas da mesma natureza, tero preferncia:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'de comisses sobre as de Plenrio;'
									},
									"2":{
										"caput":'as de comisses, a da que tiver maior competncia para se manifestar sobre a matria;'
									},
							}
						}]
					},
					"11":{
						"caput":'o dispositivo, destacado do projeto para votao em separado, preceder, na votao, as emendas e independer de parecer;'
					},
					"12":{
						"caput":'se a votao do projeto se fizer separadamente em relao a cada artigo, o texto deste ser votado antes das emendas a ele correspondentes, salvo se forem supressivas ou substitutivas;'
					},
					"13":{
						"caput":'ter preferncia para votao o substitutivo que tiver pareceres favorveis de todas as comisses, salvo se o Plenrio deliberar noutro sentido;'
					},
					"14":{
						"caput":'havendo mais de um substitutivo, a precedncia ser regulada pela ordem inversa de sua apresentao, ressalvado o disposto no inciso X, em relao aos das comisses;'
					},
					"15":{
						"caput":'o substitutivo integral, salvo deliberao em contrrio, ser votado em globo;'
					},
					"16":{
						"caput":'aprovado o substitutivo integral, ficam prejudicados o projeto e as emendas a ele oferecidas;'
					},
					"17":{
						"caput":'anunciada a votao de dispositivo ou emenda destacada, se o autor do requerimento de destaque no pedir a palavra para encaminh-la, considerarse- como tendo o Plenrio concordado com o parecer da comisso, tomando a matria destacada a sorte das demais constantes do grupo a que pertencer;'
					},
					"18":{
						"caput":'no ser submetida a votos emenda declarada inconstitucional ou injurdica pela Comisso de Constituio, Justia e Cidadania, salvo se, no sendo unnime o parecer, o requererem lderes que representem, no mnimo, a maioria da composio do Senado.'
					},
			}
		}]
	},
	"301":{
		"caput":'A rejeio do projeto prejudica as emendas a ele oferecidas.'
	},
	"302":{
		"caput":'A rejeio do art. 1 do projeto, votado artigo por artigo, prejudica os demais quando eles forem uma consequncia daquele.'
	},
	"303":{
		"caput":'A votao no se interrompe seno por falta de quorum e, observado o disposto nos arts. 178 e 179, pelo trmino da sesso.'
	},
	"304":{
		"caput":'Ocorrendo falta de nmero para as deliberaes, passar-se-  matria em discusso.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Esgotada a matria em discusso e persistindo a falta de nmero, a Presidncia poder, no caso de figurar na Ordem do Dia matria que pela sua relevncia o justifique, suspender a sesso por prazo no superior a uma hora, ou conceder a palavra a Senador que dela queira fazer uso.'
					},
			}
		}]
	},
	"305":{
		"caput":'Sobrevindo, posteriormente, a existncia de nmero, voltar-se-  matria em votao, interrompendo-se o orador que estiver na tribuna, salvo se estiver discutindo proposio em regime de urgncia e a matria a votar estiver em tramitao normal.'
	},
	"306":{
		"caput":'Nenhum Senador presente  sesso poder escusar-se de votar, salvo quando se tratar de assunto em que tenha interesse pessoal, devendo declarar o impedimento antes da votao e sendo a sua presena computada para efeito de quorum.'
	},
	"307":{
		"caput":'Em caso de votao secreta, havendo empate, proceder-se- a nova votao; persistindo o empate, a votao ser renovada na sesso seguinte ou nas subsequentes, at que se d o desempate.'
	},
	"308":{
		"caput":'Anunciada a votao de qualquer matria,  lcito ao Senador usar da palavra por cinco minutos para encaminh-la.'
	},
	"309":{
		"caput":'O encaminhamento  medida preparatria da votao; a votao s se considera iniciada aps o trmino do encaminhamento.'
	},
	"310":{
		"caput":'No tero encaminhamento de votao as eleies e os seguintes requerimentos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de permisso para falar sentado;'
					},
					"2":{
						"caput":'de prorrogao do tempo da sesso;'
					},
					"3":{
						"caput":'de prorrogao de prazo para apresentao de parecer;'
					},
					"4":{
						"caput":'de dispensa de interstcio e prvia publicao de avulso eletrnico para incluso de determinada matria em Ordem do Dia;'
					},
					"5":{
						"caput":'de dispensa de publicao de redao final para sua imediata apreciao;'
					},
					"6":{
						"caput":'de Senador, solicitando de rgo estranho ao Senado a remessa de documentos;'
					},
					"7":{
						"caput":'de comisso ou Senador, solicitando informaes oficiais;'
					},
					"8":{
						"caput":'de comisso ou Senador, solicitando a publicao, no Dirio do Senado Federal, de informaes oficiais;'
					},
					"9":{
						"caput":'de licena de Senador;'
					},
					"10":{
						"caput":'de remessa a determinada comisso de matria despachada a outra;'
					},
					"11":{
						"caput":'de destaque de disposio ou emenda.'
					},
			}
		}]
	},
	"311":{
		"caput":'Conceder-se- preferncia, mediante deliberao do Plenrio:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'de proposio sobre outra ou sobre as demais da Ordem do Dia;'
					},
					"2":{
						"caput":'de emenda ou grupo de emendas sobre as demais oferecidas  mesma proposio ou sobre outras referentes ao mesmo assunto;'
					},
					"3":{
						"caput":'de projeto sobre o substitutivo (art. 300, XIII);'
					},
					"4":{
						"caput":'de substitutivo sobre o projeto (art. 300, XIII).'
					},
			}
		}]
	},
	"312":{
		"caput":'O destaque de partes de qualquer proposio, bem como de emenda do grupo a que pertencer, pode ser concedido, mediante deliberao do Plenrio, a requerimento de qualquer Senador, para:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'constituir projeto autnomo, salvo quando a disposio a destacar seja de projeto da Cmara;'
					},
					"2":{
						"caput":'votao em separado;'
					},
					"3":{
						"caput":'aprovao ou rejeio.'
					},
			}
		}]
	},
	"313":{
		"caput":'Permite-se destacar para votao, como emenda autnoma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'parte de substitutivo, quando a votao se faa preferencialmente sobre o projeto;'
					},
					"2":{
						"caput":'parte de emenda;'
					},
					"3":{
						"caput":'subemenda;'
					},
					"4":{
						"caput":'parte de projeto, quando a votao se fizer preferencialmente sobre o substitutivo.'
					},
			}
		}]
	},
	"314":{
		"caput":'Em relao aos destaques, obedecer-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o requerimento deve ser formulado:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'ser anunciada a proposio, se o destaque atingir algumas de suas partes;'
									},
									"2":{
										"caput":'ser anunciado o grupo das emendas, quando o destaque se referir a qualquer delas;'
									},
									"3":{
										"caput":'ser anunciada a emenda, se o destaque tiver por fim separar algumas de suas partes;'
									},
							}
						}]
					},
					"2":{
						"caput":'no ser permitido destaque de expresso cuja retirada inverta o sentido da proposio ou a modifique substancialmente;'
					},
					"3":{
						"caput":'concedido o destaque para votao em separado, submeter-se- a votos, primeiramente, a matria principal e, em seguida, a destacada;'
					},
					"4":{
						"caput":'a votao de requerimento de destaque s envolve deciso sobre a parte a destacar se a finalidade do destaque for expressamente mencionada;'
					},
					"5":{
						"caput":'havendo retirada do requerimento de destaque, a matria destacada voltar ao grupo a que pertencer;'
					},
					"6":{
						"caput":'no se admitir requerimento de destaque:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'aprovao ou rejeio:'
									},
									"2":{
										"caput":'de dispositivo a que houver sido apresentada emenda;'
									},
									"3":{
										"caput":'de emendas que, regimentalmente, devam ser votadas separadamente;'
									},
									"4":{
										"caput":'emendas para constituio de grupos diferentes daqueles a que, regimentalmente, pertenam;'
									},
							}
						}]
					},
					"7":{
						"caput":'destacada uma emenda, s-lo-o, automaticamente, as que com ela tenham relao;'
					},
					"8":{
						"caput":'o destaque para projeto em separado de dispositivo ou emenda pode, tambm, ser proposto por comisso, em seu parecer;'
					},
					"9":{
						"caput":'a votao do requerimento de destaque para projeto em separado preceder a deliberao sobre a matria principal;'
					},
					"10":{
						"caput":'o destaque para projeto em separado s pode ser submetido a votos se a matria a destacar for suscetvel de constituir proposio de curso autnomo;'
					},
					"11":{
						"caput":'concedido o destaque para projeto em separado, o autor do requerimento ter o prazo de dois dias teis para oferecer o texto com que dever tramitar o novo projeto;'
					},
					"12":{
						"caput":'o projeto resultante de destaque ter a tramitao de proposio inicial.'
					},
			}
		}]
	},
	"315":{
		"caput":'O adiamento da votao obedecer aos mesmos princpios estabelecidos para o adiamento da discusso (art. 279).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento dever ser apresentado e votado como preliminar ao ser anunciada a matria.'
					},
					"2":{
						"caput":'No havendo nmero para a votao, o requerimento ficar sobrestado.'
					},
			}
		}]
	},
	"316":{
		"caput":'Proclamado o resultado da votao,  lcito ao Senador encaminhar  Mesa, para publicao, declarao de voto.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No haver declarao de voto se a deliberao for secreta, no se completar por falta de nmero ou no for suscetvel de encaminhamento.'
					},
			}
		}]
	},
	"317":{
		"caput":'Terminada a votao, com a aprovao de substitutivo, o projeto ir  comisso competente a fim de elaborar a redao para o turno suplementar.'
	},
	"318":{
		"caput":' privativo da comisso especfica para o estudo da matria elaborar a redao para o turno suplementar e a redao final nos casos de:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'reforma do Regimento Interno;'
					},
					"2":{
						"caput":'proposta de emenda  Constituio;'
					},
					"3":{
						"caput":'projeto de cdigo ou sua reforma.'
					},
			}
		}]
	},
	"319":{
		"caput":'Nos projetos da Cmara emendados pelo Senado, a redao final limitar-se- s emendas destacadamente, no as incorporando ao texto da proposio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'A redao final dos projetos de lei da Cmara destinados  sano ser dispensada, salvo se houver vcio de linguagem, defeito ou erro manifesto a corrigir.'
					},
			}
		}]
	},
	"320":{
		"caput":'Lida no Perodo do Expediente, a redao final ficar sobre a mesa para oportuna incluso em Ordem do Dia, aps publicao, no Dirio do Senado Federal e em avulso eletrnico, e interstcio regimental.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando, no decorrer da sesso em que for aprovada a matria, chegar  mesa a redao final respectiva, poder o Plenrio, por proposta do Presidente, permitir se proceda  sua leitura aps o final da Ordem do Dia.'
					},
			}
		}]
	},
	"321":{
		"caput":'A discusso e a votao da redao final podero ser feitas imediatamente aps a leitura, desde que assim o delibere o Senado.'
	},
	"322":{
		"caput":'Quando a redao final for de emendas do Senado a projeto da Cmara, no se admitiro emendas a dispositivo no emendado, salvo as de redao e as que decorram de emendas aprovadas.'
	},
	"323":{
		"caput":'As emendas de redao dependem de parecer da comisso que houver elaborado a redao final, sem prejuzo do disposto no art. 234, pargrafo nico.'
	},
	"324":{
		"caput":'Figurando a redao final na Ordem do Dia, se sua discusso for encerrada sem emendas ou retificaes, ser considerada definitivamente aprovada, sem votao, a no ser que algum Senador requeira seja submetida a votos.'
	},
	"325":{
		"caput":'Verificada a existncia de erro em texto aprovado e com redao definitiva, proceder-se- da seguinte maneira:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'tratando-se de contradio, incoerncia, prejudicialidade ou equvoco que importe em alterao do sentido do projeto, ainda no remetido  sano ou  Cmara, o Presidente encaminhar a matria  comisso competente para que proponha o modo de corrigir o erro, sendo a proposta examinada pela Comisso de Constituio, Justia e Cidadania antes de submetida ao Plenrio;'
					},
					"2":{
						"caput":'nas hipteses do inciso I, quando a matria tenha sido encaminhada  sano ou  Cmara, o Presidente, aps manifestao do Plenrio, comunicar o fato ao Presidente da Repblica ou  Cmara, remetendo novos autgrafos, se for o caso, ou solicitando a retificao do texto, mediante republicao da lei;'
					},
					"3":{
						"caput":'tratando-se de inexatido material, devida a lapso manifesto ou erro grfico, cuja correo no importe em alterao do sentido da matria, o Presidente adotar as medidas especificadas no inciso II, mediante ofcio  Presidncia da Repblica ou  Cmara, dando cincia do fato, posteriormente, ao Plenrio.'
					},
			}
		}]
	},
	"326":{
		"caput":'Quando, em autgrafo recebido da Cmara, for verificada a existncia de inexatido material, lapso ou erro manifesto, no estando ainda a proposio aprovada pelo Senado, ser sustada a sua apreciao para consulta  Casa de origem, cujos esclarecimentos sero dados a conhecer ao Senado, antes da votao, voltando a matria s comisses para novo exame se do vcio houver resultado alterao de sentido do texto.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Quando a comunicao for feita pela Cmara, proceder-se- da seguinte maneira:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'lida no Perodo do Expediente, ser a comunicao encaminhada  comisso em que estiver a matria;'
									},
									"2":{
										"caput":'se a matria j houver sido examinada por outra comisso, a Presidncia providenciar a fim de que a ela volte, para novo exame, antes do parecer do rgo em cujo poder se encontre;'
									},
									"3":{
										"caput":'ao ser a matria submetida ao Plenrio, o Presidente o advertir do ocorrido;'
									},
									"4":{
										"caput":'se a matria j houver sido votada pelo Senado, a Presidncia providenciar para que seja objeto de nova discusso, promovendo, quando necessrio, a substituio dos autgrafos remetidos  Presidncia da Repblica ou  Cmara.'
									},
							}
						}]
					},
			}
		}]
	},
	"327":{
		"caput":'Quando, aps a aprovao definitiva de projeto de lei originrio do Senado, for nele verificada a existncia de matria que deva ser objeto de projeto de decreto legislativo ou de resoluo, a Presidncia providenciar, ouvida a Comisso de Constituio, Justia e Cidadania, o desdobramento da proposio.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Seguir-se- igual orientao quando se trate de projeto de decreto legislativo ou de resoluo que contenha matria de lei.'
					},
			}
		}]
	},
	"328":{
		"caput":'A proposio, aprovada em definitivo pelo Senado, ser encaminhada, em autgrafos,  sano,  promulgao ou  Cmara, conforme o caso.'
	},
	"329":{
		"caput":'Os autgrafos reproduziro a redao final, aprovada pelo Plenrio, ou o texto da Cmara, no emendado.'
	},
	"330":{
		"caput":'O autgrafo procedente da Cmara ficar arquivado no Senado.'
	},
	"331":{
		"caput":'Quando a proposio originria da Cmara for emendada, ser remetida  Casa de origem, juntamente com os autgrafos referidos no art. 329, cpia autenticada do autgrafo procedente daquela Casa, salvo se houver segunda via, caso em que ser devolvida.'
	},
	"332":{
		"caput":'Ao final da legislatura sero arquivadas todas as proposies em tramitao no Senado, exceto:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'as originrias da Cmara ou por ela revisadas;'
					},
					"2":{
						"caput":'as de autoria de Senadores que permaneam no exerccio de mandato ou que tenham sido reeleitos;'
					},
					"3":{
						"caput":'as apresentadas por Senadores no ltimo ano de mandato;'
					},
					"4":{
						"caput":'as com parecer favorvel das comisses;'
					},
					"5":{
						"caput":'as que tratem de matria de competncia exclusiva do Congresso Nacional (Const., art. 49);'
					},
					"6":{
						"caput":'as que tratem de matria de competncia privativa do Senado Federal (Const., art. 52);'
					},
					"7":{
						"caput":'pedido de sustao de processo contra Senador em andamento no Supremo Tribunal Federal (Const., art. 53,  3 e 4, EC n 35/2001).'
					},
			}
		}]
	},
	"333":{
		"caput":'(Revogado).'
	},
	"334":{
		"caput":'O Presidente, de ofcio ou mediante consulta de qualquer Senador, declarar prejudicada matria dependente de deliberao do Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'por haver perdido a oportunidade;'
					},
					"2":{
						"caput":'em virtude de seu prejulgamento pelo Plenrio em outra deliberao.'
					},
			}
		}]
	},
	"335":{
		"caput":'O estudo de qualquer proposio poder ser sobrestado, temporariamente, a requerimento de comisso ou de Senador, para aguardar:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a deciso do Senado ou o estudo de comisso sobre outra proposio com ela conexa;'
					},
					"2":{
						"caput":'o resultado de diligncia;'
					},
					"3":{
						"caput":'o recebimento de outra proposio sobre a mesma matria.'
					},
			}
		}]
	},
	"336":{
		"caput":'A urgncia poder ser requerida:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando se trate de matria que envolva perigo para a segurana nacional ou de providncia para atender a calamidade pblica;'
					},
					"2":{
						"caput":'quando se pretenda a apreciao da matria na segunda sesso deliberativa ordinria subsequente  aprovao do requerimento;'
					},
					"3":{
						"caput":'quando se pretenda incluir em Ordem do Dia matria pendente de parecer.'
					},
			}
		}]
	},
	"337":{
		"caput":'A urgncia dispensa, durante toda a tramitao da matria, interstcios, prazos e formalidades regimentais, salvo pareceres, quorum para deliberao e distribuio de cpias da proposio principal.'
	},
	"338":{
		"caput":'A urgncia pode ser proposta:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, pela Mesa, pela maioria dos membros do Senado ou lderes que representem esse nmero;'
					},
					"2":{
						"caput":'no caso do art. 336, II, por dois teros da composio do Senado ou lderes que representem esse nmero;'
					},
					"3":{
						"caput":'no caso do art. 336, III, por um quarto da composio do Senado ou lderes que representem esse nmero;'
					},
					"4":{
						"caput":'por comisso, nos casos do art. 336, II e III;'
					},
					"5":{
						"caput":'pela Comisso de Assuntos Econmicos, quando se tratar de pedido de autorizao para realizar operaes de crdito previstas nos arts. 28 e 33 da Resoluo n 43, de 2001.'
					},
			}
		}]
	},
	"339":{
		"caput":'O requerimento de urgncia ser lido:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, imediatamente, em qualquer fase da sesso, ainda que com interrupo de discurso, discusso ou votao;'
					},
					"2":{
						"caput":'nos demais casos, no Perodo do Expediente.'
					},
			}
		}]
	},
	"340":{
		"caput":'O requerimento de urgncia ser submetido  deliberao do Plenrio:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente, no caso do art. 336, I;'
					},
					"2":{
						"caput":'aps a Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'na sesso deliberativa seguinte, includo em Ordem do Dia, no caso do art. 336, III.'
					},
			}
		}]
	},
	"341":{
		"caput":'No sero submetidos  deliberao do Plenrio requerimentos de urgncia:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos casos do art. 336, II e III, antes da publicao do avulso eletrnico da proposio respectiva;'
					},
					"2":{
						"caput":'em nmero superior a dois, na mesma sesso, no computados os casos do art. 336, I.'
					},
			}
		}]
	},
	"342":{
		"caput":'No caso do art. 336, II, o requerimento de urgncia ser considerado prejudicado se no houver nmero para a votao.'
	},
	"343":{
		"caput":'No encaminhamento da votao de requerimento de urgncia, podero usar da palavra, pelo prazo de cinco minutos, um dos signatrios e um representante de cada partido ou de bloco parlamentar e, quando se tratar de requerimento de autoria de comisso, o seu Presidente e o relator da matria para a qual foi a urgncia requerida.'
	},
	"344":{
		"caput":'A retirada de requerimento de urgncia, obedecido, no que couber, o disposto no art. 256,  admissvel mediante solicitao escrita:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'do primeiro signatrio, quando no se trate de requerimento de lderes;'
					},
					"2":{
						"caput":'do Presidente da comisso, quando de autoria desta;'
					},
					"3":{
						"caput":'das lideranas que o houverem subscrito.'
					},
			}
		}]
	},
	"345":{
		"caput":'A matria para a qual o Senado conceda urgncia ser submetida ao Plenrio:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente aps a concesso da urgncia, no caso do art. 336, I;'
					},
					"2":{
						"caput":'na segunda sesso deliberativa ordinria que se seguir  concesso da urgncia, includa a matria na Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'na quarta sesso deliberativa ordinria que se seguir  concesso da urgncia, na hiptese do art. 336, III.'
					},
			}
		}]
	},
	"346":{
		"caput":'Os pareceres sobre as proposies em regime de urgncia devem ser apresentados:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'imediatamente, nas hipteses do art. 336, I, podendo o relator solicitar prazo no excedente a duas horas;'
					},
					"2":{
						"caput":'quando a matria for anunciada na Ordem do Dia, no caso do art. 336, II;'
					},
					"3":{
						"caput":'no prazo compreendido entre a concesso da urgncia e o dia anterior ao da sesso em cuja Ordem do Dia deva a matria figurar, quando se tratar de caso previsto no art. 336, III.'
					},
			}
		}]
	},
	"347":{
		"caput":'Na discusso e no encaminhamento de votao das proposies em regime de urgncia no caso do art. 336, I, s podero usar da palavra, e por metade do prazo previsto para as matrias em tramitao normal, o autor da proposio e os relatores, alm de um orador de cada partido.'
	},
	"348":{
		"caput":'Encerrada a discusso de matria em regime de urgncia, com a apresentao de emendas, proceder-se- da seguinte forma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, os pareceres sero proferidos imediatamente, por relator designado pelo Presidente, que poder pedir o prazo previsto no art. 346, I;'
					},
					"2":{
						"caput":'no caso do art. 336, II, os pareceres podero ser proferidos imediatamente, ou, se a complexidade da matria o indicar, no prazo de vinte e quatro horas, saindo, nesta hiptese, a matria da Ordem do Dia, para nela figurar na sesso deliberativa ordinria subsequente;'
					},
					"3":{
						"caput":'no caso do art. 336, III, o projeto sair da Ordem do Dia, para nela ser novamente includo na quarta sesso deliberativa ordinria subsequente, devendo ser proferidos os pareceres sobre as emendas at o dia anterior ao da sesso em que a matria ser apreciada.'
					},
			}
		}]
	},
	"349":{
		"caput":'A realizao de diligncia, nos projetos em regime de urgncia, s  permitida no caso do art. 336, III, e pelo prazo mximo de quatro sesses.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O requerimento pode ser apresentado at ser anunciada a votao.'
					},
			}
		}]
	},
	"350":{
		"caput":'O turno suplementar de matria em regime de urgncia ser realizado imediatamente aps a aprovao, em turno nico, do substitutivo, podendo ser concedido o prazo de vinte e quatro horas para a elaborao da redao para o turno suplementar.'
	},
	"351":{
		"caput":'A redao final de matria em regime de urgncia no depende de publicao e ser submetida  deliberao do Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'no caso do art. 336, I, imediatamente aps a apresentao, ainda que com interrupo de discusso ou votao;'
					},
					"2":{
						"caput":'nos demais casos, a juzo da Presidncia, em qualquer fase da sesso.'
					},
			}
		}]
	},
	"352":{
		"caput":'Extingue-se a urgncia:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'pelo trmino da sesso legislativa;'
					},
					"2":{
						"caput":'nos casos do art. 336, II e III, at ser iniciada a votao da matria, mediante deliberao do Plenrio.'
					},
			}
		}]
	},
	"353":{
		"caput":'So consideradas em regime de urgncia, independentemente de requerimento:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'com a tramitao prevista para o caso do art. 336, I, matria que tenha por fim:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'o Presidente da Repblica a declarar guerra, a celebrar a paz, a permitir que foras estrangeiras transitem pelo territrio nacional ou nele permaneam temporariamente (Const., art. 49, II);'
									},
									"2":{
										"caput":'o estado de defesa e a interveno federal; autorizar o estado de stio ou suspender qualquer dessas medidas (Const., art. 49, IV);'
									},
							}
						}]
					},
					"2":{
						"caput":'com a tramitao prevista para o caso do art. 336, II, a matria que objetive autorizao para o Presidente e o Vice-Presidente da Repblica se ausentarem do Pas (Const., art. 49, III).'
					},
			}
		}]
	},
	"354":{
		"caput":'A proposta de emenda  Constituio apresentada ao Senado ser discutida e votada em dois turnos, considerando-se aprovada se obtiver, em ambos, trs quintos dos votos dos membros da Casa (Const., art. 60,  2);',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'No ser objeto de deliberao a proposta de emenda tendente a abolir (Const., art. 60,  4):',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":'a forma federativa de Estado;'
									},
									"2":{
										"caput":'o voto direto, secreto, universal e peridico;'
									},
									"3":{
										"caput":'a separao dos Poderes;'
									},
									"4":{
										"caput":'os direitos e garantias individuais.'
									},
							}
						}]
					},
					"2":{
						"caput":'A Constituio no poder ser emendada na vigncia de interveno federal, de estado de defesa ou de estado de stio (Const., art. 60,  1).'
					},
			}
		}]
	},
	"355":{
		"caput":'A proposta ser lida no Perodo do Expediente e publicada no Dirio do Senado Federal e em avulso eletrnico, para distribuio aos Senadores.'
	},
	"356":{
		"caput":'A proposta ser despachada  Comisso de Constituio, Justia e Cidadania, que ter prazo de at trinta dias, contado da data do despacho da Presidncia, para emitir parecer.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O parecer da Comisso de Constituio, Justia e Cidadania que concluir pela apresentao de emenda dever conter assinaturas de Senadores que, complementando as dos membros da Comisso, compreendam, no mnimo, um tero dos membros do Senado.'
					},
			}
		}]
	},
	"357":{
		"caput":'Cinco dias aps a publicao do parecer no Dirio do Senado Federal e em avulso eletrnico, a matria poder ser includa em Ordem do Dia.'
	},
	"358":{
		"caput":'Decorrido o prazo de que trata o art. 356 sem que a Comisso de Constituio, Justia e Cidadania haja proferido parecer, a proposta de emenda  Constituio ser includa em Ordem do Dia, para discusso, em primeiro turno, durante cinco sesses deliberativas ordinrias consecutivas.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'O parecer ser proferido oralmente, em plenrio, por relator designado pelo Presidente.'
					},
					"2":{
						"caput":'Durante a discusso podero ser oferecidas emendas assinadas por, no mnimo, um tero dos membros do Senado, desde que guardem relao direta e imediata com a matria tratada na proposta.'
					},
			}
		}]
	},
	"359":{
		"caput":'Para exame e parecer das emendas,  assegurado  Comisso de Constituio, Justia e Cidadania o mesmo prazo estabelecido no art. 356.'
	},
	"360":{
		"caput":'Lido o parecer no Perodo do Expediente, publicado no Dirio do Senado Federal e em avulso eletrnico com a proposta e as emendas, a matria poder ser includa em Ordem do Dia.'
	},
	"361":{
		"caput":'Esgotado o prazo da Comisso de Constituio, Justia e Cidadania, proceder-se- na forma do disposto no caput do art. 358 e em seu  1.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na sesso deliberativa ordinria que se seguir  emisso do parecer, a proposta ser includa em Ordem do Dia para votao em primeiro turno.'
					},
					"2":{
						"caput":'Somente sero admitidos requerimentos que objetivem a votao em separado de partes da proposta ou de emendas.'
					},
					"3":{
						"caput":'A deliberao sobre a proposta, as emendas e as disposies destacadas para votao em separado ser feita pelo processo nominal.'
					},
			}
		}]
	},
	"362":{
		"caput":'O interstcio entre o primeiro e o segundo turno ser de, no mnimo, cinco dias teis.'
	},
	"363":{
		"caput":'Includa a proposta em Ordem do Dia, para o segundo turno, ser aberto o prazo de trs sesses deliberativas ordinrias para discusso, quando podero ser oferecidas emendas que no envolvam o mrito.'
	},
	"364":{
		"caput":'Encerrada a discusso, em segundo turno, com apresentao de emendas, a matria voltar  Comisso, para parecer em cinco dias improrrogveis, aps o que ser includa em Ordem do Dia, em fase de votao.'
	},
	"365":{
		"caput":'Aprovada, sem emendas, a proposta ser remetida  Cmara dos Deputados; emendada, ser encaminhada  Comisso de Constituio, Justia e Cidadania, que ter o prazo de trs dias para oferecer a redao final.'
	},
	"366":{
		"caput":'A redao final, apresentada  Mesa, ser votada, com qualquer nmero, independentemente de publicao.'
	},
	"367":{
		"caput":'Considera-se proposta nova o substitutivo da Cmara a proposta de iniciativa do Senado.'
	},
	"368":{
		"caput":'Na reviso do Senado  proposta da Cmara aplicar-se-o as normas estabelecidas neste Ttulo.'
	},
	"369":{
		"caput":'Quando a aprovao da proposta for ultimada no Senado, ser o fato comunicado  Cmara dos Deputados e convocada sesso para promulgao da emenda (Const., art. 60,  3).'
	},
	"370":{
		"caput":'(Revogado).'
	},
	"371":{
		"caput":' vedada a apresentao de proposta que objetive alterar dispositivos sem correlao direta entre si.'
	},
	"372":{
		"caput":'Aplicam-se  tramitao da proposta, no que couber, as normas estabelecidas neste Regimento para as demais proposies.'
	},
	"373":{
		"caput":'A matria constante de proposta de emenda  Constituio rejeitada ou havida por prejudicada no pode ser objeto de nova proposta na mesma sesso legislativa (Const., art. 60,  5).'
	},
	"374":{
		"caput":'Na sesso em que for lido o projeto de cdigo, a Presidncia designar uma comisso temporria para seu estudo, composta de onze membros, e fixar o calendrio de sua tramitao, obedecidos os seguintes prazos e normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a comisso se reunir at o dia til seguinte  sua constituio, para eleger o Presidente e o Vice-Presidente, sendo, em seguida, designados um relator geral e tantos relatores parciais quantos necessrios;'
					},
					"2":{
						"caput":'ao projeto sero anexadas as proposies em curso ou as sobrestadas, que envolvam matria com ele relacionada;'
					},
					"3":{
						"caput":'perante a comisso, podero ser oferecidas emendas, no prazo de vinte dias teis, a contar da publicao do projeto no Dirio do Senado Federal;'
					},
					"4":{
						"caput":'encerrado o prazo para a apresentao de emendas, os relatores parciais encaminharo, dentro de dez dias teis, ao relator geral, as concluses de seus trabalhos;'
					},
					"5":{
						"caput":'o relator geral ter o prazo de cinco dias teis para apresentar,  comisso, o parecer que ser publicado em avulso eletrnico, juntamente com o estudo dos relatores parciais e as emendas;'
					},
					"6":{
						"caput":'a comisso ter cinco dias teis para concluir o seu estudo e encaminhar  Mesa o parecer final sobre o projeto e as emendas;'
					},
					"7":{
						"caput":'na comisso, a discusso da matria obedecer  diviso adotada para a designao dos relatores parciais, podendo cada membro usar da palavra uma vez, por dez minutos, o relator parcial, duas vezes, por igual prazo, e o relator geral, duas vezes, pelo prazo de quinze minutos;'
					},
					"8":{
						"caput":'as emendas e subemendas sero votadas, sem encaminhamento, em grupos, segundo o sentido dos pareceres, ressalvados os destaques requeridos pelo autor, com apoiamento de, pelo menos, cinco membros da comisso ou por lder;'
					},
					"9":{
						"caput":'publicado o parecer da comisso e publicado o avulso eletrnico, ser o projeto includo, com exclusividade, em Ordem do Dia, obedecido o interstcio regimental;'
					},
					"10":{
						"caput":'a discusso, em plenrio, far-se- sobre o projeto e as emendas, em um nico turno, podendo o relator geral usar da palavra sempre que for necessrio, ou deleg-la ao relator parcial;'
					},
					"11":{
						"caput":'a discusso poder ser encerrada mediante autorizao do Plenrio, a requerimento de lder, depois de debatida a matria em trs sesses deliberativas consecutivas;'
					},
					"12":{
						"caput":'encerrada a discusso, passar-se-  votao, sendo que os destaques s podero ser requeridos por lder, pelo relator geral ou por vinte Senadores;'
					},
					"13":{
						"caput":'aprovado com ou sem emendas, o projeto voltar  comisso para a redao final, que dever ser apresentada no prazo de cinco dias teis;'
					},
					"14":{
						"caput":'publicada em avulso eletrnico, a redao final ser includa em Ordem do Dia, obedecido o interstcio regimental;'
					},
					"15":{
						"caput":'no se far tramitao simultnea de projetos de cdigo;'
					},
					"16":{
						"caput":'os prazos previstos neste artigo podero ser aumentados at o qudruplo, por deliberao do Plenrio, a requerimento da comisso.'
					},
			}
		}]
	},
	"375":{
		"caput":'Nos projetos de lei de iniciativa do Presidente da Repblica, quando sujeitos  tramitao urgente (Const., art. 64,  1) e nos casos de apreciao de atos de outorga ou renovao de concesso, permisso ou autorizao para servio de radiodifuso sonora e de sons e imagens (Const., art. 223,  1), proceder-se- da seguinte forma:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'o projeto ser lido no Perodo do Expediente e distribudo s comisses competentes, somente podendo receber emendas na primeira comisso constante do despacho, pelo prazo de cinco dias;'
					},
					"2":{
						"caput":'o projeto ser apreciado, simultaneamente, pelas comisses, sendo feitas tantas autuaes quantas forem necessrias;'
					},
					"3":{
						"caput":'as comisses devero apresentar os pareceres at o vigsimo quinto dia contado do recebimento do projeto no Senado;'
					},
					"4":{
						"caput":'publicado o parecer em avulso eletrnico, decorrido o interstcio regimental, o projeto ser includo em Ordem do Dia;'
					},
					"5":{
						"caput":'no sendo emitidos os pareceres no prazo fixado no inciso III, aplicar-se  o disposto no art. 172, II, d;'
					},
					"6":{
						"caput":'o adiamento de discusso ou de votao no poder ser aceito por prazo superior a vinte e quatro horas;'
					},
					"7":{
						"caput":'a redao final das emendas dever ser apresentada em plenrio no prazo mximo de quarenta e oito horas aps a votao da matria;'
					},
					"8":{
						"caput":'esgotado o prazo de quarenta e cinco dias contado do recebimento do projeto sem que se tenha concluda a votao, dever ele ser includo em Ordem do Dia, sobrestando-se a deliberao sobre as demais matrias, at que se ultime a sua votao (Const., art. 64,  2).'
					},
			}
		}]
	},
	"376":{
		"caput":'O projeto de decreto legislativo referente a atos internacionais ter a seguinte tramitao:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'s ter iniciado o seu curso se estiver acompanhado de cpia autenticada do texto, em portugus, do ato internacional respectivo, bem como da mensagem de encaminhamento e da exposio de motivos;'
					},
					"2":{
						"caput":'lido no Perodo do Expediente, ser o projeto publicado e distribudo em avulso eletrnico, acompanhado dos textos referidos no inciso I e despachado  Comisso de Relaes Exteriores e Defesa Nacional;'
					},
					"3":{
						"caput":'perante a Comisso, nos cinco dias teis subsequentes  publicao de avulso eletrnico, podero ser oferecidas emendas; a Comisso ter, para opinar sobre o projeto, e emendas, o prazo de quinze dias teis, prorrogvel por igual perodo;'
					},
					"4":{
						"caput":'publicados o parecer e as emendas e o avulso eletrnico, decorrido o interstcio regimental, a matria ser includa em Ordem do Dia;'
					},
					"5":{
						"caput":'no sendo emitido o parecer, conforme estabelece o inciso III, aplicar-se- o disposto no art. 172, II, c.'
					},
			}
		}]
	},
	"377":{
		"caput":'Compete privativamente ao Senado Federal (Const., art. 52, I e II):',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'processar e julgar o Presidente e o Vice-Presidente da Repblica, nos crimes de responsabilidade, bem como os Ministros de Estado e os Comandantes da Marinha, do Exrcito e da Aeronutica nos crimes da mesma natureza conexos com aqueles;'
					},
					"2":{
						"caput":'processar e julgar os Ministros do Supremo Tribunal Federal, os membros do Conselho Nacional de Justia e do Conselho Nacional do Ministrio Pblico, o Procurador-Geral da Repblica e o Advogado-Geral da Unio, nos crimes de responsabilidade.'
					},
			}
		}]
	},
	"378":{
		"caput":'Em qualquer hiptese, a sentena condenatria s poder ser proferida pelo voto de dois teros dos membros do Senado, e a condenao limitar-se-  perda do cargo, com inabilitao, por oito anos, para o exerccio de funo pblica, sem prejuzo das sanes judiciais cabveis (Const., art. 52, pargrafo nico).'
	},
	"379":{
		"caput":'Em todos os trmites do processo e julgamento sero observadas as normas prescritas na lei reguladora da espcie.'
	},
	"380":{
		"caput":'Para julgamento dos crimes de responsabilidade das autoridades indicadas no art. 377, obedecer-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'recebida pela Mesa do Senado a autorizao da Cmara para instaurao do processo, nos casos previstos no art. 377, I, ou a denncia do crime, nos demais casos, ser o documento lido no Perodo do Expediente da sesso seguinte;'
					},
					"2":{
						"caput":'na mesma sesso em que se fizer a leitura, ser eleita comisso, constituda por um quarto da composio do Senado, obedecida a proporcionalidade das representaes partidrias ou dos blocos parlamentares, e que ficar responsvel pelo processo;'
					},
					"3":{
						"caput":'a comisso encerrar seu trabalho com o fornecimento do libelo acusatrio, que ser anexado ao processo e entregue ao Presidente do Senado Federal, para remessa, em original, ao Presidente do Supremo Tribunal Federal, com a comunicao do dia designado para o julgamento;'
					},
					"4":{
						"caput":'o Primeiro Secretrio enviar ao acusado cpia autenticada de todas as peas do processo, inclusive do libelo, intimando-o do dia e hora em que dever comparecer ao Senado para o julgamento;'
					},
					"5":{
						"caput":'estando o acusado ausente do Distrito Federal, a sua intimao ser solicitada pelo Presidente do Senado ao Presidente do Tribunal de Justia do Estado em que ele se encontre;'
					},
					"6":{
						"caput":'servir de escrivo um funcionrio da Secretaria do Senado designado pelo Presidente do Senado.'
					},
			}
		}]
	},
	"381":{
		"caput":'Instaurado o processo, o Presidente da Repblica ficar suspenso de suas funes (Const., art. 86,  1, II).',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Se, decorrido o prazo de cento e oitenta dias, o julgamento no estiver concludo, cessar o afastamento do Presidente da Repblica, sem prejuzo do regular prosseguimento do processo (Const., art. 86,  2).'
					},
			}
		}]
	},
	"382":{
		"caput":'No processo e julgamento a que se referem os arts. 377 a 381 aplicar-se , no que couber, o disposto na Lei n 1.079, de 10 de abril de 1950.'
	},
	"383":{
		"caput":'Na apreciao do Senado Federal sobre a escolha de autoridades, observar-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a mensagem, que ser lida em plenrio e encaminhada  comisso competente, dever estar acompanhada de amplos esclarecimentos sobre o candidato e de:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'vitae, no qual constem:',
										"enumeravel": [{
											"tipo":"numero",
											"lista":{
													"1":{
														"caput":'atividades profissionais exercidas pelo indicado, com a discriminao dos referidos perodos;'
													},
													"2":{
														"caput":'relao das publicaes de sua autoria, com as referncias bibliogrficas que permitam sua recuperao;'
													},
													"3":{
														"caput":'caso dos indicados na forma do inciso III do art. 52 da Constituio Federal, declarao do indicado:'
													},
													"4":{
														"caput":' existncia de parentes seus que exercem ou exerceram atividades, pblicas ou privadas, vinculadas a sua atividade profissional, com a discriminao dos referidos perodos;'
													},
													"5":{
														"caput":' sua participao, em qualquer tempo, como scio, proprietrio ou gerente, de empresas ou entidades no governamentais, com a discriminao dos referidos perodos;'
													},
													"6":{
														"caput":'regularidade fiscal, nos mbitos federal, estadual e municipal;'
													},
													"7":{
														"caput":' existncia de aes judiciais nas quais figure como autor ou ru, com indicao atualizada da tramitao processual;'
													},
													"8":{
														"caput":' sua atuao, nos ltimos 5 (cinco) anos, contados retroativamente ao ano em que se deu sua indicao, em juzos e tribunais, em conselhos de administrao de empresas estatais ou em cargos de direo de agncias reguladoras;'
													},
													"9":{
														"caput":'escrita, apresentada de forma sucinta, em que o indicado demonstre ter experincia profissional, formao tcnica adequada e afinidade intelectual e moral para o exerccio da atividade;'
													},
													"10":{
														"caput":'caso dos indicados na forma do inciso IV do art. 52 da Constituio Federal, relatrios produzidos pelo Ministrio das Relaes Exteriores contendo:'
													},
													"11":{
														"caput":'sobre o Estado ou organizao internacional para o qual o candidato foi indicado;'
													},
													"12":{
														"caput":'dos tratados e acordos assinados com o respectivo Estado ou organizao internacional, bem como dos contratos de emprstimos e financiamentos oficiais concedidos pelo Brasil, incluindo os atos referentes a perdo ou renegociao de dvidas e a renncia fiscal, diferenciando entre atos em vigor e atos ainda sujeitos  aprovao do Congresso Nacional, nos termos do art. 49, inciso I, da Constituio Federal;',
														"enumeravel": [{
															"tipo":"inciso",
															"lista":{
																	"1":{
																		"caput":'o exame das indicaes feitas na forma do inciso III do art. 52 da Constituio Federal seguir as seguintes etapas:',
																		"enumeravel": [{
																			"tipo":"letra",
																			"lista":{
																					"1":{
																						"caput":'relator apresentar o relatrio  comisso, com recomendaes, se for o caso, para que sejam apresentadas informaes adicionais;'
																					},
																					"2":{
																						"caput":'concedida, automaticamente, vista coletiva aos membros da comisso e divulgado o relatrio por meio do portal do Senado Federal;'
																					},
																					"3":{
																						"caput":'portal do Senado Federal possibilitar  sociedade encaminhar informaes sobre o indicado ou perguntas a ele dirigidas, que sero submetidas ao exame do relator com vistas ao seu aproveitamento, inclusive quanto  necessidade de realizao de audincia pblica em face das informaes e indagaes recebidas;'
																					},
																					"4":{
																						"caput":'relator poder discutir com os membros da comisso o contedo das questes que sero formuladas ao indicado;'
																					},
																					"5":{
																						"caput":'comisso convocar o candidato para, em prazo estipulado, no inferior a 5 (cinco) dias teis, ouvi-lo, em arguio pblica, sobre assuntos pertinentes ao desempenho do cargo a ser ocupado (Const., art. 52, III);'
																					},
																					"6":{
																						"caput":'inquirio de candidato, cada Senador interpelante dispor de 10 (dez) minutos, assegurado igual prazo para resposta, imediata, do interpelado, facultadas rplica e trplica, ambas tambm imediatas, por 5 (cinco) minutos;'
																					},
																					"7":{
																						"caput":'relatrio ser votado;'
																					},
																			}
																		}]
																	},
																	"2":{
																		"caput":'a arguio de candidato a chefe de misso diplomtica de carter permanente ser feita em reunio secreta (Const., art. 52, IV), aplicando-se o procedimento descrito no inciso II deste artigo, no que couber;'
																	},
																	"3":{
																		"caput":'alm da arguio do candidato e do disposto no art. 93, a comisso poder realizar investigaes e requisitar, da autoridade competente, informaes complementares;'
																	},
																	"4":{
																		"caput":'o relatrio dever conter dados sobre o candidato, passando a constituir parecer com o resultado da votao, aprovando ou rejeitando o nome indicado;'
																	},
																	"5":{
																		"caput":'a reunio ser pblica, sendo a votao procedida por escrutnio secreto, vedadas declarao ou justificao de voto, exceto com referncia ao aspecto legal;'
																	},
																	"6":{
																		"caput":'o parecer ser apreciado pelo Plenrio em sesso pblica, sendo a votao procedida por escrutnio secreto;'
																	},
																	"7":{
																		"caput":'a manifestao do Senado ser comunicada ao Presidente da Repblica, consignando-se o resultado da votao.'
																	},
															}
														}]
													},
													"13":{
														"caput":'A manifestao do Senado Federal e das comisses sobre a escolha de chefe de misso diplomtica de carter permanente ser procedida em sesso e reunio secretas (Const., art. 52, IV).'
													},
													"14":{
														"caput":'A resposta negativa s hipteses previstas nos itens 1, 2, 4 e 5 da alnea b do inciso I deste artigo dever ser declarada por escrito.'
													},
													"15":{
														"caput":'A declarao de que trata o item 3 da alnea b do inciso I deste artigo dever ser acompanhada de documentao comprobatria emitida pelos rgos competentes.'
													},
											}
										}]
									},
							}
						}]
					},
			}
		}]
	},
	"384":{
		"caput":'A eleio dos membros do Conselho da Repblica ser feita mediante lista sxtupla elaborada pela Mesa, ouvidas as lideranas com atuao no Senado.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Proceder-se-  eleio por meio de cdulas uninominais, considerando-se eleito o indicado que obtiver a maioria de votos, presente a maioria absoluta dos membros do Senado.'
					},
					"2":{
						"caput":'Eleito o primeiro representante do Senado, proceder-se-  eleio do segundo, dentre os cinco indicados restantes, obedecido o mesmo critrio previsto no  1.'
					},
					"3":{
						"caput":'Se, na primeira apurao, nenhum dos indicados alcanar maioria de votos, proceder-se- a nova votao, e, se mesmo nesta, aquele quorum no for alcanado, a eleio ficar adiada para outra sesso, a ser convocada pela Presidncia e, assim, sucessivamente.'
					},
					"4":{
						"caput":'No processamento da eleio, aplicar-se-o, no que couber, as normas regimentais que dispuserem sobre escolha de autoridades.'
					},
					"5":{
						"caput":' eleio dos suplentes, previstos na Lei n 8.041, de 5 de junho de 1990, aplica-se o disposto neste artigo.'
					},
			}
		}]
	},
	"385":{
		"caput":'A mensagem do Presidente da Repblica solicitando autorizao para destituir o Procurador-Geral da Repblica, uma vez lida em plenrio, ser distribuda, para apresentao de parecer,  Comisso de Constituio, Justia e Cidadania.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Aplicar-se- na tramitao da mensagem, no que couber, o disposto para escolha de autoridades, sendo que a destituio somente se efetivar se aprovada pela maioria absoluta de votos.'
					},
			}
		}]
	},
	"386":{
		"caput":'O Senado conhecer da declarao, proferida em deciso definitiva pelo Supremo Tribunal Federal, de inconstitucionalidade total ou parcial de lei mediante:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'comunicao do Presidente do Tribunal;'
					},
					"2":{
						"caput":'representao do Procurador-Geral da Repblica;'
					},
					"3":{
						"caput":'projeto de resoluo de iniciativa da Comisso de Constituio, Justia e Cidadania.'
					},
			}
		}]
	},
	"387":{
		"caput":'A comunicao, a representao e o projeto a que se refere o art. 386 devero ser instrudos com o texto da lei cuja execuo se deva suspender, do acrdo do Supremo Tribunal Federal, do parecer do Procurador-Geral da Repblica e da verso do registro taquigrfico do julgamento.'
	},
	"388":{
		"caput":'Lida em plenrio, a comunicao ou representao ser encaminhada  Comisso de Constituio, Justia e Cidadania, que formular projeto de resoluo suspendendo a execuo da lei, no todo ou em parte.'
	},
	"389":{
		"caput":'O Senado apreciar pedido de autorizao para operaes externas, de natureza financeira, de interesse da Unio, dos Estados, do Distrito Federal, dos Territrios e dos Municpios (Const., art. 52, V), instrudo com:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'documentos que o habilitem a conhecer, perfeitamente, a operao, os recursos para satisfazer os compromissos e a sua finalidade;'
					},
					"2":{
						"caput":'publicao oficial com o texto da autorizao do Legislativo competente;'
					},
					"3":{
						"caput":'parecer do rgo competente do Poder Executivo.'
					},
			}
		}]
	},
	"390":{
		"caput":'Na tramitao da matria de que trata o art. 389, obedecer-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'lida no Perodo do Expediente, a matria ser encaminhada  Comisso de Assuntos Econmicos, a fim de ser formulado o respectivo projeto de resoluo, concedendo ou negando a medida pleiteada;'
					},
					"2":{
						"caput":'a resoluo, uma vez promulgada, ser enviada, em todo o seu teor,  entidade interessada e ao rgo a que se refere o art. 389, III, devendo constar do instrumento da operao.'
					},
			}
		}]
	},
	"391":{
		"caput":'Qualquer modificao nos compromissos originariamente assumidos depender de nova autorizao do Senado.'
	},
	"392":{
		"caput":'O disposto nos arts. 389 a 391 aplicar-se-, tambm, aos casos de aval da Unio, Estado, Distrito Federal ou Municpio, para a contratao de emprstimo externo por entidade autrquica subordinada ao Governo Federal, Estadual ou Municipal.'
	},
	"393":{
		"caput":'Compete ao Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fixar limites globais para o montante da dvida consolidada da Unio, dos Estados, do Distrito Federal e dos Municpios (Const., art. 52, VI);'
					},
					"2":{
						"caput":'dispor sobre limites globais e condies para as operaes de crdito externo e interno da Unio, dos Estados, do Distrito Federal e dos Municpios, de suas autarquias e demais entidades controladas pelo poder pblico federal (Const., art. 52, VII);'
					},
					"3":{
						"caput":'dispor sobre limites e condies para a concesso de garantia da Unio em operaes de crdito externo e interno (Const., art. 52, VIII);'
					},
					"4":{
						"caput":'estabelecer limites globais e condies para o montante da dvida mobiliria dos Estados, do Distrito Federal e dos Municpios (Const., art. 52, IX).'
					},
			}
		}]
	},
	"393-A":{
		"caput":'A avaliao de que trata o art. 99-A ser realizada anualmente por grupo de Senadores da Comisso de Assuntos Econmicos designados pelo Presidente da Comisso.'
	},
	"393-B":{
		"caput":'Para atender aos objetivos da avaliao prevista no art. 52, XV, da Constituio Federal, o Senado poder solicitar informaes e documentos  Unio, aos Estados, ao Distrito Federal e aos Municpios, compreendidos os trs Poderes e os rgos e entidades da administrao direta e indireta, alm do Conselho Nacional de Poltica Fazendria (Confaz) e de outras instituies da sociedade organizada.'
	},
	"393-C":{
		"caput":'Sero observados os seguintes prazos nos trabalhos de avaliao peridica do Sistema Tributrio Nacional:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'para recebimento de documentos e informaes, at 15 de maro;'
					},
					"2":{
						"caput":'para realizao de audincias pblicas, at 30 de abril;'
					},
					"3":{
						"caput":'para apresentao do relatrio final, at 30 de junho.'
					},
			}
		}]
	},
	"393-D":{
		"caput":'A funcionalidade do Sistema Tributrio Nacional ser avaliada considerando-se, entre outros, os seguintes aspectos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'complexidade e qualidade da legislao;'
					},
					"2":{
						"caput":'custos de conformidade  normatizao tributria;'
					},
					"3":{
						"caput":'qualidade dos tributos, especialmente quanto:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'justia fiscal;'
									},
									"2":{
										"caput":'atendimento aos princpios constitucionais tributrios;'
									},
									"3":{
										"caput":'atendimento s necessidades oramentrias;'
									},
									"4":{
										"caput":'custo das obrigaes acessrias;'
									},
							}
						}]
					},
					"4":{
						"caput":'carga tributria;'
					},
					"5":{
						"caput":'equilbrio federativo, especialmente quanto:',
						"enumeravel": [{
							"tipo":"letra",
							"lista":{
									"1":{
										"caput":'participao da Unio, dos Estados, do Distrito Federal e dos Municpios no total da receita tributria, antes e depois das transferncias constitucionais e legais;'
									},
									"2":{
										"caput":'participao das transferncias constitucionais e legais na receita tributria dos entes federados;'
									},
							}
						}]
					},
					"6":{
						"caput":'renncias fiscais;'
					},
					"7":{
						"caput":'harmonizao normativa;'
					},
					"8":{
						"caput":'reduo das desigualdades regionais;'
					},
					"9":{
						"caput":'compatibilidade com a legislao de outros pases ou blocos econmicos.'
					},
			}
		}]
	},
	"393-E":{
		"caput":'O desempenho das administraes tributrias da Unio, dos Estados, do Distrito Federal e dos Municpios ser avaliado considerando-se, entre outros, os seguintes aspectos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'relao entre o custo da administrao e o montante arrecadado;'
					},
					"2":{
						"caput":'exerccio efetivo das competncias tributrias pelos entes federados;'
					},
					"3":{
						"caput":'desempenho da fiscalizao;'
					},
					"4":{
						"caput":'relao entre pagamento espontneo e coercitivo dos tributos;'
					},
					"5":{
						"caput":'desempenho da cobrana judicial e extrajudicial da dvida ativa tributria;'
					},
					"6":{
						"caput":'efetividade dos programas de recuperao fiscal, especialmente quanto a parcelamento, anistia e remisso;'
					},
					"7":{
						"caput":'grau de integrao das administraes tributrias;'
					},
					"8":{
						"caput":'gastos e resultados com educao fiscal;'
					},
					"9":{
						"caput":'qualidade do atendimento ao contribuinte;'
					},
					"10":{
						"caput":'grau de informalidade da economia.'
					},
			}
		}]
	},
	"393-F":{
		"caput":'O grupo de Senadores de que trata o art. 393-A elaborar relatrio conclusivo, que ser submetido  deliberao do Plenrio da Comisso de Assuntos Econmicos, em carter terminativo.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Cpia integral do relatrio aprovado ser enviada ao Presidente da Repblica,  Cmara dos Deputados, aos Governadores dos Estados e do Distrito Federal, s Assembleias Legislativas Estaduais,  Cmara Legislativa do Distrito Federal e aos Tribunais de Contas.'
					},
					"2":{
						"caput":'Resumo executivo com as principais concluses ser enviado aos Municpios.'
					},
			}
		}]
	},
	"394":{
		"caput":'Ao Senado Federal, no que se refere  competncia tributria dos Estados e do Distrito Federal, compete:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'fixar alquotas mximas do imposto sobre transmisso causa mortis e doao de quaisquer bens ou direitos (Const., art. 155,  1, IV);'
					},
					"2":{
						"caput":'estabelecer as alquotas aplicveis s operaes e prestaes interestaduais e de exportao (Const., art. 155,  2, IV);'
					},
					"3":{
						"caput":'estabelecer alquotas mnimas nas operaes internas (Const., art. 155,  2, V, a);'
					},
					"4":{
						"caput":'fixar alquotas mximas nas operaes internas para resolver conflito especfico que envolva interesse de Estados e do Distrito Federal (Const., art. 155,  2, V, b).'
					},
			}
		}]
	},
	"395":{
		"caput":'As matrias a que se referem os arts. 393 e 394 tero a tramitao regimental prevista para os demais projetos de resoluo.'
	},
	"396":{
		"caput":'O Senado Federal remeter o texto da resoluo a que se referem os arts. 389 a 394 ao Presidente da Repblica, aos Governadores, s Assembleias Legislativas,  Cmara Legislativa do Distrito Federal e aos Prefeitos e Cmaras de Vereadores dos Municpios interessados, com a indicao da sua publicao no Dirio do Senado Federal e no Dirio Oficial da Unio.'
	},
	"397":{
		"caput":'O Ministro de Estado comparecer perante o Senado:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'quando convocado, por deliberao do Plenrio, mediante requerimento de qualquer Senador ou comisso, para prestar, pessoalmente, informaes sobre assunto previamente determinado (Const., art. 50, caput);'
					},
					"2":{
						"caput":'quando o solicitar, mediante entendimento com a Mesa, para expor assunto de relevncia de seu Ministrio (Const., art. 50,  1).'
					},
			}
		}]
	},
	"398":{
		"caput":'Quando houver comparecimento de Ministro de Estado perante o Senado, adotar-se-o as seguintes normas:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'nos casos do inciso I do art. 397, a Presidncia oficiar ao Ministro de Estado, dando-lhe conhecimento da convocao e da lista das informaes desejadas, a fim de que declare quando comparecer ao Senado, no prazo que lhe estipular, no superior a trinta dias;'
					},
					"2":{
						"caput":'nos casos do inciso II do art. 397, a Presidncia comunicar ao Plenrio o dia e a hora que marcar para o comparecimento;'
					},
					"3":{
						"caput":'no plenrio, o Ministro de Estado ocupar o lugar que a Presidncia lhe indicar;'
					},
					"4":{
						"caput":'ser assegurado o uso da palavra ao Ministro de Estado na oportunidade combinada, sem embargo das inscries existentes;'
					},
					"5":{
						"caput":'a sesso em que comparecer o Ministro de Estado ser destinada exclusivamente ao cumprimento dessa finalidade;'
					},
					"6":{
						"caput":'se, entretanto, o Ministro desejar falar ao Senado no mesmo dia em que o solicitar, ser-lhe- assegurada a oportunidade aps as deliberaes da Ordem do Dia;'
					},
					"7":{
						"caput":'se o tempo normal da sesso no permitir que se conclua a exposio do Ministro de Estado, com a correspondente fase de interpelaes, ser ela prorrogada ou se designar outra sesso para esse fim;'
					},
					"8":{
						"caput":'o Ministro de Estado ficar subordinado s normas estabelecidas para o uso da palavra pelos Senadores;'
					},
					"9":{
						"caput":'o Ministro de Estado s poder ser aparteado na fase das interpelaes desde que o permita;'
					},
					"10":{
						"caput":'terminada a exposio do Ministro de Estado, que ter a durao de meia hora, abrir-se- a fase de interpelao, pelos Senadores inscritos, dentro do assunto tratado, dispondo o interpelante de cinco minutos, assegurado igual prazo para a resposta do interpelado, aps o que poder este ser contraditado pelo prazo mximo de dois minutos, concedendo-se ao Ministro de Estado o mesmo tempo para a trplica;'
					},
					"11":{
						"caput":'a palavra aos Senadores ser concedida na ordem de inscrio, intercalando-se oradores de cada partido;'
					},
					"12":{
						"caput":'ao Ministro de Estado  lcito fazer-se acompanhar de assessores, aos quais a Presidncia designar lugares prximos ao que ele deva ocupar, no lhes sendo permitido interferir nos debates.'
					},
			}
		}]
	},
	"399":{
		"caput":'Na hiptese de no ser atendida convocao feita de acordo com o disposto no art. 397, I, o Presidente do Senado promover a instaurao do procedimento legal cabvel ao caso.'
	},
	"400":{
		"caput":'O disposto nos arts. 397 a 399 aplica-se, quando possvel, aos casos de comparecimento de Ministro a reunio de comisso.'
	},
	"400-A":{
		"caput":'Aplica-se o disposto neste Ttulo, no que couber, ao comparecimento ao Senado de titulares de rgos diretamente subordinados  Presidncia da Repblica (Const., art. 50).'
	},
	"401":{
		"caput":'O Regimento Interno poder ser modificado ou reformado por projeto de resoluo de iniciativa de qualquer Senador, da Comisso Diretora ou de comisso temporria para esse fim criada, em virtude de deliberao do Senado, e da qual dever fazer parte um membro da Comisso Diretora.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Em qualquer caso, o projeto, aps publicado em avulso eletrnico, ficar sobre a mesa durante cinco dias teis a fim de receber emendas.'
					},
					"2":{
						"caput":'Decorrido o prazo previsto no  1, o projeto ser enviado:',
						"enumeravel": [{
							"tipo":"inciso",
							"lista":{
									"1":{
										"caput":' Comisso de Constituio, Justia e Cidadania, em qualquer caso;'
									},
									"2":{
										"caput":' comisso que o houver elaborado, para exame das emendas, se as houver recebido;'
									},
									"3":{
										"caput":' Comisso Diretora, se de autoria individual de Senador.'
									},
							}
						}]
					},
					"3":{
						"caput":'Os pareceres das comisses sero emitidos no prazo de dez dias teis, quando o projeto for de simples modificao, e no de vinte dias teis, quando se tratar de reforma.'
					},
					"4":{
						"caput":'Aplicam-se  tramitao do projeto de alterao ou reforma do Regimento as normas estabelecidas para os demais projetos de resoluo.'
					},
					"5":{
						"caput":'A redao final do projeto de reforma do Regimento Interno compete  comisso que o houver elaborado e o de autoria individual de Senador,  Comisso Diretora.'
					},
			}
		}]
	},
	"402":{
		"caput":'A Mesa far, ao fim de cada legislatura, consolidao das modificaes feitas no Regimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Na consolidao, a Mesa poder, sem modificao de mrito, alterar a ordenao das matrias e fazer as correes de redao que se tornarem necessrias.'
					},
			}
		}]
	},
	"403":{
		"caput":'Constituir questo de ordem, suscitvel em qualquer fase da sesso, pelo prazo de cinco minutos, qualquer dvida sobre interpretao ou aplicao deste Regimento.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Para contraditar questo de ordem  permitido o uso da palavra a um s Senador, por prazo no excedente ao fixado neste artigo.'
					},
			}
		}]
	},
	"404":{
		"caput":'A questo de ordem deve ser objetiva, indicar o dispositivo regimental em que se baseia, referir-se a caso concreto relacionado com a matria tratada na ocasio, no podendo versar sobre tese de natureza doutrinria ou especulativa.'
	},
	"405":{
		"caput":'A questo de ordem ser decidida pelo Presidente, com recurso para o Plenrio, de ofcio ou mediante requerimento, que s ser aceito se formulado ou apoiado por lder.'
	},
	"406":{
		"caput":'Considera-se simples precedente a deciso sobre questo de ordem, s adquirindo fora obrigatria quando incorporada ao Regimento.'
	},
	"407":{
		"caput":'Nenhum Senador poder falar, na mesma sesso, sobre questo de ordem j resolvida pela Presidncia.'
	},
	"408":{
		"caput":'Havendo recurso para o Plenrio, sobre deciso da Presidncia em questo de ordem,  lcito a esta solicitar a audincia da Comisso de Constituio, Justia e Cidadania sobre a matria, quando se tratar de interpretao de texto constitucional.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Solicitada a audincia, fica sobrestada a deciso.'
					},
					"2":{
						"caput":'O parecer da Comisso dever ser proferido no prazo de dois dias teis, aps o que, com ou sem parecer, ser o recurso includo em Ordem do Dia para deliberao do Plenrio.'
					},
					"3":{
						"caput":'Quando se tratar de questo de ordem sobre matria em regime de urgncia nos termos do art. 336, I, ou com prazo de tramitao, o parecer dever ser proferido imediatamente, podendo o Presidente da comisso ou o relator solicitar prazo no excedente a duas horas.'
					},
			}
		}]
	},
	"409":{
		"caput":'As peties, memoriais, representaes ou outros documentos enviados ao Senado sero recebidos pelo Servio de Protocolo e, segundo a sua natureza, despachados s comisses competentes ou arquivados, depois de lidos em plenrio, quando o merecerem, a juzo da Presidncia.'
	},
	"410":{
		"caput":'No sero recebidas peties e representaes sem data e assinaturas ou em termos desrespeitosos, podendo as assinaturas, a juzo da Presidncia, ser reconhecidas.'
	},
	"411":{
		"caput":'O Senado no encaminhar  Cmara ou a outro rgo do poder pblico documento compreendido no art. 409.'
	},
	"412":{
		"caput":'A legitimidade na elaborao de norma legal  assegurada pela observncia rigorosa das disposies regimentais, mediante os seguintes princpios bsicos:',
		"enumeravel": [{
			"tipo":"inciso",
			"lista":{
					"1":{
						"caput":'a participao plena e igualitria dos Senadores em todas as atividades legislativas, respeitados os limites regimentais;'
					},
					"2":{
						"caput":'modificao da norma regimental apenas por norma legislativa competente, cumpridos rigorosamente os procedimentos regimentais;'
					},
					"3":{
						"caput":'impossibilidade de prevalncia sobre norma regimental de acordo de lideranas ou deciso de Plenrio, exceto quando tomada por unanimidade mediante voto nominal, resguardado o quorum mnimo de trs quintos dos votos dos membros da Casa;'
					},
					"4":{
						"caput":'nulidade de qualquer deciso que contrarie norma regimental;'
					},
					"5":{
						"caput":'prevalncia de norma especial sobre a geral;'
					},
					"6":{
						"caput":'deciso dos casos omissos de acordo com a analogia e os princpios gerais de Direito;'
					},
					"7":{
						"caput":'preservao dos direitos das minorias;'
					},
					"8":{
						"caput":'definio normativa, a ser observada pela Mesa em questo de ordem decidida pela Presidncia;'
					},
					"9":{
						"caput":'deciso colegiada, ressalvadas as competncias especficas estabelecidas neste Regimento;'
					},
					"10":{
						"caput":'impossibilidade de tomada de decises sem a observncia do quorum regimental estabelecido;'
					},
					"11":{
						"caput":'pauta de decises feita com antecedncia tal que possibilite a todos os Senadores seu devido conhecimento;'
					},
					"12":{
						"caput":'publicidade das decises tomadas, exceo feita aos casos especficos previstos neste Regimento;'
					},
					"13":{
						"caput":'possibilidade de ampla negociao poltica somente por meio de procedimentos regimentais previstos.'
					},
			}
		}]
	},
	"413":{
		"caput":'A transgresso a qualquer desses princpios poder ser denunciada, mediante questo de ordem, nos termos do disposto no art. 404.',
		"enumeravel": [{
			"tipo":"paragrafo",
			"lista":{
					"1":{
						"caput":'Levantada a questo de ordem referida neste artigo, a Presidncia determinar a apurao imediata da denncia, verificando os fatos pertinentes, mediante consulta aos registros da Casa, notas taquigrficas, fitas magnticas ou outros meios cabveis'
					},
			}
		}]
	},
}
function getArtigos(){
  /*
  if(arguments.length === 2) console.log('arguments:', arguments[0], arguments[1])
  else console.log('arguments:', arguments[0])
  */
  return arguments.length === 2 ?
      Object.keys(artigos)
      .slice(arguments[0]-1, arguments[1])
      .reduce( (obj, numArtigo) => {return {...obj, [numArtigo] : artigos[numArtigo]} }, {})
      : {[arguments[0]]: artigos[arguments[0]]}
}

export { artigos, getArtigos }
